import React from "react";
import PropertyHeader from "../../../components/propertyheader";
import BestProperty from "../../../components/bestproperties";
import Agencies from "../../../components/featuredProjects";
import Featuredprojects from "../../../components/featuredProjects";
import Explore from "../../../components/explore";
import VisitPropertySite from "../../../components/visitPropertySite";
import Newsletter from "../../../components/newsletter";
import Footer from "../../../components/footer";
import Seo from "../../../components/seo";

export default function PropertyHome() {
  return (
    <div>
      <Seo
        title="Buy and Sell Property on Pakistan’s Best Real Estate Portal | Chhatt.com"
        description="Buy rent and sell your properties with Pakistan's best real estate portal at Chhatt.com. Find property dealers, developers and best property agents visit our website."
      />
      <PropertyHeader />
      <BestProperty
        title="Best properties for you"
        HideClassP="HideClass"
        marginTop="marginTop"
        sliderMargin="sliderMargin"
        top="top"
      />
      <Featuredprojects
        title="Featured Projects"
        city="Karachi"
        cardTitle="Welcome Center"
        description="Highway Developer & Builders"
        marginTop="marginTop"
        HideClassP="HideClass"
        marginBottom="marginBottom"
        id="featuredProjects"
      />
      <VisitPropertySite marginTop="marginTop" />
      <br />
      {/* <br /> */}
      <div style={{ marginBottom: "35px" }} />
      <Agencies
        title="Agencies"
        city="Karachi"
        cardTitle="ESTATE INN"
        description="UMAIR SARDAR"
        HideClassP_A="HideClass"
        marginTop="marginTop"
        marginTop_B="marginTop_B"
        id="agencies"
        anc="/real-estate-agencies"
      />
      <Explore />
      <br />
      <br />
      <br />
      <Newsletter />
      <Footer />
    </div>
  );
}
