import {
  HOME_SEARCH,
  HOME_PROPERTY_DATA,
  CITIES,
  GLOBAL_SEARCH,
} from "./homeType";
import config from "../../../config";
import axios from "axios";

// HOME_SEARCH
const areasData = (data) => {
  return {
    type: HOME_SEARCH,
    payload: data,
  };
};

// CITIES API
const citesData = (data) => {
  return {
    type: CITIES,
    payload: data,
  };
};

// HOME_SEARCH
export const fetchAreaOne = (data) => {
  return (dispatch) => {
    axios.get(`${config.apiUrl}/allareas?city=${data}`).then((res) => {
      const areas = res.data;
      dispatch(areasData(areas.data));
    });
    // axios.get(`${config.apiUrl}/areatwos`).then((res) => {
    //   const areas = res.data;
    //   dispatch(areasData(areas.data));
    // });
    // axios.get(`${config.apiUrl}/areathrees`).then((res) => {
    //   const areas = res.data;
    //   dispatch(areasData(areas.data));
    // });

    // == CALLING CITIES LIST DATA START
    axios.get(`${config.apiUrl}/cities`).then((res) => {
      const cities = res.data;
      dispatch(citesData(cities.data));
    });
  };
};

// ================ BEST PROPERTIES DATA ===============
const headerForm = (data) => {
  return {
    type: HOME_PROPERTY_DATA,
    payload: data,
  };
};

// ================ PRODUCT SEARCH FROM PROPERTY SITE HEADER START ===============
export const besPropertyApi = (data) => {
  const {
    val,
    city,
    purpose,
    propertyType,
    areaType,
    beds,
    baths,
    areaMin,
    areaMax,
    priceMin,
    priceMax,
    p,
    searched,
  } = data.urlData;

  const valEmpty = !val ? "" : val;
  const bedEmpty = !beds ? "" : beds;
  const bathEmpty = !baths ? "" : baths;
  const maxPriceEmpty = !priceMax ? "" : priceMax;
  const maxAreaEmpty = !areaMax ? "" : areaMax;
  const minAreaEmpty = !areaMin ? "" : areaMin;
  const minPriceEmpty = !priceMin ? "" : priceMin;
  const propertyTypeReplaced = propertyType && propertyType.replace("-", " ");
  const purposeProperty = purpose && purpose.replace("-", " ");
  // FILTERING ENCODE KEY
  return (dispatch) => {
    // ===== ROUTE FROM HOME (HEADER 1) SEARCH BAR START =====
    if ("home" in data.urlData) {
      // const valKey = val ? val : "";
      const key = valEmpty === "" ? "" : valEmpty.replace(/-/g, " ");
      const filtered =
        key === "" ? "" : data.homeSearch.filter((prev) => key === prev.name);
      if (filtered.length) {
        const encodeKey = encodeURI(filtered[0].key);
        axios
          .get(
            `${config.apiUrl}/properties/search?search_areas=${encodeKey}&city=${city}&page=${p}&status=1`
          )
          .then((res) => {
            const areas = res;
            dispatch(headerForm(areas));
          });
      } else {
        axios
          .get(
            `${config.apiUrl}/properties/search?city=${city}&page=${p}&status=1`
          )
          .then((res) => {
            const areas = res;
            dispatch(headerForm(areas));
          });
      }
      // ===== ROUTE FROM HOME (HEADER 1) SEARCH BAR END =====
    }
    // ===== ROUTE FROM PROPERTY GLOBAL SEARCH (HEADER 2) SEARCH BAR START =====
    else if ("searched" in data.urlData) {
      axios
        .get(
          `${config.apiUrl}/properties/search?search=${searched.replaceAll(
            "-",
            " "
          )}&page=${p}&status1`
        )
        .then((res) => {
          const data = res;
          dispatch(headerForm(data));
        });
    }
    // ===== ROUTE FROM PROPERTY GLOBAL SEARCH (HEADER 2) SEARCH BAR START =====

    // ===== ROUTE FROM VIEW ALL BTN (BEST PROPERTIES SLIDER) START =====
    else if ("allProperties" in data.urlData) {
      if ("city" in data.urlData) {
        axios
          .get(
            `${config.apiUrl}/properties/search?city=${city}${
              (areaType && `&area_type=${areaType}`) || ""
            }${
              (propertyTypeReplaced &&
                `&property_type=${propertyTypeReplaced}`) ||
              ""
            }${
              (purposeProperty && `&property_for=${purposeProperty}`) || ""
            }&page=${p}&status=1`
          )
          .then((res) => {
            const areas = res;
            dispatch(headerForm(areas));
          });
      } else {
        axios
          .get(
            `${config.apiUrl}/properties/search?search_areas=&page=${p}&status=1`
          )
          .then((res) => {
            const areas = res;
            dispatch(headerForm(areas));
          });
      }
      // BREADCRUMB AND EXPLORE BY PROPERTY TYPE APIS END
      // ===== ROUTE FROM VIEW ALL BTN (BEST PROPERTIES SLIDER) END =====
    }
    // ===== ROUTE FROM EXPLORE PROPERTY TYPE FROM HOME START =====
    // ===== ROUTE FROM PROPERTY SITE (PROPERTY HEADER) SEARCH START =====
    else if ("propertySite" in data.urlData) {
      if (data.homeSearch.length && valEmpty) {
        const key = val.replace(/-/g, " ");
        const filtered = data.homeSearch.filter((prev) => key === prev.name);
        const encodeKey = encodeURI(filtered[0].key);
        axios
          .get(
            `${config.apiUrl}/properties/search?search_areas=${encodeKey}&area_type=${areaType}&property_for=${purposeProperty}&city=${city}&property_type=${propertyTypeReplaced}&bed=${bedEmpty}&bath=${bathEmpty}&min_price=${minPriceEmpty}&max_price=${maxPriceEmpty}&min_area=${minAreaEmpty}&max_area=${maxAreaEmpty}&page=${p}&status=1`
          )
          .then((res) => {
            const areas = res;
            dispatch(headerForm(areas));
          });
      } else {
        axios
          .get(
            `${config.apiUrl}/properties/search?search_areas=${valEmpty}&area_type=${areaType}&property_for=${purposeProperty}&city=${city}&property_type=${propertyTypeReplaced}&bed=${bedEmpty}&bath=${bathEmpty}&min_price=${minPriceEmpty}&max_price=${maxPriceEmpty}&min_area=${minAreaEmpty}&max_area=${maxAreaEmpty}&page=${p}&status=1`
          )
          .then((res) => {
            const areas = res;
            dispatch(headerForm(areas));
          });
      }
    }
    // ===== ROUTE FROM PROPERTY SITE (PROPERTY HEADER) SEARCH START =====
  };
};
// ================ PRODUCT SEARCH FROM PROPERTY SITE HEADER START ===============

// ================ PRODUCT SEARCH FROM EXPLORE PROPERTY SITE START ===============
export const explorePropertyApi = (data) => {
  const replacingSpace = data[1].replace("-", " ");
  const gettingPage = (data[2] && data[2].split("-")) || 1;
  return (dispatch) => {
    axios
      .get(
        `https://uat.chhatt.com/api/properties/search?property_type=${replacingSpace}&page=${
          (gettingPage === 1 && 1) || gettingPage[1]
        }&status=1`
      )
      .then((res) => {
        const data = res;
        dispatch(headerForm(data));
      });
  };
};
// ================ PRODUCT SEARCH FROM EXPLORE PROPERTY SITE END ===============
