import React from "react";
import { MdVerifiedUser } from "react-icons/md";
import Style from "./Style.module.scss";

const AgencyDetails = ({ singleAgency }) => {
  return (
    <div className={Style.agencyDetails}>
      <h3>{singleAgency.agency_name}</h3>
      <hr />
      <div className={Style.imgDetails}>
        <img src={singleAgency.image} alt={singleAgency.agency_name} />
        <div className={Style.right_side}>
          <div>
            <h6>Name</h6>
            <h3>{singleAgency.owner_name}</h3>
          </div>
          <br />
          <div>
            <h6>Contact</h6>
            <h3>{"+92" + singleAgency.owner_contact}</h3>
          </div>
          <br />
          <div>
            <h6>Address</h6>
            <h3>{singleAgency.address}</h3>
          </div>
        </div>
      </div>
      <hr />
      <div className={Style.bottom_section}>
        <div className={Style.left_side}>
          <div>
            Agents:
            <span className="font-weight-bolder ml-1">
              {singleAgency.totalagents}
            </span>
          </div>
          <div>
            Properties:
            <span className="font-weight-bolder ml-1">
              {singleAgency.totalproperties}
            </span>
          </div>
        </div>
        <div className={Style.right_side}>
          <MdVerifiedUser style={{ fontSize: "20px", color: "#43b243" }} />
          &nbsp;Verified
        </div>
      </div>
    </div>
  );
};

export default AgencyDetails;
