import { RELATED_PROPERTIES } from "./relatedPropertiesType";
import config from "../../../config";
import axios from "axios";

const relatedProperties = (data) => {
  return {
    type: RELATED_PROPERTIES,
    payload: data,
  };
};

export const relatedPropertiesApi = (data) => {
  const { area_one_name, area_type, size } = data;
  return (dispatch) => {
    axios
      .get(
        `${
          config.apiUrl
        }/properties/related?area_one_name=${area_one_name}&area_type=${area_type}&size=${
          size ? size : ""
        }&status=1`
      )
      .then((res) => {
        const relatedPropertiesData = res.data;
        dispatch(relatedProperties(relatedPropertiesData.data));
      });
  };
};
