import {
	MOBILE_BEST_PROPERTIES,
	MOBILE_AREAS_LIST,
	MOBILE_CITIES,
	MOBILE_AGENCIES_SEARCH,
} from "./searchApiType";

const initialState = {
	areasList: [],
	mobileBestProperties: "",
	mobileCities: "",
	mobileAgenciesSearch: "",
};

const mobileBestPropertiesReducer = (state = initialState, action) => {
	switch (action.type) {
		case MOBILE_AREAS_LIST:
			return {
				...state,
				areasList: state.areasList.concat(action.payload),
			};
		case MOBILE_BEST_PROPERTIES:
			return {
				...state,
				mobileBestProperties: action.payload,
			};
		case MOBILE_CITIES:
			return {
				...state,
				mobileCities: action.payload,
			};
		case MOBILE_AGENCIES_SEARCH:
			return {
				...state,
				mobileAgenciesSearch: action.payload,
			};
		default:
			return state;
	}
};
export default mobileBestPropertiesReducer;
