import React, { useEffect } from "react";
import Style from "./Style.module.scss";
// import Breadcrumb from "../../components/breadcrumb";
import BestPropertyHeader from "../../components/bestPropertyHeader";
import Newsletter from "../../components/newsletter";
import Footer from "../../components/footer";
import { ContactForm } from "../../components/contactForm";
// import { Description } from "../../components/productDetails";
import AgencyDetails from "../../components/agencyDetails";
import Agents from "../../components/agents";
import { useParams } from "react-router-dom";
import { singleAgencyApi, agencyPropertiesApi } from "../../redux";
import { connect } from "react-redux";
import RelatedProperties from "../../components/relatedProperties";
import Seo from "../../components/seo";

const Agency = ({
  singleAgencyApi,
  singleAgency,
  agencyPropertiesApi,
  agencyProperties,
}) => {
  const { id } = useParams();
  useEffect(() => {
    singleAgencyApi(id);
    agencyPropertiesApi(id);
  }, []);

  var agentss;
  if (singleAgency) {
    if (singleAgency.agents.length) {
      agentss = <Agents singleAgency={singleAgency} />;
    }
  }

  var relateProperty;
  if (agencyProperties) {
    if (agencyProperties.length) {
      relateProperty = (
        <RelatedProperties
          heading="Agency Properties"
          agencyProperties={agencyProperties}
        />
      );
    }
  }
  return (
    <div>
      <Seo
        title={`${
          singleAgency.agency_name || "loading.."
        } | Buy Sell and Build property at Chhatt.com`}
        description={`Contact for ${singleAgency.agency_name || "loading.."} ${
          singleAgency.city
        }. And Getting latest updates for houses, flats, villas and commercial properties at leading property portal Chhatt.com.`}
      />
      <BestPropertyHeader
        heading={`${singleAgency.agency_name || "loading.."} ${
          singleAgency.city || "loading.."
        }`}
      />
      <div className={Style.detailSection}>
        <div className={Style.right_side}>
          <AgencyDetails singleAgency={singleAgency} />
          {/* <Description heading="About us" /> */}
        </div>
        <br />
        <br />
        <ContactForm singleAgency={singleAgency} />
      </div>
      {agentss}
      {relateProperty}
      <Newsletter />
      <Footer />
    </div>
  );
};

const mapStatetoProps = (state) => {
  return {
    homeSearch: state.homeSearchAreas.homeSearch,
    singleAgency: state.singlePropertyReducer.singleAgency,
    agencyProperties: state.featuredPropertiesReducer.agencyProperties,
  };
};
const mapDispatchtoProps = (dispatch) => {
  return {
    singleAgencyApi: function (id) {
      dispatch(singleAgencyApi(id));
    },
    agencyPropertiesApi: function (id) {
      dispatch(agencyPropertiesApi(id));
    },
  };
};
export default connect(mapStatetoProps, mapDispatchtoProps)(Agency);
