import React from "react";
import Style from "./Style.module.scss";
import { Link } from "react-router-dom";

export default function MobilePropertCard({ prev }) {
  const { name, image, desc, link } = prev;
  return (
    <Link to={link}>
      <div className={Style.main_div}>
        <img src={image} className={Style.propertyImg} alt={name} />
        <p>Karachi</p>
        <b> {name} </b>
        <p>
          <small> {desc} </small>
        </p>
      </div>
    </Link>
  );
}
