import React from "react";
import Style from "./Style.module.scss";
import Iframe from "react-iframe";

export default function Construction() {
  return (
    <>
      <div className={Style.main} id="Construction">
        <h1>
          <b>Gulberg Area Drone View</b>
        </h1>
        <div className={Style.sdiv}>
          <Iframe
            url="https://www.youtube.com/embed/YUOxie4Dx2w"
            width="100%"
            height="500px"
            id="myId"
            display="block"
          />
        </div>
      </div>
    </>
  );
}
