import React, { useEffect, useState } from "react";
import Style from "./Style.module.scss";
import { FormControl, InputBase, MenuItem, Select } from "@material-ui/core";
import { makeStyles, withStyles } from "@material-ui/core/styles";
import { AiOutlineSearch } from "react-icons/ai";
import { BiReset } from "react-icons/bi";
import { useHistory, useParams } from "react-router-dom";
import { mobileAreasListApi } from "../../redux";
import { connect } from "react-redux";
import useAutocomplete from "@material-ui/lab/useAutocomplete";

const FilterSection = ({ mobileAreasListApi, areasList, mobileCities }) => {
	const [selectBox, setSelectBox] = useState({
		city: "1",
		propertyType: "plot",
		areaType: "residential",
		beds: "Any",
		baths: "Any",
		priceMin: "0",
		priceMax: "Any",
		areaMin: "0",
		areaMax: "Any",
	});

	useEffect(() => {
		mobileAreasListApi();
	}, []);

	// HANDLE CHANGE FOR ALL SELECT BOXES
	const handleChange = (event) => {
		const { name, value } = event.target;
		setSelectBox((prev) => {
			return {
				...prev,
				[name]: value,
			};
		});
	};

	const onReset = () => {
		setSelectBox({
			city: "1",
			propertyType: "plot" && "plot",
			areaType: "residential",
			beds: "Any",
			baths: "Any",
			priceMin: "0",
			priceMax: "Any",
			areaMin: "0",
			areaMax: "Any",
		});
	};

	// SELECT STYLING FROM MATERIAL UI START
	const BootstrapInput = withStyles((theme) => ({
		root: {
			"label + &": {
				marginRight: theme.spacing(1),
			},
		},
		input: {
			width: "100%",
			borderRadius: 4,
			backgroundColor: "#fff",
			fontSize: 16,
			padding: "10px 26px 10px 12px",
			transition: theme.transitions.create(["border-color", "box-shadow"]),
			fontWeight: "600",

			"&:focus": {
				borderRadius: 4,
				borderColor: "#80bdff",
				boxShadow: "0 0 0 0.2rem rgba(0,123,255,.25)",
			},
		},
	}))(InputBase);

	const propertyTypeRes = [
		"Plot",
		"Flat",
		"Plot File",
		"Agriculture Land",
		"Villa",
		"Lower Portion",
		"Upper Portion",
		"Farm House",
		"Pent House",
	];
	const propertyTypeCom = [
		"Plot",
		"Plot File",
		"Lower Portion",
		"Upper Portion",
	];
	const propertyTypeInd = ["Plot", "Plot File", "Farm House", "Pent House"];

	// INPUT FIELD STYLING MATERIAL UI START
	const useStyles = makeStyles((theme) => ({
		input: {
			outline: "none",
			width: "100%",
			padding: "10px 10px",
		},
		listbox: {
			width: "93%",
			margin: 0,
			padding: 0,
			zIndex: 1,
			marginTop: "5px",
			position: "absolute",
			listStyle: "none",
			backgroundColor: theme.palette.background.paper,
			overflow: "auto",
			maxHeight: 300,
			color: "#000",
			textAlign: "left",
			border: "1px solid rgba(0,0,0,.25)",
			'& li[data-focus="true"]': {
				backgroundColor: "#4a8df6",
				color: "#fff",
				cursor: "pointer",
			},
			"& li:active": {
				backgroundColor: "#2977f5",
				color: "#000",
			},
		},
	}));
	// INPUT FIELD STYLING MATERIAL UI END

	const classes = useStyles();
	let {
		getRootProps,
		getInputProps,
		getListboxProps,
		getOptionProps,
		groupedOptions,
	} = useAutocomplete({
		id: "use-autocomplete-demo",
		options: areasList,
		getOptionLabel: (option) => option.name,
	});

	let history = useHistory();
	let { type } = useParams();

	// FOR GETTING PURPOSE FROM PARAMS
	let purpose =
		(type === "buy" && "for-sale") ||
		(type === "rent" && "for-rent") ||
		(type === "booking" && "for-booking");

	const onSubmit = () => {
		const val = getInputProps().value;
		// history.push(
		// 	`/results?propertySite&areaType=${selectBox.areaType}&propertyType=${selectBox.propertyType}&purpose=${purpose}&val=${val}&city=${selectBox.city}&beds=${selectBox.beds}&baths=${selectBox.baths}&priceMin=${selectBox.priceMin}&priceMax=${selectBox.priceMax}&areaMin=${selectBox.areaMin}&areaMax=${selectBox.areaMax}&p=1`
		// );
		history.push(
			`/results?propertySite&areaType=${selectBox.areaType}&propertyType=${
				selectBox.propertyType
			}&purpose=${purpose}${(val && `&val=${val}`) || ""}&city=${
				selectBox.city
			}${(selectBox.beds !== "Any" && `&beds=${selectBox.beds}`) || ""}${
				(selectBox.baths !== "Any" && `&baths=${selectBox.baths}`) || ""
			}${(selectBox.priceMin != 0 && `&priceMin=${selectBox.priceMin}`) || ""}${
				(selectBox.priceMax !== "Any" && `&priceMax=${selectBox.priceMax}`) ||
				""
			}${(selectBox.areaMin != 0 && `&areaMin=${selectBox.areaMin}`) || ""}${
				(selectBox.areaMax != "Any" && `&areaMax=${selectBox.areaMax}`) || ""
			}&p=1`
		);
	};

	return (
		<>
			<div className={Style.main_div}>
				<div>
					<label htmlFor="location"> Location </label>
					<br />
					{/* search field start */}
					<div style={{ width: "100%" }}>
						{areasList && (
							<div>
								<div {...getRootProps()}>
									<input
										className={classes.input}
										placeholder="Search..."
										{...getInputProps()}
									/>
								</div>
								{groupedOptions.length > 0 ? (
									<ul className={classes.listbox} {...getListboxProps()}>
										{groupedOptions.map((option, index) => (
											<li {...getOptionProps({ option, index })}>
												{`${option.name} - ${option.parent} `}
											</li>
										))}
									</ul>
								) : null}
							</div>
						)}
					</div>
					{/* search field end */}
				</div>

				<br />

				<div className={Style.twoInOne}>
					<div>
						<label htmlFor="areaType"> Area Type </label>
						<br />
						<FormControl style={{ width: "100%" }}>
							<Select
								labelId="demo-customized-select-label"
								id="demo-customized-select"
								value={selectBox.areaType}
								onChange={handleChange}
								name="areaType"
								input={<BootstrapInput />}
							>
								<MenuItem value="residential">Residential</MenuItem>
								<MenuItem value="commercial">Commercial</MenuItem>
								<MenuItem value="industrial">Industrial</MenuItem>
							</Select>
						</FormControl>
					</div>
					<div>
						<label htmlFor="areaType"> City </label>
						<br />
						{mobileCities ? (
							<div>
								<FormControl style={{ width: "100%" }}>
									<Select
										labelId="demo-customized-select-label"
										id="demo-customized-select"
										value={selectBox.city}
										onChange={handleChange}
										input={<BootstrapInput />}
									>
										{mobileCities.map((prev, i) => {
											const { id, name } = prev;
											return (
												<MenuItem key={i} value={id}>
													{name}
												</MenuItem>
											);
										})}
									</Select>
								</FormControl>
							</div>
						) : (
							<div style={{ width: "100%" }}>
								<FormControl className={classes.margin}>
									<Select
										labelId="demo-customized-select-label"
										id="demo-customized-select"
										value={selectBox.city}
										onChange={handleChange}
										input={<BootstrapInput />}
									>
										<MenuItem value="1">Karachi</MenuItem>
									</Select>
								</FormControl>
							</div>
						)}
						{/* <FormControl style={{ width: "100%" }}>
              <Select
                labelId="demo-customized-select-label"
                id="demo-customized-select"
                value={selectBox.city}
                onChange={handleChange}
                name="city"
                input={<BootstrapInput />}
              >
                {mobileCities &&
                  mobileCities.map((prev) => {
                    return <MenuItem value={prev.id}> {prev.name} </MenuItem>;
                  })}
              </Select>
            </FormControl> */}
					</div>
				</div>

				<br />

				<div>
					<label htmlFor="propertyType"> Property Type </label>
					<br />
					<FormControl style={{ width: "100%" }}>
						<Select
							labelId="demo-customized-select-label"
							id="demo-customized-select"
							value={selectBox.propertyType}
							onChange={handleChange}
							name="propertyType"
							input={<BootstrapInput />}
						>
							{/* ON THE SELECTION OF RESIDENTIAL */}
							{(selectBox.areaType === "residential" &&
								propertyTypeRes.map((prev, i) => {
									return (
										<MenuItem
											key={i}
											value={prev.toLowerCase().replace(" ", "-")}
										>
											{prev}
										</MenuItem>
									);
								})) ||
								// ON THE SELECTION OF COMMERCIAL
								(selectBox.areaType === "commercial" &&
									propertyTypeCom.map((prev, i) => {
										return (
											<MenuItem
												key={i}
												value={prev.toLowerCase().replace(" ", "-")}
											>
												{prev}
											</MenuItem>
										);
									})) ||
								// ON THE SELECTION OF INDUSTRIAL
								(selectBox.areaType === "industrial" &&
									propertyTypeInd.map((prev, i) => {
										return (
											<MenuItem
												key={i}
												value={prev.toLowerCase().replace(" ", "-")}
											>
												{prev}
											</MenuItem>
										);
									}))}
						</Select>
					</FormControl>
				</div>

				<br />

				{selectBox.propertyType !== "Plot" &&
					selectBox.propertyType !== "Plot File" && (
						<>
							<div className={Style.twoInOne}>
								<div>
									<label htmlFor="beds"> Beds </label>
									<br />
									<FormControl style={{ width: "100%" }}>
										<Select
											labelId="demo-customized-select-label"
											id="demo-customized-select"
											value={selectBox.beds}
											onChange={handleChange}
											name="beds"
											input={<BootstrapInput />}
										>
											<MenuItem value="Any">Any</MenuItem>
											<MenuItem value="1">1</MenuItem>
											<MenuItem value="2">2</MenuItem>
											<MenuItem value="3">3</MenuItem>
											<MenuItem value="4">4</MenuItem>
											<MenuItem value="5">5</MenuItem>
											<MenuItem value="6">6</MenuItem>
											<MenuItem value="7">7</MenuItem>
											<MenuItem value="8">8</MenuItem>
											<MenuItem value="9">9</MenuItem>
											<MenuItem value="10+">10+</MenuItem>
										</Select>
									</FormControl>
								</div>

								<div>
									<label htmlFor="baths"> Baths </label>
									<br />
									<FormControl style={{ width: "100%" }}>
										<Select
											labelId="demo-customized-select-label"
											id="demo-customized-select"
											value={selectBox.baths}
											onChange={handleChange}
											name="baths"
											input={<BootstrapInput />}
										>
											<MenuItem value="Any">Any</MenuItem>
											<MenuItem value="1">1</MenuItem>
											<MenuItem value="2">2</MenuItem>
											<MenuItem value="3">3</MenuItem>
											<MenuItem value="4">4</MenuItem>
											<MenuItem value="5">5</MenuItem>
											<MenuItem value="6">6</MenuItem>
											<MenuItem value="7">7</MenuItem>
											<MenuItem value="8">8</MenuItem>
											<MenuItem value="9">9</MenuItem>
											<MenuItem value="10+">10+</MenuItem>
										</Select>
									</FormControl>
								</div>
							</div>
							<br />
						</>
					)}

				<div className={Style.twoInOne}>
					<div>
						<label htmlFor="priceMin"> Price Min </label>
						<br />

						<FormControl style={{ width: "100%" }}>
							<Select
								labelId="demo-customized-select-label"
								id="demo-customized-select"
								value={selectBox.priceMin}
								onChange={handleChange}
								name="priceMin"
								input={<BootstrapInput />}
							>
								<MenuItem value="0">0</MenuItem>
								<MenuItem value="500000">500,000</MenuItem>
								<MenuItem value="1000000">1,000,000</MenuItem>
								<MenuItem value="2000000">2,000,000</MenuItem>
								<MenuItem value="3500000">3,500,000</MenuItem>
								<MenuItem value="5000000">5,000,000</MenuItem>
								<MenuItem value="6500000">6,500,000</MenuItem>
								<MenuItem value="8000000">8,000,000</MenuItem>
								<MenuItem value="10000000">10,000,000</MenuItem>
								<MenuItem value="12500000">12,500,000</MenuItem>
								<MenuItem value="15000000">15,000,000</MenuItem>
								<MenuItem value="17500000">17,500,000</MenuItem>
								<MenuItem value="20000000">20,000,000</MenuItem>
								<MenuItem value="30000000">30,000,000</MenuItem>
								<MenuItem value="40000000">40,000,000</MenuItem>
								<MenuItem value="50000000">50,000,000</MenuItem>
								<MenuItem value="75000000">75,000,000</MenuItem>
								<MenuItem value="100000000">100,000,000</MenuItem>
								<MenuItem value="250000000">250,000,000</MenuItem>
								<MenuItem value="500000000">500,000,000</MenuItem>
								<MenuItem value="500000000">500,000,000</MenuItem>
								<MenuItem value="1000000000">1,000,000,000</MenuItem>
							</Select>
						</FormControl>
					</div>

					<div>
						<label htmlFor="priceMax"> Price Max </label>
						<br />
						<FormControl style={{ width: "100%" }}>
							<Select
								labelId="demo-customized-select-label"
								id="demo-customized-select"
								value={selectBox.priceMax}
								onChange={handleChange}
								name="priceMax"
								input={<BootstrapInput />}
							>
								<MenuItem value="Any">Any</MenuItem>
								<MenuItem value="500000">500,000</MenuItem>
								<MenuItem value="1000000">1,000,000</MenuItem>
								<MenuItem value="2000000">2,000,000</MenuItem>
								<MenuItem value="3500000">3,500,000</MenuItem>
								<MenuItem value="5000000">5,000,000</MenuItem>
								<MenuItem value="6500000">6,500,000</MenuItem>
								<MenuItem value="8000000">8,000,000</MenuItem>
								<MenuItem value="10000000">10,000,000</MenuItem>
								<MenuItem value="12500000">12,500,000</MenuItem>
								<MenuItem value="15000000">15,000,000</MenuItem>
								<MenuItem value="17500000">17,500,000</MenuItem>
								<MenuItem value="20000000">20,000,000</MenuItem>
								<MenuItem value="30000000">30,000,000</MenuItem>
								<MenuItem value="40000000">40,000,000</MenuItem>
								<MenuItem value="50000000">50,000,000</MenuItem>
								<MenuItem value="75000000">75,000,000</MenuItem>
								<MenuItem value="100000000">100,000,000</MenuItem>
								<MenuItem value="250000000">250,000,000</MenuItem>
								<MenuItem value="500000000">500,000,000</MenuItem>
								<MenuItem value="500000000">500,000,000</MenuItem>
								<MenuItem value="1000000000">1,000,000,000</MenuItem>
								<MenuItem value="5000000000">5,000,000,000</MenuItem>
							</Select>
						</FormControl>
					</div>
				</div>

				<br />

				<div className={Style.twoInOne}>
					<div>
						<label htmlFor="areaMin"> Area Min </label>
						<br />
						<FormControl style={{ width: "100%" }}>
							<Select
								labelId="demo-customized-select-label"
								id="demo-customized-select"
								value={selectBox.areaMin}
								onChange={handleChange}
								name="areaMin"
								input={<BootstrapInput />}
							>
								<MenuItem value="0">0</MenuItem>
								<MenuItem value="500">500</MenuItem>
								<MenuItem value="1000">1000</MenuItem>
								<MenuItem value="2000">2000</MenuItem>
								<MenuItem value="2500">2500</MenuItem>
								<MenuItem value="3000">3000</MenuItem>
								<MenuItem value="3500">3500</MenuItem>
							</Select>
						</FormControl>
					</div>

					<div>
						<label htmlFor="areaMax"> Area Max </label>
						<br />
						<FormControl style={{ width: "100%" }}>
							<Select
								labelId="demo-customized-select-label"
								id="demo-customized-select"
								value={selectBox.areaMax}
								onChange={handleChange}
								name="areaMax"
								input={<BootstrapInput />}
							>
								<MenuItem value="Any">Any</MenuItem>
								<MenuItem value="500">500</MenuItem>
								<MenuItem value="1000">1000</MenuItem>
								<MenuItem value="2000">2000</MenuItem>
								<MenuItem value="2500">2500</MenuItem>
								<MenuItem value="3000">3000</MenuItem>
								<MenuItem value="3500">3500</MenuItem>
								<MenuItem value="40000">40000</MenuItem>
							</Select>
						</FormControl>
					</div>
				</div>

				<div className={Style.searchBtn}>
					<button onClick={onSubmit}>
						<AiOutlineSearch />
						&nbsp;Search
					</button>
					<button onClick={onReset}>
						<BiReset />
						&nbsp;Reset
					</button>
				</div>
			</div>
		</>
	);
};

const mapStatetoProps = (state) => {
	return {
		areasList: state.mobileBestPropertiesReducer.areasList,
		mobileCities: state.mobileBestPropertiesReducer.mobileCities,
	};
};
const mapDispatchtoProps = (dispatch) => {
	return {
		mobileAreasListApi: function () {
			dispatch(mobileAreasListApi());
		},
	};
};
export default connect(mapStatetoProps, mapDispatchtoProps)(FilterSection);
