import {
  MOBILE_SINGLE_PROPERTY,
  MOBILE_SINGLE_AGENCY,
} from "./propertyDetailType";

const initialState = {
  singleProperty: "",
  singleAgency: "",
};

const singlePropertyReducer = (state = initialState, action) => {
  switch (action.type) {
    case MOBILE_SINGLE_PROPERTY:
      return {
        ...state,
        singleProperty: action.payload,
      };
    case MOBILE_SINGLE_AGENCY:
      return {
        ...state,
        singleAgency: action.payload,
      };
    default:
      return state;
  }
};
export default singlePropertyReducer;
