import React from "react";
import Style from "./Style.module.scss";
import { Link } from "react-router-dom";

export default function MobilePropertCard({ prev }) {
  const {
    area_type,
    short_price,
    property_type,
    property_for,
    area_one_name,
    area_two_name,
    images,
    id,
    city,
  } = prev;

  let propertyFor = property_for.replace(" ", "_");
  let propertyType = property_type.replace(" ", "_");
  let areaTwo = area_two_name.replace(/ /g, "_");
  let areaOne = area_one_name.replace(/ /g, "_");
  let link = `/propertyDetail/${city.toLowerCase()}-${area_type.toLowerCase()}-${propertyType.toLowerCase()}-${propertyFor.toLowerCase()}-${areaOne}-${areaTwo}-${id}`;

  return (
    <Link to={link} className={Style.main_div}>
      <img
        src={images[0].original}
        className={Style.propertyImg}
        alt={`${property_type} ${property_for}`}
      />
      <p> {area_type} </p>
      <b>
        {property_type} {property_for} In {area_one_name}
      </b>
      <p>
        <b>Rs : {short_price}</b>
      </p>
    </Link>
  );
}
