import { createStore, applyMiddleware, combineReducers } from "redux";
import logger from "redux-logger";
import thunk from "redux-thunk";
import bestPropertiesReducer from "./mobileBestProperty/bestPropertyReducer";
import mobileBestPropertiesReducer from "./searchApi/searchApiReducer";
import singlePropertyReducer from "./propertAndAgencyDetail/propertyDetailReducer";
import relatedPropertiesReducer from "./relatedProperties/relatedPropertiesReducer";
import mobileFeaturedPropertiesReducer from "./featuredProjects_agencies_maps/featuredProjectsReducer";
import MenuState from "./menu/menuReducer";

const middleware = applyMiddleware(thunk, logger);
// const middleware = applyMiddleware(thunk);
const rootReducer = combineReducers({
  bestPropertiesReducer,
  mobileBestPropertiesReducer,
  singlePropertyReducer,
  relatedPropertiesReducer,
  mobileFeaturedPropertiesReducer,
  MenuState,
});
const store = createStore(rootReducer, middleware);
export default store;
