import React from 'react'
// import Style from './Style.module.scss'

import { IoShareSocialSharp } from 'react-icons/io5'
import {
  FacebookShareButton,
  FacebookIcon,
  TwitterShareButton,
  TwitterIcon,
  WhatsappShareButton,
  WhatsappIcon,
  EmailShareButton,
  EmailIcon,
  FacebookMessengerShareButton,
  FacebookMessengerIcon,
} from 'react-share'

import { useHistory } from 'react-router-dom'
import Button from '@material-ui/core/Button'
import Menu from '@material-ui/core/Menu'
import MenuItem from '@material-ui/core/MenuItem'

import { makeStyles } from '@material-ui/core/styles'

const useStyles = makeStyles((theme) => ({
  button: {
    textTransform: 'none',
    backgroundColor: '#28a745',
    color: '#ffff',
    alignItems: 'center',

    '&:hover': {
      backgroundColor: '#28a745',
    },
  },

  menuItem: {
    fontWeight: '700',
    border: '0.5px solid #e3e3e3',
    width: '100%',
    padding: '0 rem',
    '&:hover': {
      backgroundColor: '#28a745',
      color: '#ffff',
    },
    '&:focus': {
      '& .MuiListItemIcon-root, & .MuiListItemText-primary': {},
    },
  },
  menu: {
    border: '1px solid #d3d4d5',
  },
  menuPadding: {
    padding: 0,
  },
}))

export default function Share() {
  const classes = useStyles()
  let history = useHistory()
  const [anchorEl, setAnchorEl] = React.useState(null)
  const shareUrl = `https://chhatt.com${history.location.pathname}`

  const handleClick = (event) => {
    setAnchorEl(event.currentTarget)
  }

  const handleClose = () => {
    setAnchorEl(null)
  }
  return (
    <div>
      <Button
        variant='contained'
        classes={{ root: classes.button }}
        aria-controls='share-menu'
        aria-haspopup='true'
        onClick={handleClick}
      >
        <IoShareSocialSharp size='14' style={{ marginRight: '2px' }} />
        Share
      </Button>

      <Menu
        elevation={0}
        getContentAnchorEl={null}
        anchorOrigin={{
          vertical: 'bottom',
          horizontal: 'center',
        }}
        classes={{ paper: classes.menu }}
        transformOrigin={{
          vertical: 'top',
          horizontal: 'center',
        }}
        id='customized-menu'
        anchorEl={anchorEl}
        keepMounted
        open={Boolean(anchorEl)}
        onClose={handleClose}
        MenuListProps={{ classes: { padding: classes.menuPadding } }}
      >
        <MenuItem classes={{ root: classes.menuItem }}>
          <FacebookShareButton url={shareUrl}>
            <FacebookIcon
              size={28}
              round={true}
              iconFillColor='#fff'
              style={{ marginRight: '0.5rem' }}
            />

            <span>Share to Facebook</span>
          </FacebookShareButton>
        </MenuItem>
        <MenuItem
          classes={{ root: classes.menuItem }}
          style={{ marginRight: '0.5rem' }}
        >
          <TwitterShareButton url={shareUrl}>
            <TwitterIcon
              size={28}
              round={true}
              iconFillColor='#fff'
              style={{ marginRight: '0.5rem' }}
            />

            <span>Share to Twitter</span>
          </TwitterShareButton>
        </MenuItem>
        <MenuItem
          classes={{ root: classes.menuItem }}
          style={{ marginRight: '0.5rem' }}
        >
          <WhatsappShareButton url={shareUrl}>
            <WhatsappIcon
              size={28}
              round={true}
              iconFillColor='#fff'
              style={{ marginRight: '0.5rem' }}
            />

            <span>Share to Twitter</span>
          </WhatsappShareButton>
        </MenuItem>
        <MenuItem classes={{ root: classes.menuItem }}>
          <FacebookMessengerShareButton url={shareUrl}>
            <FacebookMessengerIcon
              size={28}
              round={true}
              iconFillColor='#fff'
              style={{ marginRight: '0.5rem' }}
            />

            <span>Share to Twitter</span>
          </FacebookMessengerShareButton>
        </MenuItem>
        <MenuItem classes={{ root: classes.menuItem }}>
          <EmailShareButton url={shareUrl}>
            <EmailIcon
              size={28}
              round={true}
              iconFillColor='#fff'
              style={{ marginRight: '0.5rem' }}
            />

            <span>Share to Twitter</span>
          </EmailShareButton>
        </MenuItem>
      </Menu>
      {/* <Dropdown>
        <Dropdown.Toggle variant='success' id='dropdown-basic'>
          <IoShareSocialSharp size='14' style={{ marginTop: '-2.8px' }} /> Share
        </Dropdown.Toggle>
        <Dropdown.Menu>
          <Dropdown.Item>
            <FacebookShareButton url={shareUrl}>
              <div className={Style.share_div}>
                <FacebookIcon size={28} round={true} iconFillColor='#fff' />
                <p>Share on Facebook</p>
              </div>
            </FacebookShareButton>
          </Dropdown.Item>
          <Dropdown.Item>
            <TwitterShareButton url={shareUrl}>
              <div className={Style.share_div}>
                <TwitterIcon size={28} round={true} iconFillColor='#fff' />
                <p>Share on Twitter</p>
              </div>
            </TwitterShareButton>
          </Dropdown.Item>
          <Dropdown.Item>
            <WhatsappShareButton url={shareUrl}>
              <div className={Style.share_div}>
                <WhatsappIcon size={28} round={true} iconFillColor='#fff' />
                <p>Share on Whatsapp</p>
              </div>
            </WhatsappShareButton>
          </Dropdown.Item>
          <Dropdown.Item>
            <FacebookMessengerShareButton url={shareUrl}>
              <div className={Style.share_div}>
                <FacebookMessengerIcon
                  size={28}
                  round={true}
                  iconFillColor='#fff'
                />
                <p>Send via Messenger</p>
              </div>
            </FacebookMessengerShareButton>
          </Dropdown.Item>
          <Dropdown.Item>
            <EmailShareButton url={shareUrl}>
              <div className={Style.share_div}>
                <EmailIcon size={28} round={true} iconFillColor='#fff' />
                <p>Send via Gmail</p>
              </div>
            </EmailShareButton>
          </Dropdown.Item>
        </Dropdown.Menu>
      </Dropdown> */}
    </div>
  )
}
