import React from "react";
import Style from "./Style.module.scss";
import Sliderimg from "../../../../assets/sliderImg.webp";
import Verify from "../../../../assets/icons/verify.png";
import Profile from "../../../../assets/icons/profile.png";
import Star from "../../../../assets/icons/star.png";
import cn from "classnames";
// import { PriceCommas } from "../../../util";

export default function ProductCard({ R_width, totalWidth }) {
  return (
    <div
      className={cn(
        Style.maindiv,
        R_width && Style.R_width,
        totalWidth && Style.totalWidth
      )}
    >
      <div className={Style.img_div}>
        <img src={Sliderimg} width="100%" height="90%" alt="" />
        <div className={Style.sale}> resdiential </div>
        <div className={Style.verify}>
          <img src={Verify} alt="" />
        </div>
        <div className={Style.ptxt1}>
          <div className={Style.profile_div}>
            <img src={Profile} alt="" /> <span> Sameer shaikh </span>
          </div>
          <div className={Style.rating}>
            <img src={Star} alt="" /> <span>4.8</span>
          </div>
        </div>
      </div>
      <div className={Style.mainchild}>
        <div className={Style.ptxt2}>
          <p
            style={{ fontSize: "16px" }}
            // title={`${category} ${property_type} in ${area_one_name}`}
          >
            <b>HOUSE IN DHA FOR SELL</b>
          </p>
          <b>
            <p
              style={{
                fontSize: "16px",
                color: "#D63F3F",
                marginTop: "-15px",
              }}
              // title={price}
            >
              Rs: 5.00 Cr
            </p>
          </b>
        </div>
        <div className={Style.icn_container}>
          <p> Karachi City </p>
        </div>
        {/* <div className={Style.btndiv}>
          <Link to="/">
            <button className={Style.btn2}>View more</button>
          </Link>

          <Link to="/">
            <button className={Style.btn2}>Contact Supplier</button>
          </Link>
        </div> */}
      </div>
    </div>
  );
}
