const WebsiteRoutes = () => {
  return (
    <Provider store={store}>
      <BrowserView>
        <ScrollToTop />
        <Switch>
          <Route exact path="/" component={Home} />
          <Route exact path="/1" component={Category} />
          <Route exact path="/2" component={SubCategory} />
          {/* <Route exact path="/construction" component={Construction} /> */}
          <Route exact path="/property" component={Property} />
          {/* <Route exact path="/maps" component={Maps} />
            <Route exact path="/map" component={Map} /> */}
          <Route exact path="/about-us" component={AboutUs} />
          <Route exact path="/contact-us" component={ContactUs} />
          <Route exact path="/real-estate-agencies" component={Agencies} />
          <Route exact path="/agency/:id" component={Agency} />
          {/* <Route exact path="/test" component={Test} /> */}
          <Route exact path="/propertyDetail/:productId" component={Product} />
          {/* construction */}
          <Route
            exact
            path={[
              "/results",
              "/property/:propertyType",
              "/property/:propertyType/:page",
            ]}
            component={BestProperty_P}
          />
          <Route exact path="/construction" component={Construct} />
          <Route
            exact
            path="/projects/welcome_center"
            component={WelcomeCenter}
          />
          <Route exact path="/projects/jf-tower" component={Jf_Tower} />
          <Route exact path="/wanted" component={Tellus} />
          <Route
            exact
            path="/FeaturedProjectsPage"
            component={FeaturedProjectsPage}
          />
          <Route exact component={Error} />
        </Switch>
      </BrowserView>
    </Provider>
  );
};

export default WebsiteRoutes;
