// FUNCTION FOR DIFFERENTIATE ( K, LAC, CRORE ) IN PRICE START
export const PriceCommas = function numDifferentiation(value) {
  var val = Math.abs(value);
  if (val >= 10000000) {
    if (val.toString()[1] === "0" && val.toString()[2] !== "0") {
      val = (val / 10000000).toFixed(1) + " Cr";
    } else {
      val = (val / 10000000).toFixed(2) + " Cr";
    }
  } else if (val >= 100000) {
    if (val.toString()[1] === "0" && val.toString()[2] !== "0") {
      val = (val / 100000).toFixed(1) + " Cr";
    } else {
      val = (val / 100000).toFixed(2) + " Lac";
    }
  } else if (val >= 1000) {
    val = (val / 1000).toFixed(1) + " k";
  }
  return val;
};
// FUNCTION FOR DIFFERENTIATE ( K, LAC, CRORE ) IN PRICE END

// FUNCTION FOR CAPITALIZE THE WORD START
export const capitalizeFunc = (string) => {
  return string.charAt(0).toUpperCase() + string.slice(1);
};
// FUNCTION FOR CAPITALIZE THE WORD END
