import React from "react";
import Style from "./Style.module.scss";
import MobilePropertCard from "../cards/propertyCard";

const MobileRelatedProperties = ({
  relatedProperties,
  heading,
  mobileAgencyProperties,
}) => {
  return (
    <div className={Style.main_div}>
      <div className={Style.fdiv}>
        <div className={Style.top}>
          <p> {heading} </p>
          {/* <p>View All</p> */}
        </div>
        <div className={Style.sdiv}>
          <ul>
            {heading === "Related Properties" &&
              relatedProperties &&
              relatedProperties.map((prev, i) => {
                return (
                  <li key={i}>
                    <div>
                      <MobilePropertCard prev={prev} />
                    </div>
                  </li>
                );
              })}

            {heading === "Agency Properties" &&
              mobileAgencyProperties &&
              mobileAgencyProperties.map((prev, i) => {
                return (
                  <li key={i}>
                    <div>
                      <MobilePropertCard prev={prev} />
                    </div>
                  </li>
                );
              })}
          </ul>
        </div>
      </div>
    </div>
  );
};

export default MobileRelatedProperties;
