import React from 'react'
import Style from './Style.module.scss'
import cn from 'classnames'
import { Link } from 'react-router-dom'
import Button from '@material-ui/core/Button'
import { makeStyles } from '@material-ui/core/styles'

const useStyles = makeStyles(() => ({
  button: {
    textTransform: 'none',
  },
}))

export default function TwoCards() {
  const classes = useStyles()

  return (
    <div className={cn(Style.main_div)}>
      <div className={Style.fdiv} title='Properties Site'>
        <div className={Style.fdiv_fch}>
          <p>Hundred of properties</p>
          <p>from top locations</p>
        </div>
        <div className={Style.fdiv_sch}>
          <Link to='/property'>
            <Button variant='contained' classes={{ root: classes.button }}>
              Properties Site
            </Button>
          </Link>
        </div>
      </div>
      <div className={Style.sdiv} title='Construction Site'>
        <div className={Style.sdiv_fch}>
          <p>Find the best</p>
          <p>construction materials</p>
        </div>
        <div className={Style.sdiv_sch}>
          <Link to='construction'>
            <Button classes={{ root: classes.button }} variant='contained'>
              Construction Site
            </Button>
          </Link>
        </div>
      </div>
    </div>
  )
}
