import React from "react";
import Style from "./Style.module.scss";
import { MdVerifiedUser } from "react-icons/md";

export const AgencyTop = ({ singleAgency }) => {
  return (
    <div className={Style.topContainer}>
      <div className={Style.lightColor}>
        Agents:&nbsp;
        <span className={Style.numbers}>
          {singleAgency.agents && singleAgency.agents.length}
        </span>
      </div>
      <div className={Style.lightColor}>
        Properties:
        <span className={Style.numbers}> {singleAgency.totalproperties} </span>
      </div>
      <div className={Style.verified}>
        <MdVerifiedUser /> &nbsp;
        <span className={Style.numbers}>Verified</span>
      </div>
    </div>
  );
};

export const ImageSection = ({ singleAgency }) => {
  return (
    <div className={Style.imageContainer}>
      <h5> {singleAgency.agency_name} </h5>
      <div className={Style.agencyImageContainer}>
        <img src={singleAgency.image} alt={singleAgency.agency_name} />
      </div>
      <div className={`${Style.agencyDUpper} mt-3`}>Name:</div>
      <div className={Style.fetchedData}>{singleAgency.owner_name}</div>
      <div className={Style.agencyDUpper}>Contact:</div>
      <div className={Style.fetchedData}>{singleAgency.owner_contact}</div>
      <div className={Style.agencyDUpper}>Address:</div>
      <div className={Style.fetchedData}>{singleAgency.address}</div>
    </div>
  );
};

export const AgencyAboutUs = ({ singleAgency }) => {
  return (
    <div className={Style.aboutUsContainer}>
      <h5>About Us</h5>
      <h6> {singleAgency.agency_name} </h6>
    </div>
  );
};
