import { HOME_SEARCH, HOME_PROPERTY_DATA, CITIES } from "./homeType";

const initialState = {
  homeSearch: [],
  filterProperties: "",
  citiesData: "",
};

const homeSearchAreas = (state = initialState, action) => {
  switch (action.type) {
    case HOME_SEARCH:
      return {
        ...state,
        homeSearch: action.payload,
      };
    case HOME_PROPERTY_DATA:
      return {
        ...state,
        filterProperties: action.payload,
      };
    case CITIES:
      return {
        ...state,
        citiesData: action.payload,
      };
    default:
      return state;
  }
};
export default homeSearchAreas;
