import React, { useEffect } from "react";
import BestPropertyHeader from "../../components/bestPropertyHeader";
import ImageGallery from "../../components/imageGallery";
import Newsletter from "../../components/newsletter";
import Footer from "../../components/footer";
// import Featuredprojects from "../../components/featuredProjects";
import Breadcrumb from "../../components/breadcrumb";
import { connect } from "react-redux";
import { useLocation, useParams } from "react-router";
import { singlePropertyApi, relatedPropertiesApi } from "../../redux";
import RelatedProperties from "../../components/relatedProperties";
import Seo from "../../components/seo";

const Product = ({
  singlePropertyApi,
  singleProperty,
  relatedPropertiesApi,
}) => {
  const { productId } = useParams();

  let location = useLocation();

  useEffect(() => {
    singlePropertyApi(productId);
  }, [location]);

  // DESTRUCTURING FOR RELATED PROPERTIES START
  const {
    area_one_name,
    area_two_name,
    area_type,
    size,
    size_type,
    property_type,
    property_for,
    city,
  } = singleProperty;
  const data = {
    area_one_name,
    area_type,
    size,
  };
  // DESTRUCTURING FOR RELATED PROPERTIES END

  // USE-EFFECT FOR RELATED PROPERTIES
  useEffect(() => {
    if (singleProperty) {
      relatedPropertiesApi(data);
    }
  }, [singleProperty]);

  const heading =
    (singleProperty &&
      `Beautiful ${size} ${size_type} ${property_type} ${property_for} In ${area_one_name} ${area_two_name} ${city}`) ||
    `Loading...`;

  return (
    <div>
      <Seo
        title={`Beautiful ${size} Sq ft ${property_type} ${property_for} In ${area_one_name} ${area_two_name} ${city} | Chhatt.com`}
        description={`Find the ${size} Sq ft ${property_type} ${property_for} In ${area_two_name} ${area_one_name} ${city}. Chhatt.com provides you luxury apartments, flats and properties at best prices.`}
      />
      <BestPropertyHeader heading={heading} />
      <Breadcrumb productId={productId} />
      <ImageGallery singleProperty={singleProperty} />
      <RelatedProperties
        heading="Related Properties"
        singleProperty={singleProperty}
      />
      {/* <Featuredprojects
          title="Agencies"
          city="karachi"
          cardTitle="ESTATE IN"
          description="SAMEER SHAIKH"
          marginTop="marginTop"
          /> */}
      <Newsletter />
      <Footer />
    </div>
  );
};

const mapStatetoProps = (state) => {
  return {
    singleProperty: state.singlePropertyReducer.singleProperty,
  };
};
const mapDispatchtoProps = (dispatch) => {
  return {
    singlePropertyApi: function (productID) {
      dispatch(singlePropertyApi(productID));
    },
    relatedPropertiesApi: function (data) {
      dispatch(relatedPropertiesApi(data));
    },
  };
};

export default connect(mapStatetoProps, mapDispatchtoProps)(Product);
