import React from 'react'
// IMPORTING CSS
import Style from './Style.module.scss'
import cn from 'classnames'
import { Link } from 'react-router-dom'
import { makeStyles } from '@material-ui/core/styles'
import Button from '@material-ui/core/Button'

const useStyles = makeStyles(() => ({
  button: {
    textTransform: 'none',
  },
}))

const VisitPropertySite = ({ marginTop }) => {
  const classes = useStyles()

  return (
    <div className={cn(Style.maindiv, marginTop && Style.marginTop)}>
      <div className={Style.innerDiv}>
        <div className={Style.secOne}>
          <h2>Visit Our Construction Site</h2>
        </div>
        <div className={Style.secTwo}>
          Chhatt Media Pvt.ltd commonly known as chhatt.com is a powerful
          platform which aims at connecting the realtors, builders, developers,
          brokers with right kind of clientele for various types of properties
          from various mediums.
        </div>
        <div className={Style.secThree}>
          <Link to='/construction'>
            <Button variant='contained' classes={{ root: classes.button }}>
              Visit Now
            </Button>
          </Link>
        </div>
      </div>
    </div>
  )
}

export default VisitPropertySite
