import {
  MOBILE_BEST_PROPERTIES,
  MOBILE_AREAS_LIST,
  MOBILE_CITIES,
} from "./searchApiType";
import axios from "axios";

// SEARCH LIST IN FILTER
const areasData = (data) => {
  return {
    type: MOBILE_AREAS_LIST,
    payload: data,
  };
};

// SEARCH LIST IN FILTER
const citesData = (data) => {
  return {
    type: MOBILE_CITIES,
    payload: data,
  };
};

// HOME_SEARCH
export const mobileAreasListApi = () => {
  return (dispatch) => {
    axios.get("https://uat.chhatt.com/api/areaones").then((res) => {
      const areas = res.data;
      dispatch(areasData(areas.data));
    });
    axios.get("https://uat.chhatt.com/api/areatwos").then((res) => {
      const areas = res.data;
      dispatch(areasData(areas.data));
    });
    axios.get("https://uat.chhatt.com/api/areathrees").then((res) => {
      const areas = res.data;
      dispatch(areasData(areas.data));
    });

    // == CALLING CITIES LIST DATA START
    axios.get("https://uat.chhatt.com/api/cities").then((res) => {
      const cities = res.data;
      dispatch(citesData(cities.data));
    });
  };
};

// ================ PRODUCT SEARCH FROM PROPERTY SITE HEADER START ===============

const propertiesData = (data) => {
  return {
    type: MOBILE_BEST_PROPERTIES,
    payload: data,
  };
};
export const bestPropertyApi = (data) => {
  const {
    val,
    city,
    purpose,
    propertyType,
    areaType,
    beds,
    baths,
    areaMin,
    areaMax,
    priceMin,
    priceMax,
    p,
  } = data.urlData;

  const valEmpty = !val ? "" : val;
  const bedEmpty = !beds ? "" : beds;
  const bathEmpty = !baths ? "" : baths;
  const maxPriceEmpty = !priceMax ? "" : priceMax;
  const maxAreaEmpty = !areaMax ? "" : areaMax;
  const minAreaEmpty = !areaMin ? "" : areaMin;
  const minPriceEmpty = !priceMin ? "" : priceMin;
  const purposeProperty = purpose && purpose.replace("-", " ");
  const propertyTypeReplaced = propertyType && propertyType.replace("-", " ");

  // FILTERING ENCODE KEY
  return (dispatch) => {
    // ===== ROUTE FROM BUY BTN  START =====
    if ("allProperties" in data.urlData) {
      axios
        .get(
          `https://uat.chhatt.com/api/properties/search?search_areas=&page=${p}&status=1`
        )
        .then((res) => {
          const data = res;
          dispatch(propertiesData(data));
        });
    } else if ("propertySite" in data.urlData) {
      if (data.areasList.length && valEmpty) {
        const key = val.replace("%20", " ");
        const filtered = data.areasList.filter((prev) => key === prev.name);
        const encodeKey = encodeURI(filtered[0].key);
        axios
          .get(
            `https://uat.chhatt.com/api/properties/search?search_areas=${encodeKey}&area_type=${areaType}&property_for=${purposeProperty}&city=${city}&property_type=${propertyTypeReplaced}&bed=${bedEmpty}&bath=${bathEmpty}&min_price=${minPriceEmpty}&max_price=${maxPriceEmpty}&min_area=${minAreaEmpty}&max_area=${maxAreaEmpty}&page=${p}`
          )
          .then((res) => {
            const areas = res;
            dispatch(propertiesData(areas));
          });
      } else {
        axios
          .get(
            `https://uat.chhatt.com/api/properties/search?search_areas=${valEmpty}&area_type=${areaType}&property_for=${purposeProperty}&city=${city}&property_type=${propertyTypeReplaced}&bed=${bedEmpty}&bath=${bathEmpty}&min_price=${minPriceEmpty}&max_price=${maxPriceEmpty}&min_area=${minAreaEmpty}&max_area=${maxAreaEmpty}&page=${p}`
          )
          .then((res) => {
            const areas = res;
            dispatch(propertiesData(areas));
          });
      }
    }
    // ===== ROUTE FROM PROPERTY SITE (PROPERTY HEADER) SEARCH START =====
    // ===== ROUTE FROM BUY BTN END =====
  };
};

// ================ PRODUCT SEARCH FROM EXPLORE PROPERTY SITE START ===============
export const mobileExplorePropertyApi = (data) => {
  const replacingSpace = data[1].replace("-", " ");
  const gettingPage = (data[2] && data[2].split("-")) || 1;
  return (dispatch) => {
    axios
      .get(
        `https://uat.chhatt.com/api/properties/search?property_type=${replacingSpace}&page=${gettingPage[1]}&status=1`
      )
      .then((res) => {
        const data = res;
        dispatch(propertiesData(data));
      });
  };
};
// ================ PRODUCT SEARCH FROM EXPLORE PROPERTY SITE END ===============
