import React, { useState } from 'react'
import Style from './Style.module.scss'
import { BsPersonFill } from 'react-icons/bs'
import { RiSearch2Line } from 'react-icons/ri'
import ProgressiveImage from 'react-progressive-image'
import Ig1 from '../../../assets/counstruction.webp'
import Logo from '../../../assets/logo.webp'
import { CgMenuLeftAlt, CgArrowLongDown } from 'react-icons/cg'
import play from '../../../assets/icons/play.png'
import cn from 'classnames'
import { Link as Lk } from 'react-scroll'
import { Link } from 'react-router-dom'

export default function Header2() {
  const [lgo, setLgo] = useState(Logo)
  return (
    <header id='sctop'>
      <div className={cn(Style.main_div)} id='head1'>
        <div className={Style.s_div}>
          <div className={Style.logo} id='lgo'>
            {/* <CgMenuLeftAlt id="mnu" className={Style.menu_icn} /> */}
            <Link to='/'>
              <ProgressiveImage delay={600} src={lgo} placeholder={lgo}>
                {(src) => <img src={src} alt='background' />}
              </ProgressiveImage>
            </Link>
          </div>
          <div className={Style.tabss}>
            <ul>
              <Link style={{ color: '#fff' }} to='/' title='Home'>
                <li className={Style.lif}>HOME</li>
              </Link>
              <Link to='/property' style={{ color: '#fff' }} title='Property'>
                <li className={Style.lis}>Property Site</li>
              </Link>
              <Link
                style={{ color: '#fff' }}
                to='/construction'
                title='Construction'
              >
                <li className={Style.lit}>Construction Site</li>
              </Link>
            </ul>
          </div>
          <div className={cn(Style.signin)}>
            <div className={Style.addprod}>
              <button>Add Products</button>
            </div>
            {/* <div className={Style.signin_f} id="sing">
              Sign in
            </div> */}
            <div className={Style.signin_icn}>
              <BsPersonFill />
            </div>
          </div>
        </div>
      </div>
      <ProgressiveImage delay={400} src={Ig1} placeholder={Ig1}>
        {(src, loading) => (
          <div className={Style.mn_div}>
            <img
              style={{
                filter: loading ? 'blur(4px)' : 'blur(0px)',
              }}
              src={src}
              alt='house for sell and construction material'
              className={Style.imgs}
            />
            <div className={Style.backg}>
              <div className={Style.backg_sdiv}>
                <h1>All the best for a whole lot less</h1>
                <span style={{ marginTop: '5px' }}>
                  <p>Here to make your life easy</p>
                </span>
                <div className={Style.main_bar}>
                  <div className={Style.searchdiv}>
                    <span>
                      <RiSearch2Line fontSize='26px' />
                    </span>
                    <input
                      placeholder='Search what you want'
                      className={Style.inp2}
                    ></input>
                    <select>
                      <option>Karachi</option>
                      <option>Karachi</option>
                      <option>Karachi</option>
                      <option>Karachi</option>
                    </select>
                    <button>Search</button>
                  </div>
                  <div className={Style.secategories}>
                    <div className={Style.secategories_first_div}>
                      <div className={Style.explore}>
                        Explore our categories
                      </div>
                      <div className={Style.viewmore}>View More</div>
                    </div>
                    <div className={Style.secategories_sec_div}>
                      <ul className={Style.sul}>
                        <li>
                          <button
                            style={{
                              backgroundColor: '#007bff',
                              color: '#ffffff',
                            }}
                          >
                            All
                          </button>
                        </li>
                        <li>
                          <button>Pumping</button>
                        </li>
                        <li>
                          <button>Plastic,rubber</button>
                        </li>
                        <li>
                          <button>Industrial</button>
                        </li>
                        <li>
                          <button>Drilling Bits</button>
                        </li>
                        <li>
                          <button>Wire Mesh</button>
                        </li>
                        <li>
                          <button>Electric Motors</button>
                        </li>
                      </ul>
                    </div>
                  </div>
                </div>
              </div>
              <div className={Style.bottom}>
                <img src={play} width='170px' alt='' />
              </div>
              <div className={Style.scroll}>
                <Lk smooth={true} duration={500} to='find'>
                  <div className={Style.txtScroll}>Scroll</div>
                  <div>
                    <CgArrowLongDown size={30} />
                  </div>
                </Lk>
              </div>
            </div>
          </div>
        )}
      </ProgressiveImage>
    </header>
  )
}
