import React from "react";
import Pagination from "@material-ui/lab/Pagination";
import Style from "./Style.module.scss";
import { useHistory } from "react-router-dom";

const PaginationC = ({ urlData, localHomeSearchData, citiesData }) => {
  const history = useHistory();
  const HandleChange = (event, value) => {
    if (urlData.city) {
      // FILTERING CITIES WITH NUMBERS
      const filteredCity =
        citiesData && citiesData.filter((prev) => prev.id === urlData.city);
      urlData = {
        ...urlData,
        city: filteredCity && filteredCity[0].name.toLowerCase(),
      };
    }
    // PROPERTY LIST PAGE (NEW PRODUCT) PAGINATION START
    if ("home" in urlData) {
      if (urlData.val) {
        history.push(
          `/results/?home&val=${urlData.val}&city=${urlData.city}&p=${value}`
        );
      } else {
        history.push(`/results?home&city=${urlData.city}&p=${value}`);
      }
    } else if ("allProperties" in urlData) {
      // BREADCRUMB AND EXPLORE BY PROPERTY TYPE APIS START
      if ("city" in urlData) {
        history.push(`/results?allProperties&city=${urlData.city}&p=${value}`);
      } else if ("areaType" in urlData) {
        history.push(
          `/results?allProperties&areaType=${urlData.areaType}&p=${value}`
        );
      } else if ("propertyType" in urlData) {
        history.push(
          `/results?allProperties&propertyType=${urlData.propertyType}&p=${value}`
        );
      } else if ("purpose" in urlData) {
        history.push(
          `/results?allProperties&purpose=${urlData.purpose}&p=${value}`
        );
      } else {
        history.push(`/results?allProperties&p=${value}`);
      }
      // BREADCRUMB AND EXPLORE BY PROPERTY TYPE APIS END
    } else if ("propertySite" in urlData) {
      history.push(
        `/results?propertySite&areaType=${urlData.areaType}&propertyType=${
          urlData.propertyType
        }&purpose=${urlData.purpose}${
          (urlData.val && `&val=${urlData.val}`) || ""
        }&city=${urlData.city}${
          (urlData.beds && `&beds=${urlData.beds}`) || ""
        }${(urlData.baths && `&baths=${urlData.baths}`) || ""}${
          (urlData.priceMin && `&priceMin=${urlData.priceMin}`) || ""
        }${(urlData.priceMax && `&priceMax=${urlData.priceMax}`) || ""}${
          (urlData.areaMin && `&areaMin=${urlData.areaMin}`) || ""
        }${(urlData.areaMax && `&areaMax=${urlData.areaMax}`) || ""}&p=${value}`
      );
      // PROPERTY LIST PAGE (NEW PRODUCT) PAGINATION START
    }
  };

  return (
    <>
      {localHomeSearchData && localHomeSearchData.data.meta.total > 28 && (
        <div className={Style.main_div}>
          <Pagination
            count={localHomeSearchData.data.meta.last_page}
            variant="outlined"
            shape="rounded"
            onChange={HandleChange}
            page={Number(urlData.p)}
          />
        </div>
      )}
    </>
  );
};

export default PaginationC;
