import {
  MOBILEMENU,
  MODALOPEN,
  MODALCLOSE,
  MOBILEMENUCLOSE,
} from "./welcomeType";

export const mobileMenu = () => {
  return {
    type: MOBILEMENU,
  };
};
export const mobileMenuclose = () => {
  return {
    type: MOBILEMENUCLOSE,
  };
};
export const modalopen = () => {
  return {
    type: MODALOPEN,
  };
};
export const modalclose = () => {
  return {
    type: MODALCLOSE,
  };
};
