import React from "react";
import Pagination from "@material-ui/lab/Pagination";
import Style from "./Style.module.scss";
import { useHistory } from "react-router-dom";

const MobilePaginationC = ({ urlData, localHomeSearchData }) => {
	const history = useHistory();

	const HandleChange = (event, value) => {
		if ("allProperties" in urlData) {
			history.push(`/results?allProperties&p=${value}`);
		} else if ("ePropertyType" in urlData) {
			history.push(
				`/results?ePropertyType&propertyType=${urlData.propertyType}&p=${value}`
			);
		} else if ("propertySite" in urlData) {
			history.push(
				`/results?propertySite&areaType=${urlData.areaType}&propertyType=${urlData.propertyType}&purpose=${urlData.purpose}&val=${urlData.val}&city=${urlData.city}&beds=${urlData.beds}&baths=${urlData.baths}&priceMin=${urlData.priceMin}&priceMax=${urlData.priceMax}&areaMin=${urlData.areaMin}&areaMax=${urlData.areaMax}&p=${value}`
			);
		}
	};

	return (
		<>
			{localHomeSearchData && localHomeSearchData.data.meta.total > 28 && (
				<div className={Style.main_div}>
					<Pagination
						count={localHomeSearchData.data.meta.last_page}
						variant="outlined"
						shape="rounded"
						onChange={HandleChange}
						siblingCount={0}
						page={Number(urlData.p)}
					/>
				</div>
			)}
		</>
	);
};

export default MobilePaginationC;
