import {
	MOBILE_FEATURED_PROJECTS,
	MOBILE_AGENCIES_API,
	MOBILE_AGENCIES_LIST,
	MOBILE_AGENCY_PROPERTIES,
	MOBILE_AGENCY_INPUT_ON_CHANGE,
} from "./featuredProjectsType";
import axios from "axios";

// FEATURED PROJECTS API START
const featuredProperties = (data) => {
	return {
		type: MOBILE_FEATURED_PROJECTS,
		payload: data,
	};
};

export const featuredPropertiesApi = () => {
	return (dispatch) => {
		axios.get(`https://uat.chhatt.com/api/projects`).then((res) => {
			const response = res.data;
			// dispatch(featuredProperties(response.data));
		});
	};
};
// FEATURED PROJECTS API END

// AGENCIES API START
const agenciesFunc = (data) => {
	return {
		type: MOBILE_AGENCIES_API,
		payload: data,
	};
};

export const mobileAgenciesApi = () => {
	return (dispatch) => {
		axios.get(`https://uat.chhatt.com/api/agencies`).then((res) => {
			const response = res.data;
			dispatch(agenciesFunc(response.data));
		});
	};
};
// AGENCIES API END

// AGENCIES LIST API START
const agenciesListFunc = (data) => {
	return {
		type: MOBILE_AGENCIES_LIST,
		payload: data,
	};
};

export const mobileAgenciesListApi = (pageNo) => {
	return (dispatch) => {
		axios
			.get(`https://uat.chhatt.com/api/agencies?page=${pageNo}`)
			.then((res) => {
				const response = res;
				dispatch(agenciesListFunc(response));
			});
	};
};
// AGENCIES LIST API END

// AGENCY API START
const agencyProperties = (data) => {
	return {
		type: MOBILE_AGENCY_PROPERTIES,
		payload: data,
	};
};

export const mobileAgencyPropertiesApi = (productID) => {
	const id = productID.split("-");
	return (dispatch) => {
		axios
			.get(`https://uat.chhatt.com/api/properties/search?agency_id=${id[0]}`)
			.then((res) => {
				const response = res.data;
				dispatch(agencyProperties(response.data));
			});
	};
};
// AGENCY API END

// ==== AGENCIES SEARCH / FILTER START
export const mobileAgenciesSearchApi = (data) => {
	return (dispatch) => {
		axios
			.get(`http://uat.chhatt.com/api/agencies?keyword=${data}`)
			.then((res) => {
				const agencies = res;
				dispatch(agenciesListFunc(agencies));
			});
	};
};
// ==== AGENCIES SEARCH / FILTER END

// ==== AGENCIES SEARCH / FILTER ONCHANGE EVENT START
export const mobileAgencyInputOnChange = (data) => {
	return {
		type: MOBILE_AGENCY_INPUT_ON_CHANGE,
		payload: data,
	};
};
// ==== AGENCIES SEARCH / FILTER ONCHANGE EVENT END
