import React from "react";
import noFound from "../../meta.json";
import { Helmet } from "react-helmet";

export default function Seo({ title, description }) {
  const metaDescription = description || noFound.description;
  const metatitle = title || noFound.title;
  return (
    <Helmet>
      <title>{metatitle}</title>
      <meta property="og:title" content={metatitle} />
      <meta name="description" content={metaDescription} />
      <meta name="twitter:card" content="summary_large_image" />
      <meta name="twitter:site" content="" />
      <meta name="twitter:title" content={metatitle} />
      <meta itemprop="name" content={metatitle} />
      <meta itemprop="description" content={metaDescription} />
      <meta property="og:description" content={metaDescription} />
      <meta name="twitter:description" content={metaDescription} />
      <meta itemprop="description" content={metaDescription} />
      <meta property="og:image" content="" />
      <meta name="twitter:image" content="" />
      <meta itemprop="image" content="" />
      <meta property="og:url" content="https://www.chhatt.com/" />
      <meta itemprop="url" content="https://www.chhatt.com/" />
    </Helmet>
  );
}
