import React from "react";
import Footer_Play from "../../../assets/footer_play.png";
import Footer_App from "../../../assets/footer_app.png";
import Style from "./Style.module.scss";
import {
  FaFacebookF,
  FaTwitter,
  FaLinkedinIn,
  FaInstagram,
} from "react-icons/fa";
import { NavLink } from "react-router-dom";

const Footer = () => {
  return (
    <footer className={Style.main_div}>
      <div className={Style.play_app_icons}>
        <img src={Footer_Play} alt="playstore" />
        <img src={Footer_App} alt="appstore" />
      </div>
      <div className={Style.linksContainer}>
        <div className={Style.innerLinks}>
          <NavLink exact to="/property">
            Property
          </NavLink>
          |
          <NavLink exact to="/">
            Construction
          </NavLink>
        </div>
        <div className={Style.shareIcons}>
          <FaFacebookF className={`${Style.sIcon}`} />
          <FaTwitter className={`${Style.sIcon} ml-2`} />
          <FaLinkedinIn className={`${Style.sIcon} ml-2`} />
          <FaInstagram className={`${Style.sIcon} ml-2`} />
        </div>
        <div className={Style.copyright_line}>Copyright 2021 Chhatt.com</div>
      </div>
    </footer>
  );
};

export default Footer;
