import {
	MOBILE_SINGLE_PROPERTY,
	MOBILE_SINGLE_AGENCY,
} from "./propertyDetailType";
import axios from "axios";

const singleProperty = (data) => {
	return {
		type: MOBILE_SINGLE_PROPERTY,
		payload: data,
	};
};

export const singlePropertyApi = (productID) => {
	const id = productID.split("-");
	return (dispatch) => {
		axios
			.get(`https://uat.chhatt.com/api/properties/${id.pop()}&status=1`)
			.then((res) => {
				const singleData = res.data;
				dispatch(singleProperty(singleData.data));
			});
	};
};

const singleAgency = (data) => {
	return {
		type: MOBILE_SINGLE_AGENCY,
		payload: data,
	};
};

export const MobileSingleAgencyApi = (productID) => {
	return (dispatch) => {
		axios
			.get(`https://uat.chhatt.com/api/agencies?id=${productID}`)
			.then((res) => {
				const singleData = res.data;
				dispatch(singleAgency(singleData.data));
			});
	};
};
