import {
  FEATURED_PROJECTS,
  AGENCIES_API,
  AGENCIES_LIST,
  AGENCY_PROPERTIES,
  AGENCY_INPUT_ON_CHANGE,
} from "./featuredProjectsType";
import config from "../../../config";
import axios from "axios";

// FEATURED PROJECTS API START
const featuredProperties = (data) => {
  return {
    type: FEATURED_PROJECTS,
    payload: data,
  };
};

export const featuredPropertiesApi = () => {
  return (dispatch) => {
    axios.get(`${config.apiUrl}/projects`).then((res) => {
      const response = res.data;
      // dispatch(featuredProperties(response.data));
    });
  };
};
// FEATURED PROJECTS API END

// AGENCIES API START
const agenciesFunc = (data) => {
  return {
    type: AGENCIES_API,
    payload: data,
  };
};

export const agenciesApi = () => {
  return (dispatch) => {
    axios.get(`${config.apiUrl}/agencies`).then((res) => {
      const response = res.data;
      dispatch(agenciesFunc(response.data));
    });
  };
};
// AGENCIES API END

// AGENCIES LIST API START
const agenciesListFunc = (data) => {
  return {
    type: AGENCIES_LIST,
    payload: data,
  };
};

export const agenciesListApi = (urlData) => {
  return (dispatch) => {
    if (urlData) {
      axios.get(`${config.apiUrl}/agencies?page=${urlData}`).then((res) => {
        const response = res;
        dispatch(agenciesListFunc(response));
      });
    }
  };
};
// AGENCIES LIST API END

//  SINGLE AGENCY API START
const agencyProperties = (data) => {
  return {
    type: AGENCY_PROPERTIES,
    payload: data,
  };
};
export const agencyPropertiesApi = (productID) => {
  const id = productID.split("-");
  return (dispatch) => {
    axios
      .get(`${config.apiUrl}/properties/search?agency_id=${id[0]}`)
      .then((res) => {
        const response = res.data;
        dispatch(agencyProperties(response.data));
      });
  };
};
// SINGLE AGENCY API END

// ==== AGENCIES SEARCH / FILTER START
export const agenciesSearchApi = (data) => {
  return (dispatch) => {
    axios.get(`${config.apiUrl}/agencies?keyword=${data}`).then((res) => {
      const agencies = res;
      dispatch(agenciesListFunc(agencies));
    });
  };
};
// ==== AGENCIES SEARCH / FILTER END

// ==== AGENCIES SEARCH / FILTER ONCHANGE EVENT START
export const agencyInputOnChange = (data) => {
  return {
    type: AGENCY_INPUT_ON_CHANGE,
    payload: data,
  };
};
// ==== AGENCIES SEARCH / FILTER ONCHANGE EVENT END
