import React from "react";
import Style from "./Style.module.scss";
import ILocation from "../../../../assets/welcome/location.webp";
import { RiArrowRightCircleFill } from "react-icons/ri";

export default function Location() {
  return (
    <div className={Style.main} id="Location">
      <div className={Style.right}>
        <p className={Style.para}>MAP POINT</p>
        <h1 className={Style.head}>LOCATION HIGHLIGHTS</h1>
        <p className={Style.para1}>
          <span>
            <RiArrowRightCircleFill
              size="25px"
              style={{ marginTop: "-3px", marginRight: "10px" }}
            />
          </span>
          <span>
            It is just 15 minutes drive from gulberg to other parts of Islamabad
          </span>
        </p>
        <p className={Style.para1}>
          <span>
            <RiArrowRightCircleFill
              size="25px"
              style={{ marginTop: "-3px", marginRight: "10px" }}
            />
          </span>
          <span>10 minutes drive towards airport</span>
        </p>
        <p className={Style.para1}>
          <span>
            <RiArrowRightCircleFill
              size="25px"
              style={{ marginTop: "-3px", marginRight: "10px" }}
            />
          </span>
          <span>5 minutes drive to G.T road.</span>
        </p>
        <p className={Style.para1}>
          <span>
            <RiArrowRightCircleFill
              size="25px"
              style={{ marginTop: "-3px", marginRight: "10px" }}
            />
          </span>
          <span>2 Minutes drive from Gulberg green.</span>
        </p>
        <p style={{ textAlign: "left" }} className={Style.para1}>
          <span>
            <RiArrowRightCircleFill
              size="25px"
              style={{ marginTop: "-3px", marginRight: "10px" }}
            />
          </span>
          <span>
            In Gulberg knowledge village there are many schools, colleges and
            universities which are under constructions.
          </span>
        </p>
        <p className={Style.para1}>
          <span>
            <RiArrowRightCircleFill
              size="25px"
              style={{ marginTop: "-3px", marginRight: "10px" }}
            />
          </span>
          5 star hotels ,shopping malls are also under construction Gulberg
          Islamabad is a place
        </p>
        <table />
        <a
          href="https://www.google.com/maps/place/Welcome+Center/@33.5988112,73.2140482,15z/data=!4m12!1m6!3m5!1s0x0:0x7f9ab1349dbc81b!2sWelcome+Center!8m2!3d33.5988112!4d73.2140482!3m4!1s0x0:0x7f9ab1349dbc81b!8m2!3d33.5988112!4d73.2140482"
          target="_blank"
          className={Style.btns1}
        >
          Get Direction on Map
        </a>
      </div>
      <div className={Style.left}>
        <img src={ILocation} alt="" />
      </div>
    </div>
  );
}
