import React, { useEffect } from "react";
import Style from "./Style.module.scss";
import Header from "../../components/header";
import BottomNavigation from "../../components/bottomNavigation";
import {
  AgencyAboutUs,
  AgencyTop,
  ImageSection,
} from "../../components/agencyDetails";
import { MobileSingleAgencyApi, mobileAgencyPropertiesApi } from "../../redux";
import { connect } from "react-redux";
import { useParams } from "react-router";
import AgentsSlider from "../../components/agentsSlider";
import MobileRelatedProperties from "../../components/relatedProperties";

const MobileAgency = ({
  MobileSingleAgencyApi,
  singleAgency,
  mobileAgencyPropertiesApi,
  mobileAgencyProperties,
}) => {
  const { id } = useParams();
  useEffect(() => {
    MobileSingleAgencyApi(id);
    mobileAgencyPropertiesApi(id);
  }, []);

  return (
    <div>
      <Header />
      <AgencyTop singleAgency={singleAgency} />
      <ImageSection singleAgency={singleAgency} />

      {/* <AgencyAboutUs singleAgency={singleAgency} /> */}

      {(singleAgency && singleAgency.agents.length && (
        <AgentsSlider singleAgency={singleAgency} />
      )) ||
        null}

      {mobileAgencyProperties && !mobileAgencyProperties.length && (
        <>
          <br />
          <br />
          <br />
          <br />
        </>
      )}

      {(mobileAgencyProperties && mobileAgencyProperties.length && (
        <MobileRelatedProperties
          heading="Agency Properties"
          mobileAgencyProperties={mobileAgencyProperties}
        />
      )) ||
        null}

      <BottomNavigation />
    </div>
  );
};

const mapStatetoProps = (state) => {
  return {
    singleAgency: state.singlePropertyReducer.singleAgency,
    mobileAgencyProperties:
      state.mobileFeaturedPropertiesReducer.mobileAgencyProperties,
  };
};
const mapDispatchtoProps = (dispatch) => {
  return {
    MobileSingleAgencyApi: function (id) {
      dispatch(MobileSingleAgencyApi(id));
    },
    mobileAgencyPropertiesApi: function (id) {
      dispatch(mobileAgencyPropertiesApi(id));
    },
  };
};
export default connect(mapStatetoProps, mapDispatchtoProps)(MobileAgency);
