import { BEST_PROPERTIES_DATA } from "./bestPropertType";
import config from "../../../config";
import axios from "axios";

const bestPropertiesFunc = (data) => {
  return {
    type: BEST_PROPERTIES_DATA,
    payload: data,
  };
};

export const bestPropertiesApi = (areaType) => {
  const areaT = areaType === undefined ? "" : areaType;

  return (dispatch) => {
    axios
      .get(`${config.apiUrl}/properties/search?area_type=${areaT}&status=1`)
      .then((res) => {
        const bestProperties = res.data;
        dispatch(bestPropertiesFunc(bestProperties.data));
      });
  };
};
