import React from "react";
import Style from "./Style.module.scss";
import { Link } from "react-router-dom";

export default function MobileAgencyCard({ prev }) {
  const { image, city, agency_name, owner_name, id } = prev;
  return (
    <Link
      to={`/agency/${id}-${agency_name
        .replace(/ /g, "-")
        .toLowerCase()}-${city.toLowerCase()}`}
    >
      <div className={Style.main_div}>
        <img src={image} className={Style.propertyImg} alt={agency_name} />
        <p> {city} </p>
        <b> {agency_name} </b>
        <p>
          <small> {owner_name} </small>
        </p>
      </div>
    </Link>
  );
}
