import React from "react";
import Style from "./Style.module.scss";
import { GoHome } from "react-icons/go";
import { VscTools } from "react-icons/vsc";
import Realtor from "../../../assets/mobiles/realtor.svg";
import Construction from "../../../assets/mobiles/construction.svg";
import { useHistory } from "react-router-dom";

export default function HomeCategory() {
  const history = useHistory();

  const historyPush = () => {
    history.push("/property");
  };

  return (
    <div className={Style.main_div}>
      <div className={Style.fdiv}>
        <div className={Style.top}>
          <p>Categories</p>
        </div>
        <div className={Style.sdiv}>
          <ul>
            <li onClick={historyPush}>
              <div>
                <img color="#007bff" width="25px" src={Realtor} alt="realtor" />
              </div>
              <p>Realtor</p>
            </li>
            <li onClick={historyPush}>
              <div>
                <GoHome color="#007bff" fontSize="25px" />
              </div>
              <p>Property</p>
            </li>
            <li>
              <div>
                <VscTools color="#007bff" fontSize="25px" />
              </div>
              <p>Construction</p>
            </li>
            <li>
              <div>
                <img
                  color="#007bff"
                  width="25px"
                  src={Construction}
                  alt="constructor"
                />
              </div>
              <p>contractor</p>
            </li>
          </ul>
        </div>
      </div>
    </div>
  );
}
