import { MOBILE_RELATED_PROPERTIES } from "./relatedPropertiesType";
import axios from "axios";

const relatedProperties = (data) => {
  return {
    type: MOBILE_RELATED_PROPERTIES,
    payload: data,
  };
};

export const relatedPropertiesApi = (data) => {
  const { area_one_name, area_type, size } = data;
  return (dispatch) => {
    axios
      .get(
        `https://uat.chhatt.com/api/properties/related?area_one_name=${area_one_name}&area_type=${area_type}&size=${size}&status=1`
      )
      .then((res) => {
        const relatedPropertiesData = res.data;
        dispatch(relatedProperties(relatedPropertiesData.data));
      });
  };
};
