import { CITY_SEARCH } from "./breadcrumbType";

const initialState = {
  citySearch: "",
};

const breadcrumbReducer = (state = initialState, action) => {
  switch (action.type) {
    case CITY_SEARCH:
      return {
        ...state,
        citySearch: action.payload,
      };
    default:
      return state;
  }
};
export default breadcrumbReducer;
