import React from "react";
import ImageGalleryC from "react-image-gallery";
import "react-image-gallery/styles/css/image-gallery.css";
import { ContactForm, LoginForm, SaleTage } from "../contactForm";
import { Overview, Description, Features } from "../productDetails";
import Map from "../productDetails";
import Img from "../../../assets/footer.webp";
import Style from "./Style.module.scss";

const ImageGallery = ({ map, singleProperty }) => {
  return (
    <div className={Style.maindiv}>
      <div className={Style.slider}>
        {map ? (
          <>
            {/* MAP IMAGE FOR MAP PAGE START */}
            <div className={Style.mapImage}>
              <h3> Hello world </h3>
              <img src={Img} alt="" />
            </div>
            {/* MAP IMAGE FOR MAP PAGE END */}
          </>
        ) : (
          <ImageGalleryC
            autoPlay={false}
            showIndex={true}
            showPlayButton={false}
            items={singleProperty ? singleProperty.images : []}
            allowFullScreen={true}
          />
        )}
        {/* HIDDEN FOR MAP PAGE START */}
        {!map && (
          <>
            <Overview singleProperty={singleProperty} />
            {singleProperty && singleProperty.description && (
              <Description
                heading="Description"
                singleProperty={singleProperty.description}
              />
            )}
            {/* <Features /> */}
          </>
        )}
        {/* HIDDEN FOR MAP PAGE END */}
        {singleProperty && <Map singleProperty={singleProperty} />}
      </div>
      <div className={Style.formContainer}>
        {/* HIDDEN FOR MAP PAGE START */}
        {!map && (
          <>
            <SaleTage singleProperty={singleProperty} />
            <br />
          </>
        )}
        {/* HIDDEN FOR MAP PAGE END */}
        <ContactForm singleProperty={singleProperty} />
        <br />
        {/* <LoginForm /> */}
      </div>
    </div>
  );
};

export default ImageGallery;
