import React, { useEffect } from "react";
import Style from "./Style.module.scss";
import MobileAgencyCard from "../cards/agencyCard";
import { connect } from "react-redux";
import { mobileAgenciesApi } from "../../redux";
import { Link } from "react-router-dom";

const MobileAgencies = ({ mobileAgenciesApi, mobileAgencies }) => {
  useEffect(() => {
    mobileAgenciesApi();
  }, []);

  const agencies10 = mobileAgencies.slice(0, 10);

  return (
    <div>
      <div className={Style.main_div}>
        <div className={Style.fdiv}>
          <div className={Style.top}>
            <p>Agencies</p>
            <Link to="/real-estate-agencies">View All</Link>
          </div>
          <div className={Style.sdiv}>
            <ul>
              {agencies10 &&
                agencies10.map((prev, i) => {
                  return (
                    <li key={i} className="ml-0">
                      <MobileAgencyCard prev={prev} />
                    </li>
                  );
                })}
            </ul>
          </div>
        </div>
      </div>
    </div>
  );
};

const mapStatetoProps = (state) => {
  return {
    mobileAgencies: state.mobileFeaturedPropertiesReducer.mobileAgencies,
  };
};
const mapDispatchtoProps = (dispatch) => {
  return {
    mobileAgenciesApi: function () {
      dispatch(mobileAgenciesApi());
    },
  };
};
export default connect(mapStatetoProps, mapDispatchtoProps)(MobileAgencies);
