import { SINGLE_PROPERTY , SINGLE_AGENCY } from "./propertyDetailType";

const initialState = {
  singleProperty: "",
  singleAgency: "",
};

const singlePropertyReducer = (state = initialState, action) => {
  switch (action.type) {
    case SINGLE_PROPERTY:
      return {
        ...state,
        singleProperty: action.payload,
      };
    case SINGLE_AGENCY:
      return {
        ...state,
        singleAgency: action.payload,
      };
    default:
      return state;
  }
};
export default singlePropertyReducer;
