import React from 'react'
// import { BsPersonFill } from "react-icons/bs";
import ProgressiveImage from 'react-progressive-image'
import { Link } from 'react-router-dom'
import cn from 'classnames'
import Style from './Style.module.scss'
import lgo from '../../../assets/logow.png'
import Ig1 from '../../../assets/Main_Banner.webp'
// import play from "../../../assets/icons/play.png";
import { useForm } from 'react-hook-form'
import swal from 'sweetalert'
import axios from 'axios'
import Seo from '../../components/seo'
import { mainBanner } from '../images'
import Button from '@material-ui/core/Button'
import { makeStyles } from '@material-ui/core/styles'

const useStyles = makeStyles(() => ({
  button: {
    textTransform: 'none',
    marginTop: '1rem',
    backgroundColor: '#007bff',
    padding: '0.5rem 1rem',
    borderRadius: '4px',
    textDecoration: 'none',
    color: '#fff',
    fontWeight: 600,
    '&:hover': {
      backgroundColor: '#007bff',
    },
  },
  notifyButton: {
    textTransform: 'none',
  },
}))
const Construction = () => {
  const classes = useStyles()

  const {
    register,
    handleSubmit,
    formState: { errors },
    reset,
  } = useForm()

  const onSubmit = (data) => {
    axios
      .get(`https://uat.chhatt.com/api/newsletter?email=${data}`)
      .then(() => swal('Thank You!', 'For Supporting Us', 'success'))
    reset()
  }

  // FOR NAVBAR SCROLLING EFFECT START
  window.addEventListener('scroll', function () {
    var header = document.querySelector('.navbarContainer')
    header.classList.toggle('sticky', window.scrollY > 1)
  })
  return (
    <>
      <Seo
        title='Pakistan’s Best Construction Materials & Services | Chhatt.com'
        description='Looking for the best construction materials and services provider in Pakistan. Chhatt.com provides all types of construction and building materials visit our website.'
      />
      <header id='sctop'>
        {/* <CgMenuLeftAlt id="mnu" className={Style.menu_icn} /> */}
        <div className={cn(Style.main_div)} id='head1'>
          <div className={`${Style.s_div} navbarContainer`}>
            <div className='innerContainer'>
              <div className={Style.logo} id='lgo'>
                <Link to='/'>
                  <ProgressiveImage delay={600} src={lgo} placeholder={lgo}>
                    {(src) => (
                      <img
                        src={src}
                        alt='house for sale and construction material'
                      />
                    )}
                  </ProgressiveImage>
                </Link>
              </div>
              <div className={Style.tabss}>
                <ul>
                  <Link style={{ color: '#fff' }} title='Home' to='/'>
                    <li className={cn(Style.lis)}>
                      <span>HOME</span>
                    </li>
                  </Link>
                  <Link
                    to='/property'
                    style={{ color: '#fff' }}
                    title='Property'
                  >
                    <li className={cn(Style.lis)}>
                      <span>Property</span>
                    </li>
                  </Link>
                  <Link
                    style={{ color: '#fff' }}
                    to='/construction'
                    title='Construction'
                  >
                    <li className={cn(Style.lif)}>
                      <span>Construction</span>
                    </li>
                  </Link>
                  <Link
                    style={{ color: '#fff' }}
                    to='/real-estate-agencies'
                    title='real-estate-agencies'
                  >
                    <li className={cn(Style.lis)}>
                      <span>Agencies</span>
                    </li>
                  </Link>
                  <Link
                    style={{ color: '#fff' }}
                    to='/contact-us'
                    title='contact-us'
                  >
                    <li className={cn(Style.lis)}>
                      <span>Contact Us</span>
                    </li>
                  </Link>
                  <Link style={{ color: '#fff' }} to='/wanted' title='wanted'>
                    <li className={cn(Style.lit)}>
                      <span>Wanted</span>
                    </li>
                  </Link>
                </ul>
              </div>
            </div>
            <div className={cn(Style.signin)}>
              {/* <div className={Style.signin_icn}>
              <BsPersonFill />
            </div> */}
            </div>
          </div>
        </div>

        <ProgressiveImage delay={400} src={mainBanner} placeholder={Ig1}>
          {(src, loading) => (
            <div className={Style.mn_div}>
              <img
                style={{
                  filter: loading ? 'blur(4px)' : 'blur(0px)',
                }}
                src={src}
                alt='construction'
                className={Style.imgs}
              />
              <div className={Style.backg}>
                <div className={Style.backg_sdiv}>
                  <h1>Coming Soon</h1>
                  <h5>Get ready! Something really cool is coming!</h5>
                  <br />
                  <div className={Style.input_div}>
                    <div>
                      <input
                        style={
                          errors.email && {
                            border: '2px solid #ff040d',
                          }
                        }
                        size='50'
                        placeholder='Enter your Email'
                        type='email'
                        name='email'
                        {...register('email', {
                          required: true,
                          pattern:
                            /^(([^<>()[\]\\.,;:\s@\"]+(\.[^<>()[\]\\.,;:\s@\"]+)*)|(\".+\"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/,
                        })}
                      />
                      <div className={Style.inpErrors}>
                        {errors.email &&
                          errors.email.type === 'required' &&
                          'Email is required'}
                        {errors.email &&
                          errors.email.type === 'pattern' &&
                          'please enter valid email'}
                      </div>
                    </div>
                    <div>
                      <Button
                        variant='contained'
                        classes={{ root: classes.notifyButton }}
                        onClick={handleSubmit(onSubmit)}
                      >
                        Notify me
                      </Button>
                    </div>
                  </div>
                  <Link to='/'>
                    <Button
                      variant='contained'
                      className={Style.backToHome}
                      classes={{ root: classes.button }}
                    >
                      Back to Home
                    </Button>
                  </Link>
                </div>
                {/* <div className={Style.bottom}>
                <img src={play} width="170px" alt="" />
              </div> */}
              </div>
            </div>
          )}
        </ProgressiveImage>
      </header>
    </>
  )
}

export default Construction
