import React from "react";
import Style from "./Style.module.scss";
import BestPropertyHeader from "../../components/bestPropertyHeader";
import About from "../../../assets/AboutUs.webp";
import WeAre from "../../../assets/we_are.webp";
import WeDo from "../../../assets/wedo.webp";
import Newsletter from "../../components/newsletter";
import Footer from "../../components/footer";
import Seo from "../../components/seo";

const AboutUs = () => {
  return (
    <>
      <Seo
        title="Chhatt.com: About US – Buy Sell and Build"
        description="About us – Buy Build and Sell properties on Pakistan’s No.1 real estate portal. Must visit Chhatt.com and stay connected with leading property portal."
      />
      <BestPropertyHeader heading="About Us" />
      {/* ABOUT SECTION TEXT START */}
      <div className={Style.maindiv}>
        <div className={Style.aboutSection1}>
          <div>
            <img src={About} alt="Aboute Us" />
          </div>
          <div>
            <h2>About Chhatt</h2>
            <br />
            <p>
              Chhatt Media (private) limited commonly known as chhatt.com is a
              powerful platform which aims at connecting the realtors, builders,
              developers, brokers with right kind of clientele for various types
              of properties from various mediums. It is a mix of a web platform
              as well as a mobile application(s).
            </p>
          </div>
        </div>

        <br />
        <br />
        <br />
        <hr />
        <br />
        <br />
        <br />

        <div className={Style.aboutSection2}>
          <div>
            <h3>What we are?</h3>
            <br />
            <p>
              Chhatt’s Management has over 20 years of experience in Information
              and technology, management and Real Estate business.Technology is
              changing the way all types of business are run in this era and
              real estate business is no different. The speed at which the
              communication mediums are progressing and evolving are simply
              challenging the limits of the way the business processes were
              carried out before
            </p>
          </div>
          <div>
            <img src={WeAre} alt="WeAre" />
          </div>
        </div>
        <br />
        <br />
        <br />
        <div className={Style.aboutSection3}>
          <div>
            <img src={WeDo} alt="WeDo" />
          </div>
          <div>
            <h3>What we do?</h3>
            <br />
            <p>
              Chhatt aims and relies on simple and easier communication with
              clients and providing real estate solutions at the touch of a
              finger. In short, Chhatt is a point of elevation which gives you a
              strategic view of the real estate market "
            </p>
          </div>
        </div>
      </div>
      {/* ABOUT SECTION TEXT END */}
      <Newsletter />
      <Footer />
    </>
  );
};

export default AboutUs;
