import {
  MOBILEMENU,
  MODALOPEN,
  MODALCLOSE,
  MOBILEMENUCLOSE,
} from "./welcomeType";

const initialState = {
  menushow: null,
  sToggle: false,
  popid: "",
  openmodal: false,
  btcopy: "Copy",
  mobileMenuToggle: false,
  modalIsOpen: false,
};

const MenuState = (state = initialState, action) => {
  switch (action.type) {
    case MOBILEMENU:
      return {
        ...state,
        mobileMenuToggle: true,
      };
    case MOBILEMENUCLOSE:
      return {
        ...state,
        mobileMenuToggle: false,
      };
    case MODALOPEN:
      return {
        ...state,
        modalIsOpen: true,
      };
    case MODALCLOSE:
      return {
        ...state,
        modalIsOpen: false,
      };
    default:
      return state;
  }
};
export default MenuState;
