import React from "react";
import Navbar from "../../components/welcome/navbar";
import Menu from "../../components/welcome/menu_1/menu";
import Hero from "../../components/welcome/hero";
import Bio from "../../components/welcome/developerbio";
import Overview from "../../components/welcome/overview";
import Location from "../../components/welcome/location";
import Investment from "../../components/welcome/investment";
import Gallery from "../../components/welcome/gallery";
import Payment from "../../components/welcome/payment";
import Construction from "../../components/welcome/construction";
import Address from "../../components/welcome/address";
import Footer from "../../components/welcome/footer";
import WelModal from "../../components/welcome/modal";
import Style from "./Style.module.css";
import { connect } from "react-redux";
import Seo from "../../components/seo";
function Welcome({ mobileMenuToggle }) {
  return (
    <div className={Style.main}>
      <Seo title="Chhatt Buy Sell and Rent Property" description="" />
      <Navbar />
      {mobileMenuToggle && <Menu />}
      <Hero />
      <Bio />
      <Overview />
      <Location />
      <Investment />
      <Gallery bgimage="facilities" />
      <Payment />
      <Construction />
      <Gallery bgimage="progress" />
      <Address />
      <Footer />
      <WelModal />
    </div>
  );
}
const mapStatetoProps = (state) => {
  return {
    mobileMenuToggle: state.MenuState.mobileMenuToggle,
  };
};
export default connect(mapStatetoProps)(Welcome);
