import React from "react";
import Style from "./Style.module.scss";
import Carousel from "react-bootstrap/Carousel";
import cn from "classnames";
// facilities
import Img1 from "../../../../assets/welcome/slider/ROOFTOPDINING.webp";
import Img2 from "../../../../assets/welcome/slider/OFFICESPACE.webp";
import Img3 from "../../../../assets/welcome/slider/Brandoutlets.webp";
import Img6 from "../../../../assets/welcome/slider/MEETING ROOM.webp";
import Img7 from "../../../../assets/welcome/slider/MODERN FOUNTAIN.webp";
import Img8 from "../../../../assets/welcome/slider/OPEN SITTING AREA.webp";
import Img9 from "../../../../assets/welcome/slider/ReCEptION.webp";
import Img10 from "../../../../assets/welcome/slider/RESrTUrant.webp";
import Img14 from "../../../../assets/welcome/slider/cofeeshop.webp";
import Img15 from "../../../../assets/welcome/slider/commercialshop.webp";
import Img16 from "../../../../assets/welcome/slider/generator.webp";
import Img17 from "../../../../assets/welcome/slider/valetparking.webp";
import Img18 from "../../../../assets/welcome/slider/masjid.webp";
import Img19 from "../../../../assets/welcome/slider/gym.webp";
// progress
import Imgp1 from "../../../../assets/welcome/proogress/progress1.webp";
import Imgp2 from "../../../../assets/welcome/proogress/progress2.webp";
import Imgp3 from "../../../../assets/welcome/proogress/progress3.webp";
import Imgp4 from "../../../../assets/welcome/proogress/progress4.webp";
import Imgp5 from "../../../../assets/welcome/proogress/progress5.webp";
import Imgp6 from "../../../../assets/welcome/proogress/progress6.webp";
import Imgp7 from "../../../../assets/welcome/proogress/progress7.webp";

export default function Gallery({ bgimage }) {
  const fimages = [
    { title: "ROOFTOP DINING", img: Img1 },
    { title: "OFFICE SPACE", img: Img2 },
    { title: "BRAND OUTLETS", img: Img3 },
    { title: "CONFERENCE ROOM", img: Img6 },
    { title: "MODERN FOUNTAIN", img: Img7 },
    { title: "OPEN SITTING AREA", img: Img8 },
    { title: "RECEPTION", img: Img9 },
    { title: "FOOD COURT", img: Img10 },
    { title: "COFEE SHOPS", img: Img14 },
    { title: "COMMERCIAL SHOPS", img: Img15 },
    { title: "GENERATOR AREA", img: Img16 },
    { title: "VALET PARKING", img: Img17 },
    { title: "PRAYER AREA", img: Img18 },
    { title: "FITNESS CENTER", img: Img19 },
  ];
  const pimages = [Imgp1, Imgp2, Imgp3, Imgp4, Imgp5, Imgp6, Imgp7];
  return (
    <div
      className={cn(
        bgimage === "facilities"
          ? Style.main
          : bgimage === "progress"
          ? Style.main1
          : null
      )}
      id={bgimage}
    >
      <p>
        <b>PHOTO GALLERY</b>
      </p>
      <h1 className={Style.head}>
        <b>
          {bgimage === "facilities"
            ? "FACILITIES & AMENITIES"
            : bgimage === "progress"
            ? " Latest Construction updates 27March"
            : null}
        </b>
      </h1>
      <div className={Style.slide}>
        {bgimage === "facilities" && (
          <Carousel>
            {fimages.map((data, i) => {
              return (
                <Carousel.Item key={i}>
                  <img className="d-block w-100" src={data.img} alt="" />
                  <div className="carousel-caption d-md-block ">
                    {bgimage === "facilities" && (
                      <h5 className={Style.txt}>{data.title}</h5>
                    )}
                  </div>
                </Carousel.Item>
              );
            })}
          </Carousel>
        )}
        {bgimage === "progress" && (
          <Carousel>
            {pimages.map((data, i) => {
              return (
                <Carousel.Item key={i}>
                  <img className="d-block w-100" src={data} alt="" />
                </Carousel.Item>
              );
            })}
          </Carousel>
        )}
      </div>
    </div>
  );
}
