import React from "react";
import Style from "./Style.module.scss";
import ProgressiveImage from "react-progressive-image";
import Ig1 from "../../../assets/propertyCard.webp";
import Logo from "../../../assets/logow.png";
import cn from "classnames";
import { Link, NavLink } from "react-router-dom";

const BestPropertyHeader = ({ heading }) => {
  // FOR NAVBAR SCROLLING EFFECT START
  window.addEventListener("scroll", function () {
    var header = document.querySelector(".navbarContainer");
    header.classList.toggle("sticky", window.scrollY > 1);
  });
  return (
    <header id="sctop">
      <div className={cn(Style.main_div)} id="head1">
        <div className={`${Style.s_div} navbarContainer`}>
          <div className="innerContainer">
            <div className={Style.logo} id="lgo">
              <Link to="/">
                <ProgressiveImage delay={600} src={Logo} placeholder={Logo}>
                  {(src) => <img src={src} alt="Constrcution" />}
                </ProgressiveImage>
              </Link>
            </div>
            <div className={Style.tabss}>
              <ul>
                <Link style={{ color: "#fff" }} title="Home" to="/">
                  <li className={cn(Style.lis)}>
                    <span>HOME</span>
                  </li>
                </Link>
                <Link to="/property" style={{ color: "#fff" }} title="Property">
                  <li className={cn(Style.lis)}>
                    <span>Property</span>
                  </li>
                </Link>
                <Link
                  style={{ color: "#fff" }}
                  to="/construction"
                  title="Construction"
                >
                  <li className={cn(Style.lis)}>
                    <span>Construction</span>
                  </li>
                </Link>
                <NavLink
                  style={{ color: "#fff" }}
                  activeClassName={Style.active}
                  to="/real-estate-agencies"
                  title="real-estate-agencies"
                >
                  <li className={cn(Style.lis)}>
                    <span>Agencies</span>
                  </li>
                </NavLink>
                <NavLink
                  style={{ color: "#fff" }}
                  activeClassName={Style.active}
                  to="/contact-us"
                  title="contact-us"
                >
                  <li className={cn(Style.lis)}>
                    <span>Contact Us</span>
                  </li>
                </NavLink>
                <NavLink
                  style={{ color: "#fff" }}
                  activeClassName={Style.active}
                  to="/wanted"
                  title="wanted"
                >
                  <li className={cn(Style.lit)}>
                    <span>Wanted</span>
                  </li>
                </NavLink>
              </ul>
            </div>
          </div>
          {/* <div className={cn(Style.signin)}>
            <div className={Style.signin_icn}>
              <BsPersonFill />
            </div>
          </div> */}
        </div>
      </div>
      <ProgressiveImage delay={400} src={Ig1} placeholder={Ig1}>
        {(src, loading) => (
          <div className={Style.mn_div}>
            <img
              style={{
                filter: loading ? "blur(4px)" : "blur(0px)",
              }}
              src={src}
              alt="real estate agencies"
              className={Style.imgs}
            />
            <div className={Style.backg}>
              <div className={Style.backg_sdiv}>
                <h1> {heading} </h1>
              </div>
            </div>
          </div>
        )}
      </ProgressiveImage>
    </header>
  );
};

export default BestPropertyHeader;
