import React from "react";
import Style from "./Style.module.scss";
import Gallery from "react-image-gallery";
import { GoogleApiWrapper, Map, Marker } from "google-maps-react";
import { IoMdCall } from "react-icons/io";
import { MdEmail } from "react-icons/md";

// ================ IMAGE GALLERY START ================
export const MobileImageGallery = ({ singleProperty }) => {
  return (
    <div>
      <Gallery
        autoPlay={false}
        showIndex={true}
        showPlayButton={false}
        items={singleProperty ? singleProperty.images : []}
        allowFullScreen={true}
      />
    </div>
  );
};
// ================ IMAGE GALLERY END ================

// ================ SALE TAG COMPONENT START ================
export const MobileSaleTag = ({ singleProperty }) => {
  const {
    property_type,
    property_for,
    area_one_name,
    address,
    short_price,
  } = singleProperty;

  return (
    <>
      <div className={Style.saleTagContainer}>
        <h3>
          {property_type} {property_for} in {area_one_name}
        </h3>
        <p> {address} </p>
      </div>
      <h1 className={Style.priceTag}>PKR {short_price}</h1>
    </>
  );
};
// ================ SALE TAG COMPONENT END ================

// ===================== OVERVIEW START ============================
export const MobileOverview = ({ singleProperty }) => {
  const {
    property_type,
    property_for,
    short_price,
    size,
    size_type,
    bath,
    bed,
  } = singleProperty;

  return (
    <div className={Style.overViewContainer}>
      <h3>Overview</h3>
      <div className={Style.overviewItems}>
        <div className={Style.upperContent}>
          <div>
            <h5>Type</h5>
            <h4> {property_type} </h4>
          </div>
          <span></span>
          <div className={Style.middle}>
            <h5>Price</h5>
            <h4> PKR {short_price} </h4>
          </div>
          <span></span>
          <div>
            <h5>Purpose</h5>
            <h4> {property_for} </h4>
          </div>
        </div>

        <div className={Style.lowerContent}>
          <div>
            <h5>Area</h5>
            <h4>
              {size} {size_type}
            </h4>
          </div>
          <span></span>
          {bed && (
            <>
              <div>
                <h5>Bedroom(s)</h5>
                <h4> {bed} </h4>
              </div>
              <span></span>
            </>
          )}
          {bath && (
            <div>
              <h5>Bath(s)</h5>
              <h4> {bath} </h4>
            </div>
          )}
        </div>
      </div>
    </div>
  );
};
// ===================== OVERVIEW END ============================

// ===================== DESCRIPTION START ============================
export const MobileDescription = ({ singleProperty }) => {
  return (
    <>
      {singleProperty.description && (
        <div className={Style.descriptionContainer}>
          <h1>Description</h1>
          <div>{singleProperty.description}</div>
        </div>
      )}
    </>
  );
};
// ===================== DESCRIPTION END ============================

// ===================== MAP START ============================
const MapC = (props) => {
  if (props.singleProperty) {
    var latLong = props.singleProperty.latlong.split(",");
  }
  return (
    <>
      {props.singleProperty && (
        <div className={Style.mapMainContainer}>
          <h1>Google Map</h1>
          <div className={Style.mapContainer}>
            <Map
              google={props.google}
              zoom={12}
              initialCenter={{
                lat: latLong[0],
                lng: latLong[1],
              }}
            >
              <Marker name={"Current location"} />
            </Map>
          </div>
        </div>
      )}
    </>
  );
};

export default GoogleApiWrapper({
  apiKey: "AIzaSyAAdMS03mAk6qDSf4HUmZmcjvSkiSN7jIU",
})(MapC);

// ===================== MAP END ============================

// ===================== CALL EMAIL START ============================
export const CallEmail = () => {
  return (
    <div className={Style.CEContainer}>
      <button>
        <span>
          <IoMdCall />
        </span>
        &nbsp; Call Now
      </button>
      <button>
        <span>
          <MdEmail />
        </span>
        &nbsp; Send Email
      </button>
    </div>
  );
};
// ===================== CALL EMAIL END ============================
