import React, { useEffect } from "react";
import Style from "./Style.module.scss";
import Slider from "react-slick";
import PropertyCard from "../cards/propertyCard";
import cn from "classnames";
import { connect } from "react-redux";
import { bestPropertiesApi } from "../../redux";
import { useHistory } from "react-router-dom";
import Button from "@material-ui/core/Button";
import { makeStyles } from "@material-ui/core/styles";
import SkeletonCard from "../skeletonCard";

const useStyles = makeStyles(() => ({
  button: {
    textTransform: "none",
    lineHeight: 1.5,
    "&:hover": {
      backgroundColor: "#007bff",
      color: "#ffffff",
    },
  },
  containedButton: {
    boxShadow: "none",
    "&:active": {
      boxShadow:
        "0px 3px 1px -2px rgb(0 0 0 / 20%), 0px 2px 2px 0px rgb(0 0 0 / 14%), 0px 1px 5px 0px rgb(0 0 0 / 12%)",
    },
  },
}));

function BestProperty({
  title,
  HideClassP,
  marginTop,
  sliderMargin,
  HideClassRP,
  bestProperties,
  bestPropertiesApi,
  top,
}) {
  useEffect(() => {
    bestPropertiesApi();
  }, []);
  var settings = {
    dots: false,
    infinite: false,
    speed: 500,
    slidesToShow: 4,
    slidesToScroll: 1,
    initialSlide: 0,
    responsive: [
      {
        breakpoint: 1230,
        settings: {
          slidesToShow: 3,
          slidesToScroll: 1,
          infinite: true,
        },
      },
    ],
  };

  const bestPropertyData = bestProperties && bestProperties.slice(0, 10);

  let history = useHistory();
  const classes = useStyles();

  const viewAll = () => {
    history.push(`/results?allProperties&p=1`);
  };

  const skeletonData = [1, 1, 1, 1];

  // FUNCTIONS FOR SETTING ACTIVE BTN
  const btn1 = document.getElementById("allBtn");
  const btn2 = document.getElementById("resBtn");
  const btn3 = document.getElementById("comBtn");
  const btn4 = document.getElementById("indBtn");

  const all = () => {
    bestPropertiesApi();
    btn1.classList.add("active_bestProperties");
    btn2.classList.remove("active_bestProperties");
    btn3.classList.remove("active_bestProperties");
    btn4.classList.remove("active_bestProperties");
  };
  const residential = () => {
    bestPropertiesApi("residential");
    btn2.classList.add("active_bestProperties");
    btn1.classList.remove("active_bestProperties");
    btn3.classList.remove("active_bestProperties");
    btn4.classList.remove("active_bestProperties");
  };
  const commercial = () => {
    bestPropertiesApi("commercial");
    btn3.classList.add("active_bestProperties");
    btn1.classList.remove("active_bestProperties");
    btn2.classList.remove("active_bestProperties");
    btn4.classList.remove("active_bestProperties");
  };
  const industrial = () => {
    bestPropertiesApi("industrial");
    btn4.classList.add("active_bestProperties");
    btn1.classList.remove("active_bestProperties");
    btn2.classList.remove("active_bestProperties");
    btn3.classList.remove("active_bestProperties");
  };
  return (
    <div
      id="bestProperty"
      className={cn(Style.maindiv, marginTop && Style.marginTop)}
    >
      <hr className={HideClassP && "HideClass"} />
      <br className={HideClassP && "HideClass"} />
      <br className={HideClassP && "HideClass"} />
      <div className={Style.secdiv}>
        <div className={Style.secdivtxt}>
          <h3> {title} </h3>
        </div>
        <div className={Style.sul}>
          <ul>
            <li
              title="All"
              className={`${HideClassRP && HideClassRP} active_bestProperties`}
              onClick={all}
              id="allBtn"
            >
              <Button
                variant="contained"
                classes={{
                  root: classes.button,
                  contained: classes.containedButton,
                }}
              >
                All
              </Button>
            </li>
            <li
              title="Residential"
              className={
                (HideClassP && "HideClass", HideClassRP && HideClassRP)
              }
              onClick={residential}
              id="resBtn"
            >
              <Button
                variant="contained"
                classes={{
                  root: classes.button,
                  contained: classes.containedButton,
                }}
              >
                Residential
              </Button>
            </li>
            <li
              title="Commercial"
              className={
                (HideClassP && "HideClass", HideClassRP && HideClassRP)
              }
              onClick={commercial}
              id="comBtn"
            >
              <Button
                variant="contained"
                classes={{
                  root: classes.button,
                  contained: classes.containedButton,
                }}
              >
                Commercial
              </Button>
            </li>
            <li
              title="Industrial"
              className={
                (HideClassP && "HideClass", HideClassRP && HideClassRP)
              }
              onClick={industrial}
              id="indBtn"
            >
              <Button
                variant="contained"
                classes={{
                  root: classes.button,
                  contained: classes.containedButton,
                }}
              >
                Industrial
              </Button>
            </li>
            <li title="View all" className={Style.viewall}>
              <Button
                variant="outlined"
                classes={{ root: classes.button }}
                onClick={() => viewAll()}
              >
                View all
              </Button>
            </li>
          </ul>
        </div>
      </div>
      <br />
      {/* property slider */}
      <div
        className={cn(
          Style.mainSlide_Div,
          sliderMargin && Style.sliderMargin,
          top && Style.mainTop
        )}
      >
        <Slider {...settings}>
          {bestProperties
            ? bestPropertyData.map((prev, i) => {
                return (
                  <div key={i}>
                    <PropertyCard prev={prev} />
                  </div>
                );
              })
            : skeletonData.map((prev, i) => <SkeletonCard key={i} />)}
        </Slider>
        {!HideClassP && <hr style={{ margin: "0px 0px 60px 0px" }} />}
      </div>
    </div>
  );
}
const mapStatetoProps = (state) => {
  return {
    bestProperties: state.bestPropertiesReducer.bestProperties,
  };
};
const mapDispatchtoProps = (dispatch) => {
  return {
    bestPropertiesApi: function (areaType) {
      dispatch(bestPropertiesApi(areaType));
    },
  };
};
export default connect(mapStatetoProps, mapDispatchtoProps)(BestProperty);
