import { AREA_LIST } from "./searchAreaType";

const initialState = {
  areas: [],
};

const nearByData = (state = initialState, action) => {
  switch (action.type) {
    case AREA_LIST:
      return {
        ...state,
        areas: action.payload,
      };
    default:
      return state;
  }
};
export default nearByData;
