import React from "react";
import Style from "./Style.module.scss";
import Slider from "react-slick";
import Apartment from "../../../assets/apartment.svg";
import Home from "../../../assets/home.svg";
import Office from "../../../assets/office-desk.svg";
import Villa from "../../../assets/villa.svg";
import cn from "classnames";
import { Link } from "react-router-dom";

function SampleNextArrow(props) {
	const { className, style, onClick } = props;
	return (
		<div
			className={className}
			style={{ ...style, display: "block" }}
			onClick={onClick}
		/>
	);
}

function SamplePrevArrow(props) {
	const { className, style, onClick } = props;
	return (
		<div
			className={className}
			style={{ ...style, display: "block" }}
			onClick={onClick}
		/>
	);
}

function Explore() {
	var settings = {
		dots: false,
		infinite: false,
		speed: 500,
		slidesToShow: 4,
		slidesToScroll: 1,
		initialSlide: 0,
		nextArrow: <SampleNextArrow />,
		prevArrow: <SamplePrevArrow />,
		responsive: [
			{
				breakpoint: 1270,
				settings: {
					slidesToShow: 3,
					slidesToScroll: 1,
					infinite: false,
					dots: false,
				},
			},
		],
	};
  return (
    <div className={cn(Style.main_div)}>
      <div className={Style.sdiv}>
        <div className={Style.f_div}>
          <h3>
            <b>Explore</b>
          </h3>
          <h3>
            <b>by Property Type</b>
          </h3>
          {/* <br /> */}
          {/* <button onClick={viewAll}>View All</button> */}
        </div>
        {/* property slider */}
        <div className={cn(Style.mainSlide_Div)}>
          <Slider {...settings}>
            <Link to="/property/flat" title="Flat">
              <div className={Style.apart}>
                <img
                  src={Apartment}
                  height="60px"
                  width="50px"
                  alt="flat for sale"
                />
                <br></br>
                <p>Flat</p>
              </div>
            </Link>
            <Link to="/property/villa" title="Villa">
              <div className={Style.house}>
                <img
                  src={Home}
                  height="55px"
                  width="60px"
                  alt="home for sale"
                />
                <br></br>
                <p>Villa</p>
              </div>
            </Link>
            <Link to="/property/plot" title="Plot">
              <div className={Style.office}>
                <img
                  src={Office}
                  height="55px"
                  width="60px"
                  alt="plot for sale"
                />
                <br></br>
                <p>Plot</p>
              </div>
            </Link>
            <Link to="/property/farm-house" title="Farm House">
              <div className={Style.villa}>
                <img
                  src={Villa}
                  height="55px"
                  width="60px"
                  alt="villa for sale"
                />
                <br></br>
                <p>Farm House</p>
              </div>
            </Link>
            <Link to="/property/upper-portion" title="Upper Portion">
              <div className={Style.svilla}>
                <img
                  src={Villa}
                  height="55px"
                  width="60px"
                  alt="upper portion for sale"
                />
                <br></br>
                <p>Upper Portion</p>
              </div>
            </Link>
            <Link to="/property/lower-portion" title="Lower Portion">
              <div className={Style.svilla}>
                <img
                  src={Villa}
                  height="55px"
                  width="60px"
                  alt="lower portion for sale"
                />
                <br></br>
                <p>Lower Portion</p>
              </div>
            </Link>
          </Slider>
        </div>
      </div>
    </div>
  );
}

export default Explore;
