import React from "react";
import Style from "./Style.module.scss";
import { FaPhoneAlt } from "react-icons/fa";
import { MdEmail } from "react-icons/md";
import PlaceholderImage from "../../../../assets/agentImgPlaceholder.jpg";

const MobileAgentCard = ({ singleAgency }) => {
  return (
    <div className={Style.maindiv}>
      <div className={Style.imgContainer}>
        {singleAgency.image ? (
          <img src={singleAgency.image} alt={singleAgency.agency_name} />
        ) : (
          <img src={PlaceholderImage} alt={singleAgency.agency_name} />
        )}
      </div>
      <div className={Style.card_body}>
        <h3>{singleAgency.name}</h3>
        <h6>{singleAgency.agency_name}</h6>
        <div className={Style.icons}>
          <div className={Style.icon_container}>
            <FaPhoneAlt
              className={Style.icon}
              style={{ background: "#43b243" }}
            />
          </div>
          <div className={Style.icon_container}>
            <MdEmail className={Style.icon} style={{ background: "#007bff" }} />
          </div>
        </div>
      </div>
    </div>
  );
};

export default MobileAgentCard;
