import {
	FEATURED_PROJECTS,
	AGENCIES_API,
	AGENCIES_LIST,
	AGENCY_PROPERTIES,
	AGENCY_INPUT_ON_CHANGE,
} from "./featuredProjectsType";

const initialState = {
	featuredProperties: "",
	agencies: "",
	agenciesList: "",
	agencyProperties: "",
	agencyInputVal: "",
};

const featuredPropertiesReducer = (state = initialState, action) => {
	switch (action.type) {
		case FEATURED_PROJECTS:
			return {
				...state,
				featuredProperties: action.payload,
			};
		case AGENCIES_API:
			return {
				...state,
				agencies: action.payload,
			};
		case AGENCIES_LIST:
			return {
				...state,
				agenciesList: action.payload,
			};
		case AGENCY_PROPERTIES:
			return {
				...state,
				agencyProperties: action.payload,
			};
		case AGENCY_INPUT_ON_CHANGE:
			return {
				...state,
				agencyInputVal: action.payload,
			};
		default:
			return state;
	}
};
export default featuredPropertiesReducer;
