import React from "react";
import Style from "./Style.module.scss";
import {
  mobileAgenciesSearchApi,
  mobileAgencyInputOnChange,
} from "../../redux";
import { connect } from "react-redux";

const AgencySearch = ({
  mobileAgenciesSearchApi,
  mobileAgencyInputOnChange,
  mobileAgencyInputVal,
}) => {
  const handleChange = (e) => {
    mobileAgencyInputOnChange(e.target.value);
    mobileAgenciesSearchApi(e.target.value);
  };

  return (
    <div className={Style.main_div}>
      <div className={Style.inputDiv}>
        <input
          type="text"
          placeholder="search agencies..."
          value={mobileAgencyInputVal}
          onChange={handleChange}
        />
      </div>
    </div>
  );
};

const mapStatetoProps = (state) => {
  return {
    mobileAgenciesList:
      state.mobileFeaturedPropertiesReducer.mobileAgenciesList,
    mobileAgencyInputVal:
      state.mobileFeaturedPropertiesReducer.mobileAgencyInputVal,
  };
};
const mapDispatchtoProps = (dispatch) => {
  return {
    mobileAgenciesSearchApi: function (data) {
      dispatch(mobileAgenciesSearchApi(data));
    },
    mobileAgencyInputOnChange: function (data) {
      dispatch(mobileAgencyInputOnChange(data));
    },
  };
};
export default connect(mapStatetoProps, mapDispatchtoProps)(AgencySearch);
