import React, { useEffect, memo } from "react";
import Style from "./Style.module.scss";
import Slider from "react-slick";
import FeatureCard from "../featureCard";
import SkeletonCard from "../skeletonCard";
import { Link } from "react-router-dom";
import cn from "classnames";
import {
  // featuredPropertiesApi,
  agenciesApi,
} from "../../redux/featuredProjects_agencies_maps/featuredProjectsAction";
import { connect } from "react-redux";

// FEATURED IMAGES IMPORT
import FP1 from "../../../assets/welcomeCenter.webp";
import FP2 from "../../../assets/jfTower.webp";
// import FP3 from "../../../assets/waqasHeights.webp";

const Featuredprojects = ({
  title,
  city,
  cardTitle,
  description,
  marginTop,
  marginBottom,
  HideClassP_A,
  marginTop_B,
  id,
  anc,
  // featuredPropertiesApi,
  // featuredProperties,
  agenciesApi,
  agencies,
}) => {
  useEffect(() => {
    // featuredPropertiesApi();
    agenciesApi();
  }, []);
  var settings = {
    dots: false,
    infinite: false,
    speed: 500,
    slidesToShow: 4,
    slidesToScroll: 1,
    initialSlide: 0,
    responsive: [
      {
        breakpoint: 1230,
        settings: {
          slidesToShow: 3,
          slidesToScroll: 1,
          infinite: true,
        },
      },
    ],
  };
  const agenciesData = agencies && agencies.slice(0, 10);
  const skeletonData = [1, 1, 1, 1];
  const featuredProjectData = [
    {
      name: "Welcome Center",
      desc: "Highway Developers and Builders",
      image: FP1,
      link: "/projects/welcome_center",
      alter: "under construction projects",
    },
    {
      name: "JF Tower",
      desc: "JF Developers",
      image: FP2,
      link: "/projects/jf-tower",
      alter: "under construction projects",
    },
    // { name: "Waqas Heights", desc: "Waqas Builders & Developers", image: FP3 },
  ];

  return (
    <div
      id={id}
      className={cn(
        Style.maindiv,
        marginBottom && Style.marginBottom,
        marginTop_B && Style.marginTop_B
      )}
    >
      <hr className={HideClassP_A && HideClassP_A} />
      {/* <br className={HideClassP_A && HideClassP_A} /> */}
      {/* <br className={HideClassP_A && HideClassP_A} /> */}
      {title !== "Featured Projects" && (
        <div className={Style.secdiv}>
          <div className={Style.secdivtxt}>
            <p> {title} </p>
          </div>
          <div className={Style.sul}>
            <ul>
              <li className={Style.viewall}>
                <Link to={anc}>View all</Link>
              </li>
            </ul>
          </div>
        </div>
      )}

      {/* JUST FOR FEATURED PROJECTS START */}
      {title === "Featured Projects" && (
        <div className={Style.secdiv} style={{ justifyContent: "center" }}>
          <div className={Style.secdivtxt}>
            <p>{title}</p>
          </div>
        </div>
      )}
      {/* JUST FOR FEATURED PROJECTS END */}

      <br />
      {/* property slider */}
      <div className={cn(Style.mainSlide_Div, marginTop && Style.marginTop)}>
        {/* FEATURED CARD SLIDER START */}
        {title === "Featured Projects" && (
          <div
            style={{
              display: "flex",
              width: "600px",
              margin: "5.2rem 0rem 3.2rem 0rem",
            }}
          >
            {/* <Slider {...settings}> */}
            {featuredProjectData.map((prev, i) => {
              return (
                <Link key={i} to={prev.link}>
                  <div style={{ width: "300px" }}>
                    <FeatureCard
                      title={title}
                      city={city}
                      featuredProject={prev}
                    />
                  </div>
                </Link>
              );
            })}
            {/* </Slider> */}
          </div>
        )}
        {/* FEATURED CARD SLIDER END */}

        {/* AGENCIES CARD SLIDER START */}
        {title === "Agencies" && (
          <Slider {...settings}>
            {agenciesData
              ? agenciesData.map((data, i) => {
                  const { id, agency_name, owner_name, image, city } = data;
                  return (
                    <div key={i}>
                      <FeatureCard
                        city="karachi"
                        cardTitle={agency_name}
                        description={owner_name}
                        cardImage={image}
                        title={title}
                        anc={`/agency/${id}-${agency_name
                          .replace(/ /g, "-")
                          .toLowerCase()}-${city.toLowerCase()}`}
                      />
                    </div>
                  );
                })
              : skeletonData.map((prev, i) => <SkeletonCard key={i} />)}
          </Slider>
        )}
        {/* AGENCIES CARD SLIDER END */}
        {/* MAP CARD SLIDER START */}
        {title === "Map" && (
          <Slider {...settings}>
            <div>
              <FeatureCard
                city={city}
                cardTitle={cardTitle}
                description={description}
                title={title}
              />
            </div>
            <div>
              <FeatureCard
                city={city}
                cardTitle={cardTitle}
                description={description}
                title={title}
              />
            </div>
            <div>
              <FeatureCard
                city={city}
                cardTitle={cardTitle}
                description={description}
                title={title}
              />
            </div>
            <div>
              <FeatureCard
                city={city}
                cardTitle={cardTitle}
                description={description}
                title={title}
              />
            </div>
            <div>
              <FeatureCard
                city={city}
                cardTitle={cardTitle}
                description={description}
                title={title}
              />
            </div>
            <div>
              <FeatureCard
                city={city}
                cardTitle={cardTitle}
                description={description}
                title={title}
              />
            </div>
            <div>
              <FeatureCard
                city={city}
                cardTitle={cardTitle}
                description={description}
                title={title}
              />
            </div>
            <div>
              <FeatureCard
                city={city}
                cardTitle={cardTitle}
                description={description}
                title={title}
              />
            </div>
            <div>
              <FeatureCard
                city={city}
                cardTitle={cardTitle}
                description={description}
                title={title}
              />
            </div>
            <div>
              <FeatureCard
                city={city}
                cardTitle={cardTitle}
                description={description}
                title={title}
              />
            </div>
          </Slider>
        )}
        {/* MAP CARD SLIDER END */}
      </div>
    </div>
  );
};

const mapStatetoProps = (state) => {
  return {
    featuredProperties: state.featuredPropertiesReducer.featuredProperties,
    agencies: state.featuredPropertiesReducer.agencies,
  };
};
const mapDispatchtoProps = (dispatch) => {
  return {
    // featuredPropertiesApi: function () {
    //   dispatch(featuredPropertiesApi());
    // },
    agenciesApi: function () {
      dispatch(agenciesApi());
    },
  };
};
export default connect(
  mapStatetoProps,
  mapDispatchtoProps
)(memo(Featuredprojects));
