import React from "react";
import Style from "./Style.module.scss";
import Slider from "react-slick";
import PropertyCard from "../cards/propertyCard";
import { connect } from "react-redux";

function RelatedProperties({ relatedProperties, heading, agencyProperties }) {
  if (relatedProperties) {
    var settings = {
      dots: false,
      infinite: false,
      speed: 500,

      // FOR RELATED PROPERTIES START
      slidesToShow:
        (relatedProperties.length === 1 && 1) ||
        (relatedProperties.length === 2 && 2) ||
        (relatedProperties.length === 3 && 3) ||
        (relatedProperties.length >= 4 && 4),
      // FOR RELATED PROPERTIES END

      slidesToScroll: 1,
      initialSlide: 0,
      responsive: [
        {
          breakpoint: 1360,
          settings: {
            // FOR RELATED PROPERTIES START
            slidesToShow:
              (relatedProperties.length === 1 && 1) ||
              (relatedProperties.length === 2 && 2) ||
              (relatedProperties.length >= 3 && 3),
            // FOR RELATED PROPERTIES END

            slidesToScroll: 1,
            infinite: true,
          },
        },
      ],
    };

    // CONTAINER MEDIA QUERY (RESPONSIVE ON LESS THEN 4 CARDS)
    var ContainerStyle = {
      // FOR RELATED PROPERTIES START
      width:
        (relatedProperties.length === 1 && "400px") ||
        (relatedProperties.length === 2 && "700px") ||
        (relatedProperties.length === 3 && "1050px"),
      // FOR RELATED PROPERTIES END
    };
  }

  if (agencyProperties) {
    var settingsAgencies = {
      dots: false,
      infinite: false,
      speed: 500,

      // FOR AGENCIES PROPERTIES START
      slidesToShow:
        (agencyProperties.length === 1 && 1) ||
        (agencyProperties.length === 2 && 2) ||
        (agencyProperties.length === 3 && 3) ||
        (agencyProperties.length >= 4 && 4),
      // FOR AGENCIES PROPERTIES END

      slidesToScroll: 1,
      initialSlide: 0,
      responsive: [
        {
          breakpoint: 1360,
          settings: {
            // FOR AGENCIES PROPERTIES START
            slidesToShow:
              (agencyProperties.length === 1 && 1) ||
              (agencyProperties.length === 2 && 2) ||
              (agencyProperties.length >= 3 && 3),
            // FOR AGENCIES PROPERTIES END
            slidesToScroll: 1,
            infinite: true,
          },
        },
      ],
    };

    // CONTAINER MEDIA QUERY (RESPONSIVE ON LESS THEN 4 CARDS)
    var ContainerStyleAgencies = {
      // FOR AGENCIES PROPERTIES START
      width:
        (agencyProperties.length === 1 && "400px") ||
        (agencyProperties.length === 2 && "700px") ||
        (agencyProperties.length === 3 && "1050px"),
      // FOR AGENCIES PROPERTIES END
    };
  }

  return (
    <div className={Style.maindiv}>
      <hr />
      <br />
      <br />
      <div className={Style.secdiv}>
        <div className={Style.secdivtxt}>
          <p> {heading} </p>
        </div>
      </div>
      <br />
      {/* property slider */}
      {(heading === "Related Properties" && (
        <div style={ContainerStyle} className={Style.mainSlide_Div}>
          <Slider {...settings}>
            {relatedProperties &&
              relatedProperties.map((prev, i) => {
                return (
                  <div key={i}>
                    <PropertyCard prev={prev} />
                  </div>
                );
              })}
          </Slider>
        </div>
      )) ||
        (heading === "Agency Properties" && (
          <div style={ContainerStyleAgencies} className={Style.mainSlide_Div}>
            <Slider {...settingsAgencies}>
              {agencyProperties &&
                agencyProperties.map((prev, i) => {
                  return (
                    <div key={i}>
                      <PropertyCard prev={prev} />
                    </div>
                  );
                })}
            </Slider>
          </div>
        ))}
    </div>
  );
}

const mapStatetoProps = (state) => {
  return {
    relatedProperties: state.relatedPropertiesReducer.relatedProperties,
  };
};

export default connect(mapStatetoProps, null)(RelatedProperties);
