import React from "react";
import Style from "./Style.module.scss";
import Logo from "../../../../assets/welcome/logo.webp";
import ChLogo from "../../../../assets/chhattLogo.png";
import cn from "classnames";

export default function Address() {
  return (
    <div className={Style.main}>
      <div className={cn("mr-3", Style.img_div)}>
        <img src={Logo} width="130px" alt="" />
      </div>
      <div className="mr-3">
        <h2 className={Style.head}>Booking Office</h2>
        <p className={Style.para}>
          9-C Khayaban-e-Abbasi, D.H.A Phase 7 Ext Karachi, city, Sindh
          <br />
          75500
        </p>
        {/* <p className={Style.para}>
          Islamabad <br />
          Welcome center, plot 46,47,D, Markaz Gulberg Residencia Islamabad!
        </p> */}
      </div>
      <div>
        <h2 className={Style.head}>Site Office</h2>
        <p className={Style.para}>
          Welcome center, plot 46,47,D, Markaz Gulberg Residencia Islamabad!
        </p>
      </div>
      <div>
        <a href="https://chhatt.com/">
          <img src={ChLogo} width="115px" alt="" />
        </a>
      </div>
    </div>
  );
}
