import React from "react";
import Pagination from "@material-ui/lab/Pagination";
import Style from "./Style.module.scss";
import { useHistory } from "react-router-dom";

const ExplorePagination = ({ localHomeSearchData, pathNameFromExplore }) => {
  const history = useHistory();
  const HandleChange = (event, value) => {
    history.push(`/property/${pathNameFromExplore[1]}/page-${value}`);
  };

  return (
    <>
      {localHomeSearchData && localHomeSearchData.data.meta.total > 28 && (
        <div className={Style.main_div}>
          <Pagination
            count={localHomeSearchData.data.meta.last_page}
            variant="outlined"
            shape="rounded"
            onChange={HandleChange}
          />
        </div>
      )}
    </>
  );
};

export default ExplorePagination;
