import React from "react";
import Style from "./Style.module.scss";
import Skeleton from "@material-ui/lab/Skeleton";
import cn from "classnames";

const SkeletonCard = ({ newProduct }) => {
  return (
    <>
      <div className={cn(Style.main_div, newProduct && Style.newProductCard)}>
        <Skeleton
          className="rounded-top mt-3"
          variant="rect"
          width="100%"
          height="200px"
        />
        <div
          style={{
            display: "flex",
            justifyContent: "space-between",
          }}
        >
          <Skeleton variant="text" width="30%" height={22} />
          <Skeleton variant="text" width="38%" height={22} />
        </div>
        <div style={{ display: "flex", flexDirection: "column" }}>
          <Skeleton variant="text" width="60%" height={28} />
          <Skeleton variant="text" width="40%" height={28} />
        </div>
        <div
          style={{
            display: "flex",
            justifyContent: "space-between",
          }}
        >
          <Skeleton variant="text" width="28%" height={18} />
          <Skeleton variant="text" width="28%" height={18} />
          <Skeleton variant="text" width="28%" height={18} />
        </div>
        <div
          style={{
            display: "flex",
            justifyContent: "space-between",
          }}
        >
          <Skeleton variant="text" width="46%" height={38} />
          <Skeleton variant="text" width="46%" height={38} />
        </div>
      </div>
    </>
  );
};

export default SkeletonCard;
