import React from "react";
import Style from "./Style.module.scss";
import ProgressiveImage from "react-progressive-image";
import Constructionhigh from "../../../assets/constructionhigh.webp";
import Contractorhigh from "../../../assets/contractorhigh.webp";
import Propertyhigh from "../../../assets/propertyhigh.webp";
import Realtorhigh from "../../../assets/realtorhigh.webp";
import cn from "classnames";
import { Link } from "react-router-dom";

export default function CardSection() {
  return (
    <section className={cn(Style.main_section)} id="find">
      <h2>Find the Business in town</h2>
      <div className={Style.cardss}>
        <Link to="/real-estate-agencies" title="Realtor" className={Style.crd}>
          <ProgressiveImage
            delay={200}
            src={Propertyhigh}
            placeholder={Propertyhigh}
          >
            {(src, loading) => (
              <img
                className={Style.imgs}
                style={{
                  filter: loading ? "blur(4px)" : "blur(0px)",
                }}
                src={src}
                alt="Best Realtor"
              />
            )}
          </ProgressiveImage>
          <h4>Realtor</h4>
        </Link>
        <Link to="/property" title="Property" className={Style.crd}>
          <ProgressiveImage
            delay={400}
            src={Realtorhigh}
            placeholder={Realtorhigh}
          >
            {(src, loading) => (
              <img
                className={Style.imgs}
                style={{
                  filter: loading ? "blur(4px)" : "blur(0px)",
                }}
                src={src}
                alt="property for sell"
              />
            )}
          </ProgressiveImage>
          <h4>Property</h4>
        </Link>
        <div className={Style.crd} title="Construction">
          <ProgressiveImage
            delay={600}
            src={Constructionhigh}
            placeholder={Constructionhigh}
          >
            {(src, loading) => (
              <img
                className={Style.imgs}
                style={{
                  filter: loading ? "blur(4px)" : "blur(0px)",
                }}
                src={src}
                alt="Construction Services"
              />
            )}
          </ProgressiveImage>
          <h4>Construction </h4>
        </div>
        <div className={Style.crd} title="Contractor">
          <ProgressiveImage
            delay={800}
            src={Contractorhigh}
            placeholder={Contractorhigh}
          >
            {(src, loading) => (
              <img
                className={Style.imgs}
                style={{
                  filter: loading ? "blur(4px)" : "blur(0px)",
                }}
                src={src}
                alt="Best Contractor"
              />
            )}
          </ProgressiveImage>
          <h4>Contractor</h4>
        </div>
      </div>
      <br />
      <br />
    </section>
  );
}
