import React from "react";
import {
	GiHouse,
	GiVillage,
	GiSpookyHouse,
	GiVikingLonghouse,
} from "react-icons/gi";
import { FaWarehouse } from "react-icons/fa";
import { HiOfficeBuilding } from "react-icons/hi";
import { ImOffice } from "react-icons/im";
import Style from "./Style.module.scss";
import { Link } from "react-router-dom";

const MobilePropertyType = () => {
	return (
		<div className={Style.main_div}>
			<div className={Style.fdiv}>
				<div className={Style.top}>
					<p>Property Type</p>
					{/* <p>View All</p> */}
				</div>
				<div className={Style.sdiv}>
					<ul>
						<li>
							<Link to="/property/flat">
								<div>
									<ImOffice color="#007bff" fontSize="25px" />
								</div>
								<p>Flat</p>
							</Link>
						</li>
						<li>
							<Link to="/property/house">
								<div>
									<GiHouse color="#007bff" fontSize="25px" />
								</div>
								<p>House</p>
							</Link>
						</li>
						<li>
							<Link to="/property/plot">
								<div>
									<HiOfficeBuilding color="#007bff" fontSize="25px" />
								</div>
								<p>Plot</p>
							</Link>
						</li>
						<li>
							<Link to="/property/villa">
								<div>
									<GiVillage color="#007bff" fontSize="25px" />
								</div>
								<p>Villa</p>
							</Link>
						</li>
						<li>
							<Link to="/property/farm-house">
								<div>
									<GiSpookyHouse color="#007bff" fontSize="25px" />
								</div>
								<p>Farm House</p>
							</Link>
						</li>
						<li>
							<Link to="/property/upper-portion">
								<div>
									<GiVikingLonghouse color="#007bff" fontSize="25px" />
								</div>
								<p>Upper Portion</p>
							</Link>
						</li>
						<li>
							<Link to="/property/lower-portion">
								<div>
									<FaWarehouse color="#007bff" fontSize="25px" />
								</div>
								<p>Lower Portion</p>
							</Link>
						</li>
					</ul>
				</div>
			</div>
		</div>
	);
};

export default MobilePropertyType;
