import React from 'react'
import Style from './Style.module.scss'
import axios from 'axios'
import { useForm } from 'react-hook-form'
import swal from 'sweetalert'
import Button from '@material-ui/core/Button'
import { makeStyles } from '@material-ui/core/styles'

const useStyles = makeStyles(() => ({
  subscribeButton: {
    textTransform: 'none',
  },
}))

export default function Newsletter() {
  const {
    register,
    handleSubmit,
    formState: { errors },
    reset,
  } = useForm()

  const classes = useStyles()

  const onSubmit = (data) => {
    axios
      .get(`https://uat.chhatt.com/api/newsletter?email=${data.email}`)
      .then(() => swal('Thank You!', 'For Supporting Us', 'success'))
    reset()
  }
  return (
    <div className={Style.main_div}>
      <div className={Style.sdiv}>
        <div className={Style.f_div}>
          <h4>Newsletter</h4>
          <p>
            Subscribe for newsletter and receive latest updates and trends about
            chhatt.com
          </p>
        </div>
        <div className={Style.input_div}>
          <form className={Style.sdiv} onSubmit={handleSubmit(onSubmit)}>
            <div>
              <input
                style={
                  errors.email && {
                    border: '2px solid #ff040d',
                  }
                }
                size='50'
                placeholder='Enter your Email'
                type='email'
                name='email'
                {...register('email', {
                  required: true,
                  pattern:
                    /^(([^<>()[\]\\.,;:\s@\"]+(\.[^<>()[\]\\.,;:\s@\"]+)*)|(\".+\"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/,
                })}
              />
              <div className={Style.inpErrors}>
                {errors.email &&
                  errors.email.type === 'required' &&
                  'Email is required'}
                {errors.email &&
                  errors.email.type === 'pattern' &&
                  'please enter valid email'}
              </div>
            </div>
            <div>
              {/* <button type='submit'>Subscribe</button> */}
              <Button
                variant='contained'
                type='submit'
                classes={{ root: classes.subscribeButton }}
              >
                Subscribe
              </Button>
            </div>
          </form>
        </div>
      </div>
    </div>
  )
}
