import React from "react";
import Style from "./Style.module.scss";
import Share from "../shareView";
import { Link } from "react-router-dom";
import { capitalizeFunc } from "../../util";

const Breadcrumb = ({ productId }) => {
  const string = productId.split("-");
  var urlData = [];
  for (var i = 0; i < string.length; i++) {
    var pair = string[i].split("=");
    var ind1 = pair[0];
    urlData.push(ind1);
  }

  return (
    <>
      <div className={Style.main_div}>
        <div className={Style.inner_div}>
          <div>
            <Link to="/" title="Chhatt">
              Chhatt
            </Link>
            &nbsp;
            <Link
              title={urlData[0]}
              to={`/results?allProperties&city=${urlData[0]}&p=1`}
            >
              / {capitalizeFunc(urlData[0])}
            </Link>
            &nbsp;
            <Link
              title={capitalizeFunc(urlData[1])}
              to={`/results?allProperties&city=${urlData[0]}&areaType=${urlData[1]}&p=1`}
            >
              / {capitalizeFunc(urlData[1])}
            </Link>
            &nbsp;
            <Link
              title={capitalizeFunc(urlData[2].replace("_", " "))}
              to={`/results?allProperties&city=${
                urlData[0]
              }&propertyType=${urlData[2].replace("_", "-")}&p=1`}
            >
              / {capitalizeFunc(urlData[2].replace("_", " "))}
            </Link>
            <Link
              title={capitalizeFunc(urlData[3].replace("_", " "))}
              to={`/results?allProperties&city=${
                urlData[0]
              }&purpose=${urlData[3].replace("_", "-")}&p=1`}
            >
              / {capitalizeFunc(urlData[3].replace("_", " "))}
            </Link>
            <Link
              title={urlData[4].replace("_", " ")}
              to={`/results?home&city=${urlData[0]}&val=${urlData[4].replace(
                "_",
                " "
              )}&p=1`}
            >
              / {capitalizeFunc(urlData[4].replace("_", " "))}
            </Link>
            &nbsp;/ {urlData.pop()}
          </div>
          <div className={Style.share_div}>
            <Share />
            {/* <button>
              <MdFavoriteBorder /> Favorite
            </button> */}
            {/* <button>Print</button> */}
          </div>
        </div>
        <hr />
      </div>
    </>
  );
};

export default Breadcrumb;
