import React, { useState, useEffect } from "react";
import Style from "./Style.module.scss";
// import { BsPersonFill } from "react-icons/bs";
import { RiSearch2Line } from "react-icons/ri";
import ProgressiveImage from "react-progressive-image";
import Ig1 from "../../../assets/counstruction.webp";
import Logo from "../../../assets/logow.png";
import { CgArrowLongDown } from "react-icons/cg";
// import play from "../../../assets/icons/play.png";
import cn from "classnames";
import { Link as Lk } from "react-scroll";
import { Link } from "react-router-dom";
import { FormControl, MenuItem, Select } from "@material-ui/core";
import { makeStyles, withStyles } from "@material-ui/core/styles";
import Button from "@material-ui/core/Button";
// import useMediaQuery from "@material-ui/core/useMediaQuery";
import InputBase from "@material-ui/core/InputBase";
import useAutocomplete from "@material-ui/lab/useAutocomplete";
import { connect } from "react-redux";
import { fetchAreaOne } from "../../redux";
import { useHistory } from "react-router-dom";
import { propertyBanner } from "../images";

function Header2({ citiesData, fetchAreaOne, homeSearch }) {
  const [windowSize, setWindowSize] = useState({
    width: undefined,
  });
  const [lgo, setLgo] = useState(Logo);
  const [viewMore, setViewMore] = useState(false);
  const [inputValue, setInputValue] = useState("");

  // FOR GETTING DATA FROM SELECT BOXES
  const [selectBox, setSelectBox] = useState({
    city: "karachi",
    purpose: "for-sale",
    propertyType: "flat",
    areaType: "residential",
    beds: "Any",
    baths: "Any",
    priceMin: "0",
    priceMax: "Any",
    areaMin: "0",
    areaMax: "Any",
  });

  useEffect(() => {
    fetchAreaOne(selectBox.city);
  }, [selectBox.city]);

  useEffect(() => {
    // Handler to call on window resize
    function handleResize() {
      // Set window width/height to state
      setWindowSize({
        width: window.innerWidth,
      });
    }
    // Add event listener
    window.addEventListener("resize", handleResize);
    // Call handler right away so state gets updated with initial window size
    handleResize();
    // Remove event listener on cleanup
    return () => window.removeEventListener("resize", handleResize);
  }, []); // Empty array ensures that effect is only run on mount

  const handleChange = (e) => {
    const { name, value } = e.target;
    setSelectBox((prev) => {
      return {
        ...prev,
        [name]: value,
      };
    });
  };

  const vmHandle = () => {
    setViewMore((prev) => !prev);
  };

  const resetHandle = () => {
    setSelectBox({
      city: "karachi",
      purpose: "for-sale",
      propertyType: "flat",
      areaType: "residential",
      beds: "Any",
      baths: "Any",
      priceMin: "0",
      priceMax: "Any",
      areaMin: "0",
      areaMax: "Any",
    });
  };

  // INPUT FIELD STYLING MATERIAL UI START
  const useStyles = makeStyles((theme) => ({
    input: {
      outline: "none",
      width: windowSize.width <= 1400 ? 355 : 424,
      padding: "10px 10px",
    },
    listbox: {
      width: windowSize.width <= 1400 ? 680 : 780,
      margin: 0,
      padding: 0,
      zIndex: 1,
      marginTop: "5px",
      position: "absolute",
      left: "0%",
      listStyle: "none",
      backgroundColor: theme.palette.background.paper,
      overflow: "auto",
      maxHeight: 300,
      color: "#000",
      textAlign: "left",
      border: "1px solid rgba(0,0,0,.25)",
      '& li[data-focus="true"]': {
        backgroundColor: "#4a8df6",
        color: "#fff",
        cursor: "pointer",
      },
      "& li:active": {
        backgroundColor: "#2977f5",
        color: "#000",
      },
    },
    searchButton: {
      textTransform: "none",
    },
  }));
  // INPUT FIELD STYLING MATERIAL UI END

  // SELECT STYLING FROM MATERIAL UI START
  const BootstrapInput = withStyles((theme) => ({
    root: {
      "label + &": {
        marginRight: theme.spacing(1),
      },
    },
    input: {
      borderRadius: 3,
      position: "relative",
      backgroundColor: "#f2f2f2",
      fontSize: 16,
      padding: "10px 26px 10px 12px",
      transition: theme.transitions.create(["border-color", "box-shadow"]),
      fontWeight: "600",
      width: "62px",

      "&:focus": {
        borderRadius: 4,
        borderColor: "#80bdff",
        boxShadow: "0 0 0 0.2rem rgba(0,123,255,.25)",
      },
    },
  }))(InputBase);

  // MORE INPUTS BELOW 4 INPUTS
  const MoreBootstrapInput = withStyles((theme) => ({
    root: {
      "label + &": {
        marginRight: theme.spacing(1),
      },
    },
    input: {
      width: windowSize.width <= 1400 ? 114 : 140,
      borderRadius: 4,
      position: "relative",
      backgroundColor: "#f2f2f2",
      fontSize: windowSize.width <= 1400 ? 15 : 16,
      padding: "7px 26px 7px 12px",
      transition: theme.transitions.create(["border-color", "box-shadow"]),
      fontWeight: "600",

      "&:focus": {
        borderRadius: 4,
        borderColor: "#80bdff",
        boxShadow: "0 0 0 0.2rem rgba(0,123,255,.25)",
      },
    },
  }))(InputBase);
  // SELECT STYLING FROM MATERIAL UI END

  // FOR NAVBAR SCROLLING EFFECT START
  window.addEventListener("scroll", function () {
    var header = document.querySelector(".navbarContainer");
    header.classList.toggle("sticky", window.scrollY > 1);
  });

  const classes = useStyles();
  // SEARCH FIELD FROM INPUT SEARCH LIBRARY
  let {
    getRootProps,
    getInputProps,
    getListboxProps,
    getOptionProps,
    groupedOptions,
  } = useAutocomplete({
    id: "use-autocomplete-demo",
    options: homeSearch,
    getOptionLabel: (option) => option.name,
    onInputChange: (e) => {
      setInputValue(e.target.value);
    },
  });

  // USE HISTORY HOOKS
  let history = useHistory();

  const formSubmit = () => {
    if (inputValue) {
      history.push(`/results?searched=${inputValue.replace(/ /g, "-")}&p=1`);
    }
    if (!inputValue || getInputProps().value) {
      const val = getInputProps().value;
      history.push("");
      history.push(
        `/results?propertySite&areaType=${selectBox.areaType}&propertyType=${
          selectBox.propertyType
        }&purpose=${selectBox.purpose}${
          (val && `&val=${val.replace(/ /g, "-")}`) || ""
        }&city=${selectBox.city}${
          (selectBox.beds !== "Any" && `&beds=${selectBox.beds}`) || ""
        }${(selectBox.baths !== "Any" && `&baths=${selectBox.baths}`) || ""}${
          (selectBox.priceMin != 0 && `&priceMin=${selectBox.priceMin}`) || ""
        }${
          (selectBox.priceMax !== "Any" && `&priceMax=${selectBox.priceMax}`) ||
          ""
        }${(selectBox.areaMin != 0 && `&areaMin=${selectBox.areaMin}`) || ""}${
          (selectBox.areaMax != "Any" && `&areaMax=${selectBox.areaMax}`) || ""
        }&p=1`
      );
    }
  };

  return (
    <header id="sctop">
      <div className={cn(Style.main_div)} id="head1">
        <div className={`${Style.s_div} navbarContainer`}>
          <div className="innerContainer">
            <div className={Style.logo} id="lgo">
              {/* <CgMenuLeftAlt id="mnu" className={Style.menu_icn} /> */}
              <Link to="/">
                <ProgressiveImage delay={600} src={lgo} placeholder={lgo}>
                  {(src) => <img src={src} alt="home" />}
                </ProgressiveImage>
              </Link>
            </div>
            <div className={Style.tabss}>
              <ul>
                <Link style={{ color: "#fff" }} title="Home" to="/">
                  <li className={cn(Style.lis)}>
                    <span>HOME</span>
                  </li>
                </Link>
                <Link to="/property" style={{ color: "#fff" }} title="Property">
                  <li className={cn(Style.lif)}>
                    <span>Property</span>
                  </li>
                </Link>
                <Link
                  style={{ color: "#fff" }}
                  to="/construction"
                  title="Construction"
                >
                  <li className={cn(Style.lis)}>
                    <span>Construction</span>
                  </li>
                </Link>
                <Link
                  style={{ color: "#fff" }}
                  to="/real-estate-agencies"
                  title="real-estate-agencies"
                >
                  <li className={cn(Style.lis)}>
                    <span>Agencies</span>
                  </li>
                </Link>
                <Link
                  style={{ color: "#fff" }}
                  to="/contact-us"
                  title="contact-us"
                >
                  <li className={cn(Style.lis)}>
                    <span>Contact Us</span>
                  </li>
                </Link>
                <Link style={{ color: "#fff" }} to="/wanted" title="wanted">
                  <li className={cn(Style.lit)}>
                    <span>Wanted</span>
                  </li>
                </Link>
              </ul>
            </div>
            {/* <div className={cn(Style.signin)}>
            <div className={Style.addprod}>
              <button>Add Products</button>
            </div>
            <div className={Style.signin_f} id="sing">
              Sign in
            </div>
            <div className={Style.signin_icn}>
              <BsPersonFill />
            </div>
          </div> */}
          </div>
        </div>
      </div>
      <ProgressiveImage delay={400} src={propertyBanner} placeholder={Ig1}>
        {(src, loading) => (
          <div className={Style.mn_div}>
            <img
              style={{
                filter: loading ? "blur(4px)" : "blur(0px)",
              }}
              src={src}
              alt="property real estate"
              className={Style.imgs}
            />
            <div className={Style.backg}>
              <div className={Style.backg_sdiv}>
                <h1>Search Your Desired Property</h1>
                {/* <span style={{ marginTop: "5px" }}>
                  <p>Millions of properties from top locations</p>
                </span> */}
                <div className={Style.main_bar}>
                  <div className={Style.searchdiv}>
                    <span className={Style.searchIcon}>
                      <RiSearch2Line fontSize="26px" />
                    </span>

                    {/* search field start */}
                    <div style={{ width: "450px" }}>
                      {homeSearch && (
                        <div id="listBox">
                          <div {...getRootProps()}>
                            <input
                              className={classes.input}
                              placeholder="Search..."
                              {...getInputProps()}
                            />
                          </div>
                          {getInputProps().value &&
                          groupedOptions.length > 0 ? (
                            <ul
                              className={classes.listbox}
                              {...getListboxProps()}
                            >
                              {groupedOptions.map((option, index) => (
                                <li {...getOptionProps({ option, index })}>
                                  {`${option.name} - ${option.parent} `}
                                </li>
                              ))}
                            </ul>
                          ) : null}
                        </div>
                      )}
                    </div>
                    {/* search field end */}

                    {/* MATERIAL UI SELECT BOXES START */}
                    {citiesData ? (
                      <div style={{ marginRight: "12px" }}>
                        <FormControl className={classes.margin}>
                          <Select
                            labelId="demo-customized-select-label"
                            id="demo-customized-select"
                            value={selectBox.city}
                            onChange={handleChange}
                            name="city"
                            input={<BootstrapInput />}
                          >
                            {citiesData.map((prev, i) => {
                              const { id, name } = prev;
                              return (
                                <MenuItem key={i} value={name.toLowerCase()}>
                                  {name}
                                </MenuItem>
                              );
                            })}
                          </Select>
                        </FormControl>
                      </div>
                    ) : (
                      <div style={{ marginRight: "12px" }}>
                        <FormControl className={classes.margin}>
                          <Select
                            labelId="demo-customized-select-label"
                            id="demo-customized-select"
                            value={selectBox.city}
                            onChange={handleChange}
                            name="city"
                            input={<BootstrapInput />}
                          >
                            <MenuItem value="karachi">Karachi</MenuItem>
                          </Select>
                        </FormControl>
                      </div>
                    )}

                    <FormControl className={classes.margin}>
                      <Select
                        labelId="demo-customized-select-label"
                        id="demo-customized-select"
                        value={selectBox.purpose}
                        onChange={handleChange}
                        name="purpose"
                        input={<BootstrapInput />}
                      >
                        <MenuItem value="for-sale">Buy</MenuItem>
                        <MenuItem value="for-rent">Rent</MenuItem>
                        <MenuItem value="for-booking">Booking</MenuItem>
                      </Select>
                    </FormControl>
                    {/* MATERIAL UI SELECT BOXES START */}

                    <Button
                      variant="contained"
                      classes={{ root: classes.searchButton }}
                      onClick={formSubmit}
                    >
                      Search
                    </Button>
                  </div>

                  <div className={Style.secategories}>
                    <div className={Style.VMBtn}>
                      <button onClick={resetHandle}>Reset</button>
                      &nbsp;&nbsp;&nbsp;&nbsp;
                      <button onClick={vmHandle}>View more</button>
                    </div>
                    <div className={Style.selectBoxes}>
                      <div className={Style.leftSide}>
                        <div>
                          <label htmlFor="category"> Property Type </label>
                          <br />

                          <FormControl className={classes.margin}>
                            <Select
                              labelId="demo-customized-select-label"
                              id="demo-customized-select"
                              value={selectBox.propertyType}
                              onChange={handleChange}
                              name="propertyType"
                              input={<MoreBootstrapInput />}
                            >
                              <MenuItem value="flat">Flat</MenuItem>
                              <MenuItem value="plot">Plot</MenuItem>
                              <MenuItem value="plot-file">Plot File</MenuItem>
                              <MenuItem value="agriculture-land">
                                Agriculture Land
                              </MenuItem>
                              <MenuItem value="villa">Villa</MenuItem>
                              <MenuItem value="shop">Shop</MenuItem>
                              <MenuItem value="lower-portion">
                                Lower Portion
                              </MenuItem>
                              <MenuItem value="upper-portion">
                                Upper Portion
                              </MenuItem>
                              <MenuItem value="farm-house">Farm House</MenuItem>
                              <MenuItem value="pent-house">Pent House</MenuItem>
                            </Select>
                          </FormControl>
                        </div>
                        <div>
                          <label htmlFor="type"> Area Type </label>
                          <br />
                          <FormControl className={classes.margin}>
                            <Select
                              labelId="demo-customized-select-label"
                              id="demo-customized-select"
                              value={selectBox.areaType}
                              onChange={handleChange}
                              name="areaType"
                              input={<MoreBootstrapInput />}
                            >
                              <MenuItem value="residential">
                                Residential
                              </MenuItem>
                              <MenuItem value="commercial">Commercial</MenuItem>
                              <MenuItem value="industrial">Industrial</MenuItem>
                            </Select>
                          </FormControl>
                        </div>
                      </div>

                      <div className={Style.rightSide}>
                        <div>
                          <label htmlFor="beds"> Beds </label>
                          <br />
                          <FormControl className={classes.margin}>
                            <Select
                              labelId="demo-customized-select-label"
                              id="demo-customized-select"
                              value={selectBox.beds}
                              onChange={handleChange}
                              name="beds"
                              input={<MoreBootstrapInput />}
                            >
                              <MenuItem value="Any">Any</MenuItem>
                              <MenuItem value="1">1</MenuItem>
                              <MenuItem value="2">2</MenuItem>
                              <MenuItem value="3">3</MenuItem>
                              <MenuItem value="4">4</MenuItem>
                              <MenuItem value="5">5</MenuItem>
                              <MenuItem value="6">6</MenuItem>
                              <MenuItem value="7">7</MenuItem>
                              <MenuItem value="8">8</MenuItem>
                              <MenuItem value="9">9</MenuItem>
                              <MenuItem value="10+">10+</MenuItem>
                            </Select>
                          </FormControl>
                        </div>
                        <div>
                          <label htmlFor="bath"> Baths </label>
                          <br />
                          <FormControl className={classes.margin}>
                            <Select
                              labelId="demo-customized-select-label"
                              id="demo-customized-select"
                              value={selectBox.baths}
                              onChange={handleChange}
                              name="baths"
                              input={<MoreBootstrapInput />}
                            >
                              <MenuItem value="Any">Any</MenuItem>
                              <MenuItem value="1">1</MenuItem>
                              <MenuItem value="2">2</MenuItem>
                              <MenuItem value="3">3</MenuItem>
                              <MenuItem value="4">4</MenuItem>
                              <MenuItem value="5">5</MenuItem>
                              <MenuItem value="6">6</MenuItem>
                              <MenuItem value="7">7</MenuItem>
                              <MenuItem value="8">8</MenuItem>
                              <MenuItem value="9">9</MenuItem>
                              <MenuItem value="10+">10+</MenuItem>
                            </Select>
                          </FormControl>
                        </div>
                      </div>
                    </div>

                    {/* SELECT BOXES FOR RANGES AND PRICES START */}
                    {viewMore && (
                      <div className={Style.selectBoxes}>
                        <div className={Style.leftSide}>
                          <div>
                            <label htmlFor="category"> Price Min </label>
                            <br />

                            <FormControl className={classes.margin}>
                              <Select
                                labelId="demo-customized-select-label"
                                id="demo-customized-select"
                                value={selectBox.priceMin}
                                onChange={handleChange}
                                name="priceMin"
                                input={<MoreBootstrapInput />}
                              >
                                <MenuItem value="0">0</MenuItem>
                                <MenuItem value="500000">500,000</MenuItem>
                                <MenuItem value="1000000">1,000,000</MenuItem>
                                <MenuItem value="2000000">2,000,000</MenuItem>
                                <MenuItem value="3500000">3,500,000</MenuItem>
                                <MenuItem value="5000000">5,000,000</MenuItem>
                                <MenuItem value="6500000">6,500,000</MenuItem>
                                <MenuItem value="8000000">8,000,000</MenuItem>
                                <MenuItem value="10000000">10,000,000</MenuItem>
                                <MenuItem value="12500000">12,500,000</MenuItem>
                                <MenuItem value="15000000">15,000,000</MenuItem>
                                <MenuItem value="17500000">17,500,000</MenuItem>
                                <MenuItem value="20000000">20,000,000</MenuItem>
                                <MenuItem value="30000000">30,000,000</MenuItem>
                                <MenuItem value="40000000">40,000,000</MenuItem>
                                <MenuItem value="50000000">50,000,000</MenuItem>
                                <MenuItem value="75000000">75,000,000</MenuItem>
                                <MenuItem value="100000000">
                                  100,000,000
                                </MenuItem>
                                <MenuItem value="250000000">
                                  250,000,000
                                </MenuItem>
                                <MenuItem value="500000000">
                                  500,000,000
                                </MenuItem>
                                <MenuItem value="500000000">
                                  500,000,000
                                </MenuItem>
                                <MenuItem value="1000000000">
                                  1,000,000,000
                                </MenuItem>
                              </Select>
                            </FormControl>
                          </div>
                          <div>
                            <label htmlFor="type"> Price Max </label>
                            <br />
                            <FormControl className={classes.margin}>
                              <Select
                                labelId="demo-customized-select-label"
                                id="demo-customized-select"
                                value={selectBox.priceMax}
                                onChange={handleChange}
                                name="priceMax"
                                input={<MoreBootstrapInput />}
                              >
                                <MenuItem value="Any">Any</MenuItem>
                                <MenuItem value="500000">500,000</MenuItem>
                                <MenuItem value="1000000">1,000,000</MenuItem>
                                <MenuItem value="2000000">2,000,000</MenuItem>
                                <MenuItem value="3500000">3,500,000</MenuItem>
                                <MenuItem value="5000000">5,000,000</MenuItem>
                                <MenuItem value="6500000">6,500,000</MenuItem>
                                <MenuItem value="8000000">8,000,000</MenuItem>
                                <MenuItem value="10000000">10,000,000</MenuItem>
                                <MenuItem value="12500000">12,500,000</MenuItem>
                                <MenuItem value="15000000">15,000,000</MenuItem>
                                <MenuItem value="17500000">17,500,000</MenuItem>
                                <MenuItem value="20000000">20,000,000</MenuItem>
                                <MenuItem value="30000000">30,000,000</MenuItem>
                                <MenuItem value="40000000">40,000,000</MenuItem>
                                <MenuItem value="50000000">50,000,000</MenuItem>
                                <MenuItem value="75000000">75,000,000</MenuItem>
                                <MenuItem value="100000000">
                                  100,000,000
                                </MenuItem>
                                <MenuItem value="250000000">
                                  250,000,000
                                </MenuItem>
                                <MenuItem value="500000000">
                                  500,000,000
                                </MenuItem>
                                <MenuItem value="500000000">
                                  500,000,000
                                </MenuItem>
                                <MenuItem value="1000000000">
                                  1,000,000,000
                                </MenuItem>
                                <MenuItem value="5000000000">
                                  5,000,000,000
                                </MenuItem>
                              </Select>
                            </FormControl>
                          </div>
                        </div>

                        <div className={Style.rightSide}>
                          <div>
                            <label htmlFor="beds"> Area Min </label>
                            <br />
                            <FormControl className={classes.margin}>
                              <Select
                                labelId="demo-customized-select-label"
                                id="demo-customized-select"
                                value={selectBox.areaMin}
                                onChange={handleChange}
                                name="areaMin"
                                input={<MoreBootstrapInput />}
                              >
                                <MenuItem value="0">0</MenuItem>
                                <MenuItem value="500">500</MenuItem>
                                <MenuItem value="1000">1000</MenuItem>
                                <MenuItem value="2000">2000</MenuItem>
                                <MenuItem value="2500">2500</MenuItem>
                                <MenuItem value="3000">3000</MenuItem>
                                <MenuItem value="3500">3500</MenuItem>
                              </Select>
                            </FormControl>
                          </div>
                          <div>
                            <label htmlFor="bath"> Area Max </label>
                            <br />
                            <FormControl className={classes.margin}>
                              <Select
                                labelId="demo-customized-select-label"
                                id="demo-customized-select"
                                value={selectBox.areaMax}
                                onChange={handleChange}
                                name="areaMax"
                                input={<MoreBootstrapInput />}
                              >
                                <MenuItem value="Any">Any</MenuItem>
                                <MenuItem value="500">500</MenuItem>
                                <MenuItem value="1000">1000</MenuItem>
                                <MenuItem value="2000">2000</MenuItem>
                                <MenuItem value="2500">2500</MenuItem>
                                <MenuItem value="3000">3000</MenuItem>
                                <MenuItem value="3500">3500</MenuItem>
                                <MenuItem value="40000">40000</MenuItem>
                              </Select>
                            </FormControl>
                          </div>
                        </div>
                      </div>
                    )}
                    {/* SELECT BOXES FOR RANGES AND PRICES END */}
                  </div>
                </div>
                <div
                  title="Wanted"
                  onClick={() => history.push("/wanted")}
                  className={cn(Style.wanted, Style.btn, Style.effect01)}
                >
                  <span>WANTED</span>
                </div>
              </div>
              {/* <div className={Style.bottom}>
                <img src={play} width="170px" alt="" />
              </div> */}
              <div className={Style.scroll}>
                <Lk smooth={true} duration={500} to="bestProperty">
                  <div className={Style.txtScroll}>Scroll</div>
                  <div>
                    <CgArrowLongDown size={30} />
                  </div>
                </Lk>
              </div>
              {/* <div className={Style.verticalLink}>
                <Lk
                  className="scrollLink"
                  smooth={true}
                  duration={500}
                  to="featuredProjects"
                >
                  Project
                </Lk>
                <Link to="maps">Maps</Link>
                <Lk
                  className="scrollLink"
                  smooth={true}
                  duration={500}
                  to="agencies"
                >
                  Agencies
                </Lk>
              </div> */}
            </div>
          </div>
        )}
      </ProgressiveImage>
    </header>
  );
}

const mapStatetoProps = (state) => {
  return {
    homeSearch: state.homeSearchAreas.homeSearch,
    citiesData: state.homeSearchAreas.citiesData,
  };
};
const mapDispatchtoProps = (dispatch) => {
  return {
    fetchAreaOne: function (data) {
      dispatch(fetchAreaOne(data));
    },
  };
};
export default connect(mapStatetoProps, mapDispatchtoProps)(Header2);
