import React from 'react'
import Style from './Style.module.scss'
import Stamp from "../../../../assets/welcome/cbc.webp";
import DHA from '../../../../assets/welcome/DHA.webp'
import { connect } from 'react-redux'
import { modalopen } from '../../../redux'

import brochure from '../../../../assets/welcome/Welcome_Center_brocher.pdf'

function Hero({ modalopen, bg }) {
  return (
    <div>
      <div
        className={
          bg === 'welbg' ? Style.img : bg === 'mjbg' ? Style.img1 : null
        }
      >
        <div className={Style.left}>
          <img src={Stamp} width="130px" height="165px" alt="" />
        </div>
        <div className={Style.right}>
          <p>SHOPS AND OFFICES ON ONE YEAR EASY INSTALLMENTS</p>
          <a className={Style.anc1} href={brochure} download='brochure'>
            Download Brochure
          </a>
          <a className={Style.anc2} onClick={() => modalopen()}>
            Book Your Unit Now!
          </a>
        </div>
        <div className={Style.gul}>
          <img src={DHA} width='140px' height='140px' alt='' />
        </div>
      </div>
    </div>
  )
}

const mapStatetoProps = (state) => {
  return {
    modalIsOpen: state.MenuState.modalIsOpen,
  }
}
const mapDispatchtoProps = (dispatch) => {
  return {
    modalopen: function () {
      dispatch(modalopen())
    },
  }
}
export default connect(mapStatetoProps, mapDispatchtoProps)(Hero)
