import { SINGLE_PROPERTY, SINGLE_AGENCY } from "./propertyDetailType";
import config from "../../../config";
import axios from "axios";

// SINGLE PROPERTY API START ==
const singleProperty = (data) => {
  return {
    type: SINGLE_PROPERTY,
    payload: data,
  };
};

export const singlePropertyApi = (productID) => {
  const id = productID.split("-");
  return (dispatch) => {
    axios
      .get(`${config.apiUrl}/properties/${id.pop()}&status=1`)
      .then((res) => {
        const singleData = res.data;
        dispatch(singleProperty(singleData.data));
      });
  };
};
// SINGLE PROPERTY API END ==

// SINGLE AGENCY API START ==
const singleAgency = (data) => {
  return {
    type: SINGLE_AGENCY,
    payload: data,
  };
};

export const singleAgencyApi = (productID) => {
  const id = productID.split("-");
  return (dispatch) => {
    axios.get(`${config.apiUrl}/agencies?id=${id[0]}&status=1`).then((res) => {
      const singleData = res.data;
      dispatch(singleAgency(singleData.data));
    });
  };
};
// SINGLE AGENCY API END ==
