import React, { useEffect } from "react";
import Style from "./Style.module.scss";
import MobilePropertCard from "../cards/propertyCard";
import { bestPropertiesApi } from "../../redux/index";
import { connect } from "react-redux";
import { useHistory } from "react-router-dom";
import SkeletonCard from "../../../website/components/skeletonCard";

function MobileBestProperty({ bestPropertiesApi, bestProperties }) {
	useEffect(() => {
		bestPropertiesApi();
	}, []);

	// SORT ARRAY TO TOP 10
	const top10properties = bestProperties.slice(0, 10);

	let history = useHistory();

	const skeleton = [1, 1, 1, 1, 1, 1];

	return (
		<div className={Style.main_div}>
			<div className={Style.fdiv}>
				<div className={Style.top}>
					<p>Best Properties For You </p>
					<p onClick={() => history.push("/results?allProperties&p=1")}>
						View All
					</p>
				</div>
				<div className={Style.sdiv}>
					<ul>
						{
							top10properties &&
								top10properties.map((prev, i) => {
									return (
										<li key={i}>
											<div>
												<MobilePropertCard prev={prev} />
											</div>
										</li>
									);
								})
							// : skeleton.map((prev, i) => {
							//     return (
							//       <div className={Style.SkeletonCard}>
							//         <SkeletonCard />
							//       </div>
							//     );
							//   })
						}
					</ul>
				</div>
			</div>
		</div>
	);
}

const mapStateToProps = (state) => ({
	bestProperties: state.bestPropertiesReducer.bestProperties,
});

const mapDispatchToProps = (dispatch) => {
	return {
		bestPropertiesApi: function () {
			dispatch(bestPropertiesApi());
		},
	};
};

export default connect(mapStateToProps, mapDispatchToProps)(MobileBestProperty);
