import React from "react";
import Style from "./Style.module.scss";
import FeaturedCard from "../cards/featuredCard";
import FP1 from "../../../assets/welcomeCenter.webp";
import FP2 from "../../../assets/jfTower.webp";

export default function MobileFeatureProject() {
  const featuredProjectData = [
    {
      name: "Welcome Center",
      desc: "Highway Developers and Builders",
      image: FP1,
      link: "/projects/welcome_center",
    },
    {
      name: "JF Tower",
      desc: "JF Developers",
      image: FP2,
      link: "/projects/jf-tower",
    },
    // { name: "Waqas Heights", desc: "Waqas Builders & Developers", image: FP3 },
  ];

  return (
    <div className={Style.main_div}>
      <div className={Style.fdiv}>
        <div className={Style.top}>
          <p>Featured Projects</p>
          {/* <p>View All</p> */}
        </div>
        <div className={Style.sdiv}>
          <ul>
            {featuredProjectData.map((prev, i) => {
              return (
                <li key={i} className="ml-0">
                  <FeaturedCard prev={prev} />
                </li>
              );
            })}
          </ul>
        </div>
      </div>
    </div>
  );
}
