import React from "react";
import Style from "./Style.module.scss";
import Price from "../../../../assets/welcome/price_plan_left.webp";
import Welcome from "../../../../assets/welcome/Welcome_Center_brocher.pdf";
import { connect } from "react-redux";
import { modalopen } from "../../../redux";

function Payments({ modalopen }) {
  return (
    <div className={Style.main} id="Price">
      <div className={Style.left}>
        <img src={Price} alt="" />
      </div>
      <div className={Style.right}>
        <p className={Style.para}>PAYMENT PLAN</p>
        <h1 className={Style.head}>PRICING STRUCTURE</h1>
        <div className={Style.bt_div}>
          <span className={Style.sp1}>Easy Installment Plan</span>
          <span className={Style.sp2}>30% Down Payment</span>
        </div>
        <h1 className={Style.head1}>READY SHOPS AND OFFICES ON </h1>
        <h1 className={Style.head1}>Easy 24 installments</h1>
        <div className={Style.btn_div}>
          <a
            href={Welcome}
            download="Welcome_Center_brocher"
            className={Style.btn_span1}
          >
            Download Brochure
          </a>
          <span className={Style.btn_span2} onClick={() => modalopen()}>
            Book Your Unit Now!
          </span>
        </div>
      </div>
    </div>
  );
}

const mapStatetoProps = (state) => {
  return {
    modalIsOpen: state.MenuState.modalIsOpen,
  };
};
const mapDispatchtoProps = (dispatch) => {
  return {
    modalopen: function () {
      dispatch(modalopen());
    },
  };
};
export default connect(mapStatetoProps, mapDispatchtoProps)(Payments);
