import React from "react";
import BestPropertyHeader from "../../components/bestPropertyHeader";
import BestPropertySearch from "../../components/bestPropertySearch";
import Newproducts from "../../components/newproducts";
import Newsletter from "../../components/newsletter";
import Footer from "../../components/footer";
import Seo from "../../components/seo";
import { useLocation } from "react-router";
import { capitalizeFunc } from "../../util";

const BestProperty_P = () => {
  // GETTING URL AS AN OBJECT START
  const location = useLocation();

  const string = location.search.substring(1).split("&");
  var urlData = {};
  for (var i = 0; i < string.length; i++) {
    var pair = string[i].split("=");
    var ind1 = pair[0];
    var ind2 = pair[1];

    urlData = { ...urlData, [ind1]: ind2 };
  }

  if (location.pathname && location.pathname !== "/results") {
    var forPropertyType = location.pathname.substring(1).split("/");
  }

  // GETTING URL AS AN OBJECT END
  const area = (urlData.val && urlData.val.replace(/-/g, " ")) || "";
  const heading = `Find the best properties from top locations ${
    area && `in ${area}`
  }`;

  return (
    <div>
      <Seo
        title={
          (forPropertyType &&
            `Browse The Luxury ${capitalizeFunc(
              forPropertyType[1]
            )} in Pakistan | Chhaat.com`) ||
          (area &&
            `Chhatt.com: Buy Sell and build for ${area} in ${urlData.city}`) ||
          (urlData.city &&
            `Find Out Best Properties in ${urlData.city}, Pakistan | Chhatt.com`) ||
          "Find Out Best Properties in Pakistan | Chhatt.com"
        }
        description={
          (forPropertyType &&
            `Looking for the Luxury ${capitalizeFunc(
              forPropertyType[1]
            )} in Pakistan? Must visit Chhatt.com and find the luxury flats, plots, villas, and apartments in your budget.`) ||
          `Browse the best properties in ${urlData.city} Pakistan. Find the latest apartments, plots, houses and commercial properties at leading property portal Chhatt.com`
        }
      />
      <BestPropertyHeader heading={heading} />
      <BestPropertySearch />
      <Newproducts HideClass="HideClass" Margins="margins" />
      <Newsletter />
      <Footer />
    </div>
  );
};

export default BestProperty_P;
