import React, { useState, useEffect } from "react";
import Style from "./Style.module.scss";
import { RiSearch2Line } from "react-icons/ri";
import useAutocomplete from "@material-ui/lab/useAutocomplete";
import { makeStyles, withStyles } from "@material-ui/core/styles";
import cn from "classnames";
import { connect } from "react-redux";
import { fetchAreaOne } from "../../redux";
import MenuItem from "@material-ui/core/MenuItem";
import FormControl from "@material-ui/core/FormControl";
import Select from "@material-ui/core/Select";
import InputBase from "@material-ui/core/InputBase";
import Button from "@material-ui/core/Button";

import { useHistory, useLocation } from "react-router-dom";

const BestPropertySearch = ({
  HideClass,
  heading,
  extraPadding,
  cityWidth,
  erasePadding,
  eraseMarginB,

  // REDUX FUNCTIONS FOR STATE START
  fetchAreaOne,
  homeSearch,
  citiesData,
  // REDUX FUNCTIONS FOR STATE END
}) => {
  const [windowSize, setWindowSize] = useState({
    width: undefined,
  });
  const [inputValue, setInputValue] = useState("");

  const location = useLocation();
  if (location.search) {
    // FOR ALREADY SELECTED TYPES START
    const string = location.search.substring(1).split("&");
    var urlData = {};
    for (var i = 0; i < string.length; i++) {
      var pair = string[i].split("=");
      var ind1 = pair[0];
      var ind2 = pair[1];

      urlData = { ...urlData, [ind1]: ind2 };
    }
    // FOR ALREADY SELECTED TYPES END
  }

  if (location.pathname && location.pathname !== "/results") {
    var pathNameFromExplore = location.pathname.substring(1).split("/");
  }

  let preInpVal = urlData && urlData.val && urlData.val.replace(/-/g, " ");

  const [selectBox, setSelectBox] = useState({
    city: (urlData && urlData.city) || "karachi",
    purpose: (urlData && urlData.purpose) || "for-sale",
    propertyType:
      (urlData && urlData.propertyType) ||
      (pathNameFromExplore && pathNameFromExplore[1]) ||
      "flat",
    areaType: (urlData && urlData.areaType) || "residential",
    beds: (urlData && urlData.beds) || "Any",
    baths: (urlData && urlData.baths) || "Any",
    priceMin: (urlData && urlData.priceMin) || "0",
    priceMax: (urlData && urlData.priceMax) || "Any",
    areaMin: (urlData && urlData.areaMin) || "0",
    areaMax: (urlData && urlData.areaMax) || "Any",
  });

  // API CALL DATA START
  useEffect(() => {
    fetchAreaOne(selectBox.city);
  }, [selectBox.city]);

  useEffect(() => {
    // Handler to call on window resize
    function handleResize() {
      // Set window width/height to state
      setWindowSize({
        width: window.innerWidth,
      });
    }
    // Add event listener
    window.addEventListener("resize", handleResize);
    // Call handler right away so state gets updated with initial window size
    handleResize();
    // Remove event listener on cleanup
    return () => window.removeEventListener("resize", handleResize);
  }, []); // Empty array ensures that effect is only run on mount

  // HANDLE CHANGE FOR ALL SELECT BOXES
  const handleChange = (event) => {
    const { name, value } = event.target;
    setSelectBox((prev) => {
      return {
        ...prev,
        [name]: value,
      };
    });
  };

  const resetHandle = () => {
    setSelectBox({
      city: "karachi",
      purpose: "for sale",
      propertyType: "flat",
      areaType: "residential",
      beds: "Any",
      baths: "Any",
      priceMin: "0",
      priceMax: "Any",
      areaMin: "0",
      areaMax: "Any",
    });
  };

  // INPUT FIELD STYLING MATERIAL UI START
  const useStyles = makeStyles((theme) => ({
    input: {
      outline: "none",

      width:
        (windowSize.width <= 1040 && 570) ||
        (windowSize.width <= 1070 && 600) ||
        (windowSize.width <= 1200 && 620) ||
        (windowSize.width <= 1250 && 700) ||
        (windowSize.width <= 1300 && 800) ||
        840,
      padding: "10px 10px",
    },
    listbox: {
      width:
        (windowSize.width <= 1040 && 895) ||
        (windowSize.width <= 1070 && 930) ||
        (windowSize.width <= 1200 && 1030) ||
        (windowSize.width <= 1250 && 1120) ||
        (windowSize.width <= 1300 && 1180) ||
        // 840,
        1220,
      margin: 0,
      padding: 0,
      zIndex: 1,
      marginTop: "5px",
      marginLeft: "-45px",
      [theme.breakpoints.down("md")]: {
        marginLeft: "-35px",
      },
      position: "absolute",

      listStyle: "none",
      backgroundColor: theme.palette.background.paper,
      overflow: "auto",
      maxHeight: 300,
      color: "#000",
      textAlign: "left",
      border: "1px solid rgba(0,0,0,.25)",
      '& li[data-focus="true"]': {
        backgroundColor: "#4a8df6",
        color: "#fff",
        cursor: "pointer",
      },
      "& li:active": {
        backgroundColor: "#2977f5",
        color: "#000",
      },
    },
    button: {
      textTransform: "none",
    },
  }));
  // INPUT FIELD STYLING MATERIAL UI END

  // SELECT STYLING FROM MATERIAL UI START
  const BootstrapInput = withStyles((theme) => ({
    root: {
      "label + &": {
        marginRight: theme.spacing(1),
      },
    },
    input: {
      borderRadius: 4,
      position: "relative",
      backgroundColor: "#f2f2f2",
      fontSize: 16,
      padding: "10px 26px 10px 12px",
      transition: theme.transitions.create(["border-color", "box-shadow"]),
      fontWeight: "600",

      "&:focus": {
        borderRadius: 4,
        borderColor: "#80bdff",
        boxShadow: "0 0 0 0.2rem rgba(0,123,255,.25)",
      },
    },
  }))(InputBase);

  // MORE INPUTS BELOW 4 INPUTS
  const MoreBootstrapInput = withStyles((theme) => ({
    root: {
      "label + &": {
        marginRight: theme.spacing(1),
      },
    },
    input: {
      width: "210px",
      borderRadius: 4,
      position: "relative",
      backgroundColor: "#ffff",
      fontSize: 16,
      padding: "7px 26px 7px 12px",
      transition: theme.transitions.create(["border-color", "box-shadow"]),
      fontWeight: "600",

      "&:focus": {
        borderRadius: 4,
        borderColor: "#80bdff",
        boxShadow: "0 0 0 0.2rem rgba(0,123,255,.25)",
      },
    },
  }))(InputBase);

  // SELECT STYLING FROM MATERIAL UI END

  const classes = useStyles();
  let {
    getRootProps,
    getInputProps,
    getListboxProps,
    getOptionProps,
    groupedOptions,
  } = useAutocomplete({
    id: "use-autocomplete-demo",
    options: homeSearch,
    getOptionLabel: (option) => option.name,
    onInputChange: (e) => {
      setInputValue(e.target.value);
    },
    defaultValue: preInpVal == undefined ? undefined : { name: preInpVal },
  });

  // USE HISTORY HOOKS
  let history = useHistory();

  const FormSubmit = () => {
    const val = getInputProps().value;
    history.push("");
    if (inputValue) {
      history.push(`/results?searched=${inputValue.replace(/ /g, "-")}&p=1`);
    }
    if (!inputValue || getInputProps().value) {
      history.push(
        `/results?propertySite&areaType=${selectBox.areaType}&propertyType=${
          selectBox.propertyType
        }&purpose=${selectBox.purpose}${
          (val && `&val=${val.replace(/ /g, "-")}`) || ""
        }&city=${selectBox.city}${
          (selectBox.beds !== "Any" && `&beds=${selectBox.beds}`) || ""
        }${(selectBox.baths !== "Any" && `&baths=${selectBox.baths}`) || ""}${
          (selectBox.priceMin != 0 && `&priceMin=${selectBox.priceMin}`) || ""
        }${
          (selectBox.priceMax !== "Any" && `&priceMax=${selectBox.priceMax}`) ||
          ""
        }${(selectBox.areaMin != 0 && `&areaMin=${selectBox.areaMin}`) || ""}${
          (selectBox.areaMax != "Any" && `&areaMax=${selectBox.areaMax}`) || ""
        }&p=1`
      );
    }
  };

  return (
    <div className={cn(Style.maindiv, eraseMarginB && Style.eraseMarginB)}>
      <div className={cn(Style.secdiv, extraPadding && Style.extraPadding)}>
        {/* ============================  SEARCH BOX START  ================================ */}
        <h3> {heading} </h3>
        <div className={Style.searchdiv}>
          <div className={Style.leftSec}>
            <span>
              <RiSearch2Line fontSize="26px" />
            </span>
            {/* search field start */}
            <div style={{ width: "450px" }}>
              {homeSearch && (
                <div id="listBox">
                  <div {...getRootProps()}>
                    <input
                      className={classes.input}
                      {...getInputProps()}
                      placeholder="Search..."
                    />
                  </div>
                  {getInputProps().value && groupedOptions.length > 0 ? (
                    <ul className={classes.listbox} {...getListboxProps()}>
                      {groupedOptions.map((option, index) => (
                        <li {...getOptionProps({ option, index })}>
                          {`${option.name} - ${option.parent} `}
                        </li>
                      ))}
                    </ul>
                  ) : null}
                </div>
              )}
            </div>
            {/* search field end */}
          </div>
          <div className={cn(Style.rightSec, HideClass && HideClass)}>
            {citiesData ? (
              <div style={{ marginRight: "12px" }}>
                <FormControl className={classes.margin}>
                  <Select
                    labelId="demo-customized-select-label"
                    id="demo-customized-select"
                    value={selectBox.city}
                    onChange={handleChange}
                    name="city"
                    input={<BootstrapInput />}
                  >
                    {citiesData.map((prev, i) => {
                      const { id, name } = prev;
                      return (
                        <MenuItem key={i} value={name.toLowerCase()}>
                          {name}
                        </MenuItem>
                      );
                    })}
                  </Select>
                </FormControl>
              </div>
            ) : (
              <div style={{ marginRight: "12px" }}>
                <FormControl className={classes.margin}>
                  <Select
                    labelId="demo-customized-select-label"
                    id="demo-customized-select"
                    value={selectBox.city}
                    onChange={handleChange}
                    name="city"
                    input={<BootstrapInput />}
                  >
                    <MenuItem value="karachi">Karachi</MenuItem>
                  </Select>
                </FormControl>
              </div>
            )}
            <div>
              <FormControl className={classes.margin}>
                <Select
                  labelId="demo-customized-select-label"
                  id="demo-customized-select"
                  value={selectBox.purpose}
                  onChange={handleChange}
                  name="purpose"
                  input={<BootstrapInput />}
                >
                  <MenuItem value="for-sale">Buy</MenuItem>
                  <MenuItem value="for-rent">Rent</MenuItem>
                  <MenuItem value="for-booking">Booking</MenuItem>
                </Select>
              </FormControl>
            </div>
            <Button
              variant="contained"
              classes={{ root: classes.button }}
              onClick={FormSubmit}
            >
              Search
            </Button>
          </div>
        </div>
        {/* ============================  SEARCH BOX END  ================================ */}

        {/* ============================  SELECT BOXES START  ================================ */}
        <div
          className={cn(
            Style.selectBoxes,
            HideClass && HideClass,
            erasePadding && Style.erasePadding
          )}
        >
          <div className={cn(Style.leftSide, HideClass && HideClass)}>
            <div>
              <label htmlFor="category"> Property Type </label>
              <br />

              <FormControl className={classes.margin}>
                <Select
                  labelId="demo-customized-select-label"
                  id="demo-customized-select"
                  value={selectBox.propertyType}
                  onChange={handleChange}
                  name="propertyType"
                  input={<MoreBootstrapInput />}
                >
                  <MenuItem value="flat">Flat</MenuItem>
                  <MenuItem value="plot">Plot</MenuItem>
                  <MenuItem value="plot-file">Plot File</MenuItem>
                  <MenuItem value="agriculture-land">Agriculture Land</MenuItem>
                  <MenuItem value="villa">Villa</MenuItem>
                  <MenuItem value="house">House</MenuItem>
                  <MenuItem value="shop">Shop</MenuItem>
                  <MenuItem value="lower-portion">Lower Portion</MenuItem>
                  <MenuItem value="upper-portion">Upper Portion</MenuItem>
                  <MenuItem value="farm-house">Farm House</MenuItem>
                  <MenuItem value="pent-house">Pent House</MenuItem>
                </Select>
              </FormControl>
            </div>
            <div>
              <label htmlFor="type"> Area Type </label>
              <br />
              <FormControl className={classes.margin}>
                <Select
                  labelId="demo-customized-select-label"
                  id="demo-customized-select"
                  value={selectBox.areaType}
                  onChange={handleChange}
                  name="areaType"
                  input={<MoreBootstrapInput />}
                >
                  <MenuItem value="residential">Residential</MenuItem>
                  <MenuItem value="commercial">Commercial</MenuItem>
                  <MenuItem value="industrial">Industrial</MenuItem>
                </Select>
              </FormControl>
            </div>
          </div>

          <div className={cn(Style.rightSide, HideClass && HideClass)}>
            <div>
              <label htmlFor="beds"> Beds </label>
              <br />
              <FormControl className={classes.margin}>
                <Select
                  labelId="demo-customized-select-label"
                  id="demo-customized-select"
                  value={selectBox.beds}
                  onChange={handleChange}
                  name="beds"
                  input={<MoreBootstrapInput />}
                >
                  <MenuItem value="Any">Any</MenuItem>
                  <MenuItem value="1">1</MenuItem>
                  <MenuItem value="2">2</MenuItem>
                  <MenuItem value="3">3</MenuItem>
                  <MenuItem value="4">4</MenuItem>
                  <MenuItem value="5">5</MenuItem>
                  <MenuItem value="6">6</MenuItem>
                  <MenuItem value="7">7</MenuItem>
                  <MenuItem value="8">8</MenuItem>
                  <MenuItem value="9">9</MenuItem>
                  <MenuItem value="10+">10+</MenuItem>
                </Select>
              </FormControl>
            </div>
            <div>
              <label htmlFor="bath"> Baths </label>
              <br />
              <FormControl className={classes.margin}>
                <Select
                  labelId="demo-customized-select-label"
                  id="demo-customized-select"
                  value={selectBox.baths}
                  onChange={handleChange}
                  name="baths"
                  input={<MoreBootstrapInput />}
                >
                  <MenuItem value="Any">Any</MenuItem>
                  <MenuItem value="1">1</MenuItem>
                  <MenuItem value="2">2</MenuItem>
                  <MenuItem value="3">3</MenuItem>
                  <MenuItem value="4">4</MenuItem>
                  <MenuItem value="5">5</MenuItem>
                  <MenuItem value="6">6</MenuItem>
                  <MenuItem value="7">7</MenuItem>
                  <MenuItem value="8">8</MenuItem>
                  <MenuItem value="9">9</MenuItem>
                  <MenuItem value="10+">10+</MenuItem>
                </Select>
              </FormControl>
            </div>
          </div>
        </div>
        {/* ============================  SELECT BOXES END  ================================ */}
        {/* ============================  SELECT BOXES START  ================================ */}
        <div
          className={cn(
            Style.selectBoxes,
            HideClass && HideClass,
            erasePadding && Style.erasePadding
          )}
        >
          <div className={cn(Style.leftSide, HideClass && HideClass)}>
            <div>
              <label htmlFor="category"> Price Min </label>
              <br />

              <FormControl className={classes.margin}>
                <Select
                  labelId="demo-customized-select-label"
                  id="demo-customized-select"
                  value={selectBox.priceMin}
                  onChange={handleChange}
                  name="priceMin"
                  input={<MoreBootstrapInput />}
                >
                  <MenuItem value="0">0</MenuItem>
                  <MenuItem value="500000">500,000</MenuItem>
                  <MenuItem value="1000000">1,000,000</MenuItem>
                  <MenuItem value="2000000">2,000,000</MenuItem>
                  <MenuItem value="3500000">3,500,000</MenuItem>
                  <MenuItem value="5000000">5,000,000</MenuItem>
                  <MenuItem value="6500000">6,500,000</MenuItem>
                  <MenuItem value="8000000">8,000,000</MenuItem>
                  <MenuItem value="10000000">10,000,000</MenuItem>
                  <MenuItem value="12500000">12,500,000</MenuItem>
                  <MenuItem value="15000000">15,000,000</MenuItem>
                  <MenuItem value="17500000">17,500,000</MenuItem>
                  <MenuItem value="20000000">20,000,000</MenuItem>
                  <MenuItem value="30000000">30,000,000</MenuItem>
                  <MenuItem value="40000000">40,000,000</MenuItem>
                  <MenuItem value="50000000">50,000,000</MenuItem>
                  <MenuItem value="75000000">75,000,000</MenuItem>
                  <MenuItem value="100000000">100,000,000</MenuItem>
                  <MenuItem value="250000000">250,000,000</MenuItem>
                  <MenuItem value="500000000">500,000,000</MenuItem>
                  <MenuItem value="500000000">500,000,000</MenuItem>
                  <MenuItem value="1000000000">1,000,000,000</MenuItem>
                </Select>
              </FormControl>
            </div>
            <div>
              <label htmlFor="type"> Price Max </label>
              <br />
              <FormControl className={classes.margin}>
                <Select
                  labelId="demo-customized-select-label"
                  id="demo-customized-select"
                  value={selectBox.priceMax}
                  onChange={handleChange}
                  name="priceMax"
                  input={<MoreBootstrapInput />}
                >
                  <MenuItem value="Any">Any</MenuItem>
                  <MenuItem value="500000">500,000</MenuItem>
                  <MenuItem value="1000000">1,000,000</MenuItem>
                  <MenuItem value="2000000">2,000,000</MenuItem>
                  <MenuItem value="3500000">3,500,000</MenuItem>
                  <MenuItem value="5000000">5,000,000</MenuItem>
                  <MenuItem value="6500000">6,500,000</MenuItem>
                  <MenuItem value="8000000">8,000,000</MenuItem>
                  <MenuItem value="10000000">10,000,000</MenuItem>
                  <MenuItem value="12500000">12,500,000</MenuItem>
                  <MenuItem value="15000000">15,000,000</MenuItem>
                  <MenuItem value="17500000">17,500,000</MenuItem>
                  <MenuItem value="20000000">20,000,000</MenuItem>
                  <MenuItem value="30000000">30,000,000</MenuItem>
                  <MenuItem value="40000000">40,000,000</MenuItem>
                  <MenuItem value="50000000">50,000,000</MenuItem>
                  <MenuItem value="75000000">75,000,000</MenuItem>
                  <MenuItem value="100000000">100,000,000</MenuItem>
                  <MenuItem value="250000000">250,000,000</MenuItem>
                  <MenuItem value="500000000">500,000,000</MenuItem>
                  <MenuItem value="500000000">500,000,000</MenuItem>
                  <MenuItem value="1000000000">1,000,000,000</MenuItem>
                  <MenuItem value="5000000000">5,000,000,000</MenuItem>
                </Select>
              </FormControl>
            </div>
          </div>

          <div className={cn(Style.rightSide, HideClass && HideClass)}>
            <div>
              <label htmlFor="beds"> Area Min </label>
              <br />
              <FormControl className={classes.margin}>
                <Select
                  labelId="demo-customized-select-label"
                  id="demo-customized-select"
                  value={selectBox.areaMin}
                  onChange={handleChange}
                  name="areaMin"
                  input={<MoreBootstrapInput />}
                >
                  <MenuItem value="0">0</MenuItem>
                  <MenuItem value="500">500</MenuItem>
                  <MenuItem value="1000">1000</MenuItem>
                  <MenuItem value="2000">2000</MenuItem>
                  <MenuItem value="2500">2500</MenuItem>
                  <MenuItem value="3000">3000</MenuItem>
                  <MenuItem value="3500">3500</MenuItem>
                </Select>
              </FormControl>
            </div>

            <div>
              <label htmlFor="bath"> Area Max </label>
              <br />
              <FormControl className={classes.margin}>
                <Select
                  labelId="demo-customized-select-label"
                  id="demo-customized-select"
                  value={selectBox.areaMax}
                  onChange={handleChange}
                  name="areaMax"
                  input={<MoreBootstrapInput />}
                >
                  <MenuItem value="Any">Any</MenuItem>
                  <MenuItem value="500">500</MenuItem>
                  <MenuItem value="1000">1000</MenuItem>
                  <MenuItem value="2000">2000</MenuItem>
                  <MenuItem value="2500">2500</MenuItem>
                  <MenuItem value="3000">3000</MenuItem>
                  <MenuItem value="3500">3500</MenuItem>
                  <MenuItem value="40000">40000</MenuItem>
                </Select>
              </FormControl>
            </div>
          </div>
        </div>
        {/* ============================  SELECT BOXES END  ================================ */}
        <div className={cn(Style.VMBtn, HideClass && HideClass)}>
          <button onClick={resetHandle}>Reset</button>
        </div>
      </div>
    </div>
  );
};

const mapStatetoProps = (state) => {
  return {
    homeSearch: state.homeSearchAreas.homeSearch,
    citiesData: state.homeSearchAreas.citiesData,
  };
};
const mapDispatchtoProps = (dispatch) => {
  return {
    fetchAreaOne: function (data) {
      dispatch(fetchAreaOne(data));
    },
  };
};
export default connect(mapStatetoProps, mapDispatchtoProps)(BestPropertySearch);
