import React, { useState } from 'react'
import Style from './Style.module.scss'
import PhoneInput from 'react-phone-input-2'
import 'react-phone-input-2/lib/style.css'
import { IoCall } from 'react-icons/io5'
import { MdEmail } from 'react-icons/md'
import { useForm } from 'react-hook-form'
import swal from 'sweetalert'
import axios from 'axios'
import { useLocation } from 'react-router-dom'
import Button from '@material-ui/core/Button'
import { makeStyles } from '@material-ui/core/styles'
import { PriceCommas } from 'website/util'

const useStyles = makeStyles(() => ({
  button: {
    textTransform: 'none',
    '&:hover': {
      backgroundColor: '#43b243',
    },
  },
}))

// ================ CONTACT FORM COMPONENT START ================
export const ContactForm = ({ singleProperty, singleAgency }) => {
  const classes = useStyles()

  const [phone, setPhone] = useState('')
  const [callNow, setCallNow] = useState(false)
  const location = useLocation()
  if (singleProperty) {
    var { user, id } = singleProperty
    var num = user.phone
    var u_id = user.id
  }
  const {
    register,
    handleSubmit,
    formState: { errors },
    reset,
  } = useForm()
  const onSubmit = (data) => {
    const { name, email, message } = data
    let sendingMessage = `${message} \nRegards: ${name}`
    axios
      .post(
        `https://uat.chhatt.com/api/leads?user_id=${u_id}&name=${name}&email=${email}&message=${sendingMessage}&phone=${num}&property_id=${id}&link=https://chhatt.com${location.pathname}&from=web`
      )
      .then(() =>
        swal(
          'Thank You!',
          'Your request has been submitted. You will be receiving a call from this realtor shortly.',
          'success'
        )
      )
    reset()
  }
  const defaultMessage = `I saw your ad on Chhatt.com (چھت). \nI am interested in your property`
  return (
    <div className={Style.contactContainer}>
      <h2>Contact Us</h2>
      <div className={Style.contactUser}>
        <div className={Style.imageContainer}>
          <img
            src={singleProperty && user.image}
            alt={singleProperty && user.name}
          />
        </div>
        <h5> {singleProperty && user.name} </h5>
      </div>
      <Button
        classes={{ root: classes.button }}
        variant='contained'
        onClick={() => setCallNow(true)}
        className={Style.showNumber}
      >
        <IoCall className='text-white' style={{ fontSize: '14px' }} />
        &nbsp;
        {callNow
          ? (user && '+92' + user.phone) ||
            (singleAgency && '+92' + singleAgency.owner_contact)
          : 'Call Now'}
      </Button>
      <hr />
      <div className={Style.innerContactform}>
        <form onSubmit={handleSubmit(onSubmit)}>
          <input
            style={
              errors.name && {
                border: '2px solid #ff040d',
              }
            }
            className={Style.inpC}
            type='text'
            placeholder='Name'
            {...register('name', {
              required: true,
              maxLength: 30,
              minLength: 3,
              pattern: /^[a-zA-Z]+(([',. -][a-zA-Z ])?[a-zA-Z]*)*$/g,
            })}
          />
          <div className={Style.inpErrors}>
            {errors.name &&
              errors.name.type === 'required' &&
              'name is required'}
            {errors.name &&
              errors.name.type === 'minLength' &&
              'Minimum 3 Characters required'}
            {errors.name &&
              errors.name.type === 'maxLength' &&
              'Maximum 30 Characters Allowed'}
            {errors.name &&
              errors.name.type === 'pattern' &&
              'please enter valid name'}
          </div>
          <br />
          <input
            style={
              errors.email && {
                border: '2px solid #ff040d',
              }
            }
            className={Style.inpC}
            type='text'
            name='email'
            placeholder='Email'
            {...register('email', {
              required: true,
              pattern:
                /^(([^<>()[\]\\.,;:\s@\"]+(\.[^<>()[\]\\.,;:\s@\"]+)*)|(\".+\"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/,
            })}
          />
          <div className={Style.inpErrors}>
            {errors.email &&
              errors.email.type === 'required' &&
              'Email is required'}
            {errors.email &&
              errors.email.type === 'pattern' &&
              'please enter valid email'}
          </div>
          <br />
          <PhoneInput
            country={'pk'}
            value={phone}
            onChange={(value) => setPhone(value)}
            countryCodeEditable={false}
            enableSearch={true}
            // style={
            //   errors.email && {
            //     border: "2px solid #ff040d",
            //     borderRadius: "6px",
            //   }
            // }
            inputProps={{
              required: true,
            }}
          />
          <textarea
            rows='5'
            defaultValue={defaultMessage}
            {...register('message', { pattern: /^([^0-9]*)$/ })}
          ></textarea>
          <br />
          <Button
            variant='contained'
            classes={{ root: classes.button }}
            type='submit'
          >
            <MdEmail className='text-white' style={{ fontSize: '17px' }} />
            &nbsp;Submit
          </Button>
        </form>
      </div>
    </div>
  )
}
// ================ CONTACT FORM COMPONENT END ================

// ================ SALE TAG COMPONENT START ================
export const SaleTage = ({ singleProperty }) => {
  const { price, area_one_name, address, property_type, property_for } =
    singleProperty

  return (
    <div className={Style.saleTagContainer}>
      <h2>
        {property_type} {property_for} in {area_one_name}
      </h2>
      <h2>PKR {PriceCommas(price)}</h2>
      <p> {address} </p>
    </div>
  )
}
// ================ SALE TAG COMPONENT END ================

// ================ LOGIN FORM COMPONENT START ================
export const LoginForm = () => {
  return (
    <div className={Style.loginForm}>
      <h4>Login</h4>
      <form>
        <input type='text' name='name' placeholder='Name' />
        <br />
        <input type='text' name='email' placeholder='Email' />
        <br />
        <button type='submit'>
          <MdEmail className='text-white' style={{ fontSize: '17px' }} />
          &nbsp;Submit
        </button>
      </form>
      <br />
      <h4 style={{ fontSize: '15px' }}>
        Need and account? <a href='http://chhatt.com/'>Register here!</a> <br />
        <a href='http://chhatt.com/'> Forgot Password? </a>
      </h4>
    </div>
  )
}
// ================ LOGIN FORM COMPONENT END ================
