import React from "react";
import Style from "./Style.module.css";
import cn from "classnames";
import Modal from "react-modal";
import { connect } from "react-redux";
import { modalopen, modalclose } from "../../../redux";
import axios from "axios";
import swal from "sweetalert";

const customStyles = {
  content: {
    top: "50%",
    left: "50%",
    right: "auto",
    bottom: "auto",
    marginRight: "-50%",
    transform: "translate(-50%, -50%)",
  },
};
Modal.setAppElement("#root");

function WelModal({ modalIsOpen, modalclose }) {
  let name = React.createRef();
  let email = React.createRef();
  let purchase = React.createRef();
  let phone = React.createRef();
  function closeModal() {
    modalclose();
  }
  function handlesubmit(e) {
    e.preventDefault();
    axios
      .post("https://chhatt.com/backend/admin/api/leads", {
        name: name.current.value,
        email: email.current.value,
        phone: phone.current.value,
        leadsource: "Project",
        property_type: purchase.current.value,
        project_id: "1",
      })
      .then(() => {
        modalclose();
        swal("Your request sucessfully register!");
      });
  }
  return (
    <div>
      <Modal
        isOpen={modalIsOpen}
        onRequestClose={closeModal}
        style={customStyles}
        contentLabel="Example Modal"
      >
        <h2 className={cn("text-center", Style.head)}>BOOK YOUR UNIT NOW</h2>
        {/* <div className="text-center">
          Enter your details below. Our sales representative will contact you
          shortly.
        </div> */}
        <form onSubmit={(e) => handlesubmit(e)}>
          <div className="form-group">
            <label htmlFor="exampleInputEmail1">Your Name*</label>
            <input
              type="text"
              className="form-control"
              id="exampleInputEmail1"
              placeholder="Name"
              ref={name}
              required
            />
            {/* <small id="emailHelp" className="form-text text-muted">
              We'll never share your email with anyone else.
            </small> */}
          </div>
          <div className="form-group">
            <label htmlFor="exampleInputPassword1">EMAIL ADDRESS*</label>
            <input
              type="email"
              className="form-control"
              id="exampleInputPassword1"
              placeholder="Email"
              ref={email}
              required
            />
          </div>
          <div className="form-group">
            <label htmlFor="exampleFormControlSelect1">PURCHASING*</label>
            <select
              className="form-control"
              id="exampleFormControlSelect1"
              ref={purchase}
              required
            >
              <option>Shop</option>
              <option>Office</option>
            </select>
          </div>
          <div className="form-group">
            <label htmlFor="exampleInputPassword1">PHONE NUMBER*</label>
            <input
              type="number"
              className="form-control"
              id="exampleInputPassword1"
              placeholder="eg:03032569826"
              ref={phone}
              required
            />
          </div>
          <div className="form-group">
            <label className="text-uppercase block">Select your City*</label>
            <select name="city_id" className="form-control" required>
              <option value="385">Karachi</option>
              <option value="385">Abbottabad</option>
              <option value="3">Islamabad</option>
              <option value="36">Murree</option>
              <option value="41">Rawalpindi</option>
            </select>
          </div>
          <div className={Style.bton_div}>
            <button
              type="submit"
              onClick={(e) => handlesubmit(e)}
              className="btn btn-success"
            >
              Submit
            </button>
            <button onClick={closeModal} className="btn btn-danger">
              close
            </button>
          </div>
        </form>
      </Modal>
    </div>
  );
}

const mapStatetoProps = (state) => {
  return {
    modalIsOpen: state.MenuState.modalIsOpen,
  };
};
const mapDispatchtoProps = (dispatch) => {
  return {
    modalopen: function () {
      dispatch(modalopen());
    },
    modalclose: function () {
      dispatch(modalclose());
    },
  };
};
export default connect(mapStatetoProps, mapDispatchtoProps)(WelModal);
