import React, { useEffect } from "react";
import Style from "./Style.module.scss";
import Header from "../../../mobile/components/header";
import ShareBtns from "../../components/shareBtns";
import {
	CallEmail,
	MobileDescription,
	MobileImageGallery,
	MobileSaleTag,
	MobileOverview,
} from "../../components/productBody";
import MapC from "../../components/productBody";
import MobileRelatedProperties from "../../components/relatedProperties";
import { useParams } from "react-router";
import { connect } from "react-redux";
import { singlePropertyApi, relatedPropertiesApi } from "../../redux";
import { useLocation } from "react-router-dom";

const MobileProduct = ({
	singlePropertyApi,
	singleProperty,
	relatedPropertiesApi,
	relatedProperties,
}) => {
	const { productId } = useParams();

	let location = useLocation();

	useEffect(() => {
		singlePropertyApi(productId);
	}, [location]);

	// USE-EFFECT FOR RELATED PROPERTIES
	useEffect(() => {
		if (singleProperty) {
			relatedPropertiesApi(data);
		}
	}, [singleProperty]);

	// DESTRUCTURING FOR RELATED PROPERTIES START
	const { area_one_name, area_type, size } = singleProperty;
	const data = {
		area_one_name,
		area_type,
		size,
	};
	// DESTRUCTURING FOR RELATED PROPERTIES END

	return (
		<div>
			<Header />
			<ShareBtns />
			<MobileImageGallery singleProperty={singleProperty} />
			<MobileSaleTag singleProperty={singleProperty} />
			<MobileOverview singleProperty={singleProperty} />
			<MobileDescription singleProperty={singleProperty} />
			<MapC singleProperty={singleProperty} />
			<MobileRelatedProperties
				heading="Related Properties"
				relatedProperties={relatedProperties}
			/>
			<CallEmail />
		</div>
	);
};

const mapStatetoProps = (state) => {
	return {
		singleProperty: state.singlePropertyReducer.singleProperty,
		relatedProperties: state.relatedPropertiesReducer.relatedProperties,
	};
};
const mapDispatchtoProps = (dispatch) => {
	return {
		singlePropertyApi: function (productID) {
			dispatch(singlePropertyApi(productID));
		},
		relatedPropertiesApi: function (data) {
			dispatch(relatedPropertiesApi(data));
		},
	};
};
export default connect(mapStatetoProps, mapDispatchtoProps)(MobileProduct);
