import React from "react";
import AgentCard from "../cards/agentCard";
import Slider from "react-slick";
import Style from "./Style.module.scss";

const Agents = ({ singleAgency }) => {
  var settings = {
    dots: false,
    infinite: false,
    speed: 500,
    slidesToShow:
      (singleAgency.agents.length === 1 && 1) ||
      (singleAgency.agents.length === 2 && 2) ||
      (singleAgency.agents.length === 3 && 3) ||
      (singleAgency.agents.length >= 4 && 4),
    slidesToScroll: 1,
    initialSlide: 0,
    responsive: [
      {
        breakpoint: 1360,
        settings: {
          slidesToShow:
            (singleAgency.agents.length === 1 && 1) ||
            (singleAgency.agents.length === 2 && 2) ||
            (singleAgency.agents.length >= 3 && 3),
          slidesToScroll: 1,
          infinite: true,
        },
      },
    ],
  };

  // CONTAINER MEDIA QUERY (RESPONSIVE ON LESS THEN 4 CARDS)
  const ContainerStyle = {
    width:
      (singleAgency.agents.length === 1 && "400px") ||
      (singleAgency.agents.length === 2 && "700px") ||
      (singleAgency.agents.length === 3 && "1050px"),
  };

  return (
    <div className={Style.maindiv}>
      <hr />
      <div className={Style.secdiv}>
        <h3> Agents </h3>
      </div>

      {/* property slider */}
      <div className={Style.mainSlide_Div} style={ContainerStyle}>
        {singleAgency && (
          <Slider {...settings}>
            {singleAgency.agents.map((data, i) => {
              return (
                <div key={i}>
                  <AgentCard singleAgency={data} />
                </div>
              );
            })}
          </Slider>
        )}
      </div>
    </div>
  );
};

export default Agents;
