import React, { useEffect } from "react";
import { connect } from "react-redux";
import { useHistory, useLocation } from "react-router";
import Style from "./Style.module.scss";
import {
  bestPropertyApi,
  mobileAgenciesListApi,
  mobileExplorePropertyApi,
} from "../../redux";
import MobilePaginationC from "../pagination";
import FP1 from "../../../assets/welcomeCenter.webp";
import FP2 from "../../../assets/jfTower.webp";
import SkeletonCard from "../skeletonCard";
import MobileAgencyPagination from "../agenciesPagination";
import MobileExplorePagination from "../explorePagination";
import { Link } from "react-router-dom";
import ErrorImg from "../../../assets/not_found_error.svg";

const MobileBestPropertiesList = ({
  bestPropertyApi,
  mobileBestProperties,
  heading,
  mobileAgenciesListApi,
  mobileAgenciesList,
  areasList,
  mobileAgencyInputVal,
  mobileExplorePropertyApi,
}) => {
  // GETTING URL AS AN OBJECT STARTb
  const location = useLocation();

  if (location.search) {
    const string = location.search.substring(1).split("&");

    var urlData = {};
    for (var i = 0; i < string.length; i++) {
      var pair = string[i].split("=");
      var ind1 = pair[0];
      var ind2 = pair[1];

      urlData = { ...urlData, [ind1]: ind2 };
    }
    // GETTING URL AS AN OBJECT EMD
  }

  // IN GETTING PATHNAME IN THE URL START
  if (location.pathname && location.pathname !== "/results") {
    var pathNameFromExplore = location.pathname.substring(1).split("/");
  }
  // IN GETTING PATHNAME IN THE URL END

  useEffect(() => {
    if (
      location.pathname &&
      location.pathname !== "/results" &&
      location.pathname !== "/real-estate-agencies"
    ) {
      mobileExplorePropertyApi(pathNameFromExplore);
    } else if (location.search) {
      // FOR PROPERTIES
      bestPropertyApi({ urlData, areasList });
    }

    // FOR AGENCIES
    if (
      !mobileAgencyInputVal &&
      location.pathname === "/real-estate-agencies"
    ) {
      mobileAgenciesListApi();
    }
  }, [location]);
  const history = useHistory();

  const featuredProjectData = [
    {
      name: "Welcome Center",
      desc: "Highway Developers and Builders",
      image: FP1,
      link: "/projects/welcome_center",
    },
    {
      name: "JF Tower",
      desc: "JF Developers",
      image: FP2,
      link: "/projects/jf-tower",
    },
    // { name: "Waqas Heights", desc: "Waqas Builders & Developers", image: FP3 },
  ];

  const skeletonData = [1, 1, 1, 1, 1, 1, 1, 1];

  return (
    <div className={Style.main_div}>
      <div className={Style.top}>
        <p> {heading} </p>
      </div>
      <div className={Style.cards_container}>
        {/* ===== BEST PROPERTIES LIST START ====== */}
        {(heading === "Best Properties For You" &&
          mobileBestProperties &&
          mobileBestProperties.data.data.map((prev, i) => {
            const {
              area_type,
              short_price,
              property_type,
              property_for,
              area_one_name,
              area_two_name,
              images,
              id,
              city,
            } = prev;

            let propertyFor = property_for.replace(" ", "_");
            let propertyType = property_type.replace(" ", "_");
            let areaTwo = area_two_name.replace(/ /g, "_");
            let areaOne = area_one_name.replace(/ /g, "_");
            let link = `/propertyDetail/${city.toLowerCase()}-${area_type.toLowerCase()}-${propertyType.toLowerCase()}-${propertyFor.toLowerCase()}-${areaOne}-${areaTwo}-${id}`;

            return (
              <Link to={link} key={i} className={Style.card_div}>
                <img
                  src={images[0].original}
                  className={Style.propertyImg}
                  alt={`${property_type} ${property_for}`}
                />
                <p> {area_type} </p>
                <b>
                  {property_type} {property_for} In {area_one_name}
                </b>
                <p>
                  <b>Rs : {short_price}</b>
                </p>
              </Link>
            );
          })) ||
          (heading === "Best Properties For You" &&
            skeletonData.map((prev, i) => {
              return (
                <div key={i} className={Style.skeleton_card_div}>
                  <SkeletonCard />
                </div>
              );
            }))}
        {/* ===== BEST PROPERTIES LIST END ====== */}

        {/* ===== PROJECT LIST START ====== */}
        {heading === "Featured Projects" &&
          featuredProjectData.map((prev, i) => {
            const { link, name, desc, image } = prev;
            return (
              <div
                onClick={() => history.push(link)}
                key={i}
                className={Style.project_card_div}
              >
                <img src={image} className={Style.propertyImg} alt={name} />
                <p> Karachi </p>
                <p> {name} </p>
                <h6>{desc}</h6>
              </div>
            );
          })}

        {/* ===== PROJECT LIST END ====== */}

        {/* ===== PROJECT LIST START ====== */}
        {(heading === "Agencies" &&
          mobileAgenciesList &&
          mobileAgenciesList.data.data.map((prev, i) => {
            const { city, agency_name, owner_name, image, id } = prev;

            return (
              <div
                onClick={() =>
                  history.push(
                    `/agency/${id}-${agency_name
                      .replace(/ /g, "-")
                      .toLowerCase()}-${city.toLowerCase()}`
                  )
                }
                key={i}
                className={Style.agencies_card_div}
              >
                <img
                  src={image}
                  className={Style.propertyImg}
                  alt={agency_name}
                />
                <p> {city} </p>
                <p> {agency_name} </p>
                <h6> {owner_name} </h6>
              </div>
            );
          })) ||
          (heading === "Agencies" &&
            skeletonData.map((prev, i) => {
              return (
                <div key={i} className={Style.skeleton_card_div}>
                  <SkeletonCard />
                </div>
              );
            }))}
        {/* ===== PROJECT LIST END ====== */}
      </div>

      {/* LOADING WHEN TH FILTER HAS 0 DATA TO SHOW START */}
      {(mobileBestProperties && mobileBestProperties.data.meta.total === 0 && (
        <div className={Style.errorImg}>
          <img src={ErrorImg} alt="error" />
        </div>
      )) ||
        (mobileAgenciesList && mobileAgenciesList.data.meta.total === 0 && (
          <div className={Style.errorImg}>
            <img src={ErrorImg} alt="error" />
          </div>
        )) ||
        ""}
      {/* LOADING WHEN TH FILTER HAS 0 DATA TO SHOW END */}

      {/* ===== BEST PROPERTIES PAGINATION START ====== */}
      {heading === "Best Properties For You" && location.search && (
        <MobilePaginationC
          localHomeSearchData={mobileBestProperties}
          urlData={urlData}
        />
      )}
      {/* ===== BEST PROPERTIES PAGINATION END ====== */}

      {/* ===== BEST PROPERTIES PAGINATION FROM EXPLORE PROPERTY START ====== */}
      {heading === "Best Properties For You" &&
        location.pathname &&
        location.pathname !== "/results" && (
          <MobileExplorePagination
            localHomeSearchData={mobileBestProperties}
            pathNameFromExplore={pathNameFromExplore}
          />
        )}
      {/* ===== BEST PROPERTIES PAGINATION FROM EXPLORE PROPERTY END ====== */}

      {/* ===== AGENCIES PAGINATION START ====== */}
      {heading === "Agencies" && mobileAgencyInputVal.length === 0 && (
        <MobileAgencyPagination mobileAgenciesList={mobileAgenciesList} />
      )}
      {/* ===== AGENCIES PAGINATION END ====== */}
    </div>
  );
};

const mapStatetoProps = (state) => {
  return {
    mobileBestProperties:
      state.mobileBestPropertiesReducer.mobileBestProperties,
    mobileAgenciesList:
      state.mobileFeaturedPropertiesReducer.mobileAgenciesList,
    areasList: state.mobileBestPropertiesReducer.areasList,
    mobileAgencyInputVal:
      state.mobileFeaturedPropertiesReducer.mobileAgencyInputVal,
  };
};

const mapDispatchtoProps = (dispatch) => {
  return {
    bestPropertyApi: function (data) {
      dispatch(bestPropertyApi(data));
    },
    mobileAgenciesListApi: function (data) {
      dispatch(mobileAgenciesListApi(data));
    },
    mobileExplorePropertyApi: function (data) {
      dispatch(mobileExplorePropertyApi(data));
    },
  };
};

export default connect(
  mapStatetoProps,
  mapDispatchtoProps
)(MobileBestPropertiesList);
