import { createStore, applyMiddleware, combineReducers } from "redux";
// import logger from "redux-logger";
import thunk from "redux-thunk";
import homeSearchAreas from "./searchapis/homeReducer";
import singlePropertyReducer from "./propertDetail/propertyDetailReducer";
import bestPropertiesReducer from "./bestProperty/bestPropertyReducer";
import relatedPropertiesReducer from "./relatedProperties/relatedPropertiesReducer";
import featuredPropertiesReducer from "./featuredProjects_agencies_maps/featuredProjectsReducer";
import MenuState from "./menu/menuReducer";
import nearByData from "./wanted/searchAreaReducer";
import breadcrumbReducer from "./breadcrumb/breadcrumbReducer";

// const middleware = applyMiddleware(thunk, logger);
const middleware = applyMiddleware(thunk);
// const middleware = applyMiddleware(thunk);
const rootReducer = combineReducers({
  homeSearchAreas,
  singlePropertyReducer,
  bestPropertiesReducer,
  relatedPropertiesReducer,
  featuredPropertiesReducer,
  featuredPropertiesReducer,
  MenuState,
  nearByData,
  breadcrumbReducer,
});
const store = createStore(rootReducer, middleware);
export default store;
