import { BEST_PROPERTIES_DATA } from "./bestPropertType";

const initialState = {
  bestProperties: "",
};

const bestPropertiesReducer = (state = initialState, action) => {
  switch (action.type) {
    case BEST_PROPERTIES_DATA:
      return {
        ...state,
        bestProperties: action.payload,
      };
    default:
      return state;
  }
};
export default bestPropertiesReducer;
