import React from 'react'
import Style from './Style.module.scss'
import Blogo from '../../../assets/chhattLogo.png'
import play from '../../../assets/icons/play.png'
import Call from '../../../assets/icons/call.png'
import Mail from '../../../assets/icons/email.png'
import Pin from '../../../assets/icons/pin.png'
import { MdKeyboardArrowUp } from 'react-icons/md'
import { Link as Lk } from 'react-scroll'
import { Link } from 'react-router-dom'

import {
  FaInstagram,
  FaFacebook,
  FaYoutube,
  FaLinkedin,
  FaGooglePlay,
} from 'react-icons/fa'

export default function Footer() {
  return (
    <footer className={Style.main_div}>
      <div className={Style.sub_div}>
        <div className={Style.fdiv}>
          <div className={Style.call}>
            <div className={Style.callimg}>
              <img src={Call} alt='chhatt' />
            </div>
            <div className={Style.txt_div}>
              <p className='m-0'>Phone</p>
              <p className='m-0'>033 02 244288</p>
            </div>
          </div>
          <div className={Style.call1}>
            <div className={Style.callimg}>
              <img src={Mail} alt='mail' />
            </div>
            <div className={Style.txt_div}>
              <p className='m-0'>Email</p>
              <p className='m-0'>info@Chhatt.com</p>
            </div>
          </div>
          <div className={Style.call2}>
            <div className={Style.callimg}>
              <img src={Pin} alt='calling' />
            </div>
            <div className={Style.txt_div}>
              <p className='m-0'>Address</p>
              <p className='m-0'>9-C Khayaban-e-Abbasi, D.H.A Phase 7</p>
              <p className='m-0'>Ext Karachi, city, Sindh 75500</p>
            </div>
          </div>
        </div>
        <div className={Style.sdiv}>
          <p>
            <b>Company</b>
          </p>
          <ul>
            <Link to='/'>
              <li>Home</li>
            </Link>
            {/* <li>
              <Link to="/FeaturedProjectsPage">Projects</Link>
            </li> */}
            <li>
              <Link to='/real-estate-agencies'>Agencies</Link>
            </li>
            {/* <li>
              <Link to="/maps">Maps</Link>
            </li> */}
            <li>
              <Link to='/about-us'>About Us</Link>
            </li>
            <li>
              <Link to='/contact-us'>Contact Us</Link>
            </li>
          </ul>
        </div>
        <div className={Style.tdiv}>
          <img src={Blogo} alt='Chhatt Logo' title='Chhatt' />
          <p>
            Chhatt is a powerful platform which aims at connecting
            realtors,builders,developers,brokers with right kind of clientele
            for variouse types of properties from various mediums. It is a mix
            of a web platform as well as a mobile application(s)
          </p>
          <div className={Style.play}>
            {/* <p>
              <b>Search, Compare & Get</b>
            </p> */}

            <div className={Style.playHeading}>
              <h1>Get in Touch</h1>
            </div>
            <div className={Style.playIcons}>
              <a
                href='https://www.instagram.com/chhattcom/?igshid=1aynfz0mquewo'
                className={`${Style.icon} ${Style.instagram}`}
              >
                <FaInstagram size={25} className={Style.iconInstagram} />
              </a>

              <a
                href='https://www.facebook.com/ChhattPK/'
                className={`${Style.icon} ${Style.facebook}`}
              >
                {/* <div > */}
                <FaFacebook size={25} className={Style.iconFacebook} />
                {/* </div> */}
              </a>
              <a
                href='https://www.youtube.com/channel/UCrQuXvCQMRvNSpaU3znG9cA/featured'
                className={`${Style.icon} ${Style.youtube}`}
              >
                <FaYoutube size={25} className={Style.iconYoutube} />
              </a>

              <a
                href='https://www.linkedin.com/company/chhatt-com'
                className={`${Style.icon} ${Style.linkedin}`}
              >
                <FaLinkedin size={25} />
              </a>
              <a className={`${Style.icon} ${Style.google}`}>
                {/* <img
                  src={play}
                  width={40}
                  height={40}
                  alt='Open Google Play to Download and Install this App'
                  title='Open Google Play to Download and Install this App'
                  aria-label='Open Google Play to Download and Install this App'
                /> */}
                <FaGooglePlay size={25} style={{ paddingLeft: '0.2rem' }} />
              </a>
            </div>
          </div>
        </div>
      </div>
      <div className={Style.footerbottom}>
        <div className={Style.footersub_div}>
          <p className='m-0'>
            &copy;2021 <span style={{ color: '#007BFF' }}>Chhatt</span>, All
            rights reserved
          </p>
          <Lk className='soll' smooth={true} duration={500} to='sctop'>
            <button>
              <MdKeyboardArrowUp style={{ color: 'white', fontSize: '30px' }} />
            </button>
          </Lk>
        </div>
      </div>
    </footer>
  )
}
