import React from "react";
import Style from "./Style.module.scss";
import { AiOutlineHome } from "react-icons/ai";
import { VscKey } from "react-icons/vsc";
import { IoConstructOutline } from "react-icons/io5";
import { BsSearch } from "react-icons/bs";
import { NavLink } from "react-router-dom";
import cn from "classnames";
import { GoPlus } from "react-icons/go";

const BottomNavigation = () => {
  return (
    <>
      <div className={Style.container}>
        <div className={Style.main_container}>
          <div className={Style.main_div}>
            <NavLink exact activeClassName="active" to="/">
              <div className={Style.each_navigation}>
                <AiOutlineHome className={cn(Style.NavIcon, Style.active)} />
                <h6>Home</h6>
              </div>
            </NavLink>
            <NavLink exact activeClassName="active" to="/property">
              <div className={Style.each_navigation}>
                <VscKey className={Style.NavIcon} />
                <h6>Property</h6>
              </div>
            </NavLink>
            <NavLink exact activeClassName="active" to="/">
              <div className={Style.each_navigation}>
                <IoConstructOutline className={Style.NavIcon} />
                <h6>Construction</h6>
              </div>
            </NavLink>
            <NavLink exact activeClassName="active" to="/filter/buy">
              <div className={Style.each_navigation}>
                <BsSearch className={Style.NavIcon} />
                <h6>Search</h6>
              </div>
            </NavLink>
          </div>
          {/* <div className={Style.addBtn}>
            <span>
              <GoPlus />
            </span>
          </div> */}
        </div>
      </div>
    </>
  );
};

export default BottomNavigation;
