import React, { useState, useEffect } from 'react'
import Style from './Style.module.css'
import cn from 'classnames'
import PhoneInput from 'react-phone-input-2'
import 'react-phone-input-2/lib/style.css'
import background2 from '../../../assets/mob.webp'
import bc from '../../../assets/mob.png'
import { connect } from 'react-redux'
import { fetchAreaOne } from '../../redux'
import Axios from 'axios'
import Modal from 'react-bootstrap/Modal'
import Button from 'react-bootstrap/Button'
import BestPropertyHeader from '../../components/bestPropertyHeader'
import Footer from '../../components/footer'
import Newsletter from '../../components/newsletter'
import MuiButton from '@material-ui/core/Button'
import { makeStyles } from '@material-ui/core/styles'
// import Header from "../../Components/Header/index";

const useStyles = makeStyles(() => ({
  button: {
    textTransform: 'none',
    border: 'none',
    outline: 'none',
    backgroundColor: '#007bff',
    color: '#ffffff',
    padding: '7px 10px',
    marginTop: '20px',
    '&:hover': {
      backgroundColor: '#007bff',
    },
  },
}))

function Tellus({ homeSearch, fetchAreaOne }) {
  const classes = useStyles()

  const [area, setArea] = useState()
  const [email, setEmail] = useState()
  const [name, setName] = useState()
  const [budget, setBudget] = useState()
  const [leadType, setLeadType] = useState('For Buy')
  const [soon, setSoon] = useState('How Soon')
  const [familyMem, setFamilyMem] = useState('')
  const [phoneNumber, setPhoneNumber] = useState()
  const [textArea, setTextArea] = useState()
  const [propType, setPropType] = useState('Flat/Apartment')
  const [error, setError] = useState()
  const [show, setShow] = useState(false)

  let handleClose = () => {
    setShow(false)
    if (show) {
      window.location = '/'
    }
  }
  const handleShow = () => {
    setShow(true)
  }

  useEffect(() => {
    fetchAreaOne()
  }, [])
  const submitHandle = (e) => {
    e.preventDefault()
    let num = ''
    if (phoneNumber === undefined) {
      setError('Phone number is not valid')
    } else {
      num = phoneNumber
      Axios.post('https://uat.chhatt.com/api/leads', {
        area: area,
        name: name,
        email: email,
        budget: budget,
        lead_type: leadType,
        how_soon: soon,
        family_members: familyMem,
        phone: phoneNumber,
        description: textArea,
        property_type: propType,
        leadsource: 'From Wanted',
        from: 'wanted',
      }).then(
        (response) => {
          handleShow()
        }
        // (error) => {
        // console.log(error);
        // }
      )
    }
  }
  let a = window.screen.height
  let hei = `calc(${a}px - ${a / 11}px )`
  // console.log(a - a / 11);
  return (
    // <div className={Style.mn_div}>
    <>
      {/* Modal */}
      {/* <Header /> */}
      <BestPropertyHeader />
      <div className={Style.w_div}>
        <div className={Style.wanted}>
          <h1>WANTED</h1>
        </div>
      </div>
      <Modal show={show}>
        <Modal.Header>
          <Modal.Title style={{ color: '#1a9bd0', margin: 'auto' }}>
            Successfully
          </Modal.Title>
        </Modal.Header>
        <Modal.Body style={{ margin: 'auto' }}>
          Your Requirement Registered Successfully!
        </Modal.Body>
        <Modal.Footer>
          <Button
            variant='success'
            style={{ padding: '10px 20px', margin: 'auto' }}
            onClick={handleClose}
          >
            OK
          </Button>
        </Modal.Footer>
      </Modal>
      {/* Modal */}
      <div className={Style.m_div}>
        <div className={Style.div1}>
          <picture className={Style.igm}>
            <source type='image/webp' srcSet={background2} />
            <img src={bc} alt='' width='100%' height='100%' />
          </picture>
        </div>
        <div className={Style.main_div}>
          <h2 className='f_size_Large text-center mt-3 mb-3'>
            TELL US WHAT YOU WANT
          </h2>
          <form className={Style.form} onSubmit={(e) => submitHandle(e)}>
            {/* area */}
            <div className={cn(Style.abd)}>
              <input
                className={cn(Style.product)}
                list='datalistOptions'
                placeholder='Select Area'
                required
                onChange={(e) => setArea(e.target.value)}
              />
              <datalist
                id='datalistOptions'
                style={{
                  wordWrap: 'break-word',
                  width: '200px',
                  height: '300px',
                }}
              >
                {homeSearch.map((option, i) => {
                  return (
                    <option
                      width='150'
                      key={i}
                      style={{ wordWrap: 'break-word', width: '200px' }}
                    >
                      {`${option.name} - ${option.parent} `}
                    </option>
                  )
                })}
              </datalist>
            </div>
            {/* area */}
            {/* name */}
            <input
              required
              placeholder='Enter your name'
              className={cn(Style.product, 'mt-3')}
              type='text'
              onChange={(e) => setName(e.target.value)}
            />
            {/* name */}
            {/* email */}
            <input
              required
              placeholder='Enter your email'
              className={cn(Style.product, 'mt-3')}
              type='text'
              onChange={(e) => setEmail(e.target.value)}
            />
            {/* email */}
            {/* Budget */}
            <input
              required
              placeholder='Budget'
              className={cn(Style.product, 'mt-3')}
              type='number'
              onChange={(e) => setBudget(e.target.value)}
            />
            {/* Budget */}
            {/* lead type */}
            <select
              required
              className={cn(Style.lead, 'form-control mt-3')}
              value={leadType}
              onChange={(e) => setLeadType(e.target.value)}
            >
              <option>For Buy</option>
              <option>For Rent</option>
            </select>
            {/* lead type */}
            {/* how soon */}
            <select
              required
              className={cn(Style.lead, 'form-control mt-3')}
              value={soon}
              onChange={(e) => setSoon(e.target.value)}
            >
              <option disabled hidden>
                How Soon
              </option>
              <option>15 Days</option>
              <option>1 Month</option>
              <option>3 Months</option>
            </select>
            {/* how soon */}
            {/* property type */}
            <select
              required
              className={cn(Style.lead, 'form-control mt-3')}
              value={propType}
              onChange={(e) => setPropType(e.target.value)}
            >
              <option disabled hidden>
                Property Type
              </option>
              <option>Flat/Apartment</option>
              <option>House</option>
              <option>Villa</option>
            </select>
            {/* property type */}
            {/* Faimly Members */}
            <input
              required
              placeholder='Faimly Members'
              className={cn(Style.product, 'mt-3')}
              type='text'
              onChange={(e) => setFamilyMem(e.target.value)}
            />
            {/* Faimly Members */}
            {/* phoneNumber */}
            <PhoneInput
              countryCodeEditable={false}
              enableSearch={true}
              country={'pk'}
              value={phoneNumber}
              containerClass='abc mt-3'
              inputStyle={{
                width: '100%',
                borderRadius: '1px',
              }}
              inputProps={{
                name: 'phone',
                required: true,
              }}
              onChange={(phone, country, e, formattedValue) => {
                let coun = country.format.length
                let format = formattedValue.length
                if (coun === format) {
                  setPhoneNumber(phone)
                }
              }}
            />
            <p className={Style.error}>{error}</p>
            {/* phoneNumber */}
            {/* text area */}
            <textarea
              width='100%'
              placeholder='Requirement'
              rows='4'
              className={cn('mt-3 form-control', Style.textare)}
              onChange={(e) => setTextArea(e.target.value)}
            ></textarea>
            {/* text area */}
            <MuiButton
              variant='contained'
              // className={Style.btnsub}
              classes={{ root: classes.button }}
              type='submit'
            >
              Submit Requirement
            </MuiButton>
          </form>
        </div>
      </div>
      <br />
      <br />
      <Newsletter />
      <Footer />
    </>
    // {/* </div> */}
  )
}
const mapStatetoProps = (state) => {
  return {
    homeSearch: state.homeSearchAreas.homeSearch,
  }
}
const mapDispatchtoProps = (dispatch) => {
  return {
    fetchAreaOne: function () {
      dispatch(fetchAreaOne())
    },
  }
}
export default connect(mapStatetoProps, mapDispatchtoProps)(Tellus)
