import React, { useRef, useState } from "react";
import Style from "./Style.module.scss";
import Areaimg from "../../../../assets/icons/area.png";
import cn from "classnames";
import { PriceCommas } from "../../../util";
import { FaMobileAlt, FaPhoneAlt } from "react-icons/fa";
import { MdContentCopy } from "react-icons/md";
import moment from "moment";
import Dialog from "@material-ui/core/Dialog";
import DialogContent from "@material-ui/core/DialogContent";
import DialogTitle from "@material-ui/core/DialogTitle";
import Paper from "@material-ui/core/Paper";
import Button from "@material-ui/core/Button";
import { makeStyles } from "@material-ui/core/styles";

import Draggable from "react-draggable";
import { Link } from "react-router-dom";

// TO MAKE (CONTACT-US) POP-UP DRAGGABLE START
function PaperComponent(props) {
  return (
    <Draggable
      handle="#draggable-dialog-title"
      cancel={'[class*="MuiDialogContent-root"]'}
    >
      <Paper {...props} />
    </Draggable>
  );
}

const useStyles = makeStyles(() => ({
  button: {
    textTransform: "none",
  },
}));
// TO MAKE (CONTACT-US) POP-UP DRAGGABLE END
export default function PropertyCard({ totalWidth, prev }) {
  const classes = useStyles();
  const [open, setOpen] = useState(false);
  const handleClickOpen = () => {
    setOpen(true);
  };
  const handleClose = () => {
    setOpen(false);
  };
  // DATA GETTING FROM HOME_PROPERTY START
  const {
    // address,
    area_one_name,
    area_two_name,
    category,
    images,
    price,
    property_type,
    area_type,
    property_for,
    id,
    size,
    size_type,
    bed,
    bath,
    city,
    created_at,
    // user: { image, name },
  } = prev;
  // DATA GETTING FROM HOME_PROPERTY END

  // console.log("lol", area_two_name, id);
  let propertyFor = property_for.replace(" ", "_");
  let propertyType = property_type.replace(" ", "_");
  let areaTwo =
    (area_two_name !== null && area_two_name.replace(/ /g, "_")) || "";
  let areaOne =
    (area_one_name !== null && area_one_name.replace(/ /g, "_")) || "";
  let val = `/propertyDetail/${city.toLowerCase()}-${area_type.toLowerCase()}-${propertyType.toLowerCase()}-${propertyFor.toLowerCase()}-${areaOne}-${areaTwo}-${id}`;

  // CREATING DAYS AGO FOR CARD
  var ago = moment.duration(moment().diff(created_at)).humanize() + " ago";
  const copyD1 = useRef(null);
  const copyD2 = useRef(null);
  const copy1 = () => {
    copyD1.current.select();
    document.execCommand("copy");
  };
  const copy2 = () => {
    copyD2.current.select();
    document.execCommand("copy");
  };

  return (
    <>
      {/* modal start */}
      <div>
        <Dialog
          open={open}
          onClose={handleClose}
          aria-describedby="alert-dialog-description"
          aria-labelledby="draggable-dialog-title"
          PaperComponent={PaperComponent}
        >
          <DialogTitle
            id="draggable-dialog-title"
            style={{ borderTop: "6px solid #007BFF", cursor: "move" }}
          >
            {/* {"Contact Us  "} */}
            <div className={Style.text_center}>
              <h4>
                <strong>Contact Us</strong>
              </h4>
              {prev.user.agent.name && (
                <>
                  <small> {prev.user.agent.name} </small>
                  <br />
                </>
              )}
              {prev.user.agent.agency_name && (
                <small> {prev.user.agent.agency_name} </small>
              )}
            </div>
          </DialogTitle>
          {prev.user.agent.contact && (
            <>
              <DialogContent>
                <div className={Style.mob_div}>
                  <div className={Style.sec_div}>
                    <div className={Style.mob_icn}>
                      <FaMobileAlt fontSize="20px" color="#000" />
                    </div>
                    <div className={Style.mob_num}>
                      <span className={Style.span_mob}>Mobile</span>
                      <input
                        type="text"
                        readOnly
                        ref={copyD1}
                        value={`+92-${prev.user.agent.contact}`}
                        style={{ color: "#007BFF" }}
                      />
                    </div>
                  </div>
                  <button onFocus={copy1} className={Style.copy_div}>
                    <MdContentCopy fontSize="22px" />
                    Copy
                  </button>
                </div>
              </DialogContent>
            </>
          )}
          {prev.user.phone && (
            <DialogContent>
              <div className={Style.mob_div}>
                <div className={Style.sec_div}>
                  <div className={Style.mob_icn}>
                    <FaPhoneAlt fontSize="20px" color="#000" />
                  </div>
                  <div className={Style.mob_num}>
                    <span className={Style.span_mob}>Phone</span>
                    <input
                      type="text"
                      readOnly
                      ref={copyD2}
                      value={`+92-${prev.user.phone}`}
                      style={{ color: "#007BFF" }}
                    />
                  </div>
                </div>
                <button onFocus={copy2} className={Style.copy_div}>
                  <MdContentCopy fontSize="22px" />
                  Copy
                </button>
              </div>
            </DialogContent>
          )}
          <DialogContent>
            <div className={Style.refer}>
              Please quote property reference
              <br />
              Chhatt - ID{prev.id} <br /> when calling us.
            </div>
          </DialogContent>
        </Dialog>
      </div>
      {/* modal end */}
      <div className={cn(Style.maindiv, totalWidth && Style.totalWidth)}>
        <div className={Style.img_div}>
          <Link to={val}>
            <img
              src={images[0].original}
              width="100%"
              height="90%"
              title={
                property_for === "For Sale"
                  ? "properties for sale"
                  : property_for === "For Rent"
                  ? "properties for rent"
                  : property_for === "For Booking"
                  ? "properties for buy"
                  : ""
              }
              alt={
                property_for === "For Sale"
                  ? "properties for sale"
                  : property_for === "For Rent"
                  ? "properties for rent"
                  : property_for === "For Booking"
                  ? "properties for buy"
                  : ""
              }
            />
          </Link>
          <div className={Style.sale}> {property_for} </div>
          <div className={Style.ptxt1}>
            <p>{area_type}</p>
            <p> {ago} </p>
          </div>
        </div>
        <div className={Style.mainchild}>
          <div className={Style.ptxt2}>
            <Link to={val}>
              <p style={{ fontSize: "16px" }}>
                <strong>
                  {category} {property_type} {property_for} in {area_one_name}
                </strong>
              </p>
            </Link>
            <b>
              <p
                style={{
                  fontSize: "16px",
                  color: "#D63F3F",
                  marginTop: "-15px",
                  marginBottom: "5px",
                }}
              >
                Rs: {price ? PriceCommas(price) : "On Request"}
              </p>
            </b>
          </div>
          <div className={Style.icn_container}>
            {bed && (
              <div className={Style.icn1}>
                <svg
                  className={Style.pro_bed}
                  xmlns="http://www.w3.org/2000/svg"
                  viewBox="0 0 24 24"
                >
                  <path d="M0 16L3 5V1a1 1 0 0 1 1-1h16a1 1 0 0 1 1 1v4l3 11v5a1 1 0 0 1-1 1v2h-1v-2H2v2H1v-2a1 1 0 0 1-1-1v-5zM19 5h1V1H4v4h1V4a1 1 0 0 1 1-1h4a1 1 0 0 1 1 1v1h2V4a1 1 0 0 1 1-1h4a1 1 0 0 1 1 1v1zm0 1v2a1 1 0 0 1-1 1h-4a1 1 0 0 1-1-1V6h-2v2a1 1 0 0 1-1 1H6a1 1 0 0 1-1-1V6H3.76L1.04 16h21.92L20.24 6H19zM1 17v4h22v-4H1zM6 4v4h4V4H6zm8 0v4h4V4h-4z"></path>
                </svg>
                &nbsp; &nbsp;
                <span className={Style.bed_count}>{bed}</span>
              </div>
            )}

            {bath && (
              <div className={Style.icn2}>
                <svg
                  className={Style.pro_bath}
                  xmlns="http://www.w3.org/2000/svg"
                  viewBox="0 0 24 24"
                >
                  <path d="M17.03 21H7.97a4 4 0 0 1-1.3-.22l-1.22 2.44-.9-.44 1.22-2.44a4 4 0 0 1-1.38-1.55L.5 11h7.56a4 4 0 0 1 1.78.42l2.32 1.16a4 4 0 0 0 1.78.42h9.56l-2.9 5.79a4 4 0 0 1-1.37 1.55l1.22 2.44-.9.44-1.22-2.44a4 4 0 0 1-1.3.22zM21 11h2.5a.5.5 0 1 1 0 1h-9.06a4.5 4.5 0 0 1-2-.48l-2.32-1.15A3.5 3.5 0 0 0 8.56 10H.5a.5.5 0 0 1 0-1h8.06c.7 0 1.38.16 2 .48l2.32 1.15a3.5 3.5 0 0 0 1.56.37H20V2a1 1 0 0 0-1.74-.67c.64.97.53 2.29-.32 3.14l-.35.36-3.54-3.54.35-.35a2.5 2.5 0 0 1 3.15-.32A2 2 0 0 1 21 2v9zm-5.48-9.65l2 2a1.5 1.5 0 0 0-2-2zm-10.23 17A3 3 0 0 0 7.97 20h9.06a3 3 0 0 0 2.68-1.66L21.88 14h-7.94a5 5 0 0 1-2.23-.53L9.4 12.32A3 3 0 0 0 8.06 12H2.12l3.17 6.34z"></path>
                </svg>
                <span className={Style.bath_count}>{bath}</span>
              </div>
            )}
            <div className={Style.icn3}>
              <img className={Style.pro_area} src={Areaimg} alt="area" />
              <span className={Style.area_count}>
                {size} {size_type}
              </span>
            </div>
          </div>
          <div className={Style.btndiv}>
            {/* <Link to={val} className={Style.VMBtn}>
              View more
            </Link> */}
            <Button
              component={Link}
              to={val}
              variant="contained"
              className={Style.VMBtn}
              classes={{ root: classes.button }}
            >
              View more
            </Button>

            <Button
              className={Style.btn2}
              onClick={() => handleClickOpen()}
              classes={{ root: classes.button }}
            >
              Contact Us
            </Button>
          </div>
        </div>
      </div>
    </>
  );
}
