import React from "react";
import Style from "./Style.module.scss";
import MobileAgentCard from "../cards/agentCard";

const MobileAgentsSlider = ({ singleAgency }) => {
  return (
    <div className={Style.main_div}>
      <div className={Style.fdiv}>
        <div className={Style.top}>
          <p>Agents</p>
        </div>
        <div className={Style.sdiv}>
          <ul>
            {singleAgency &&
              singleAgency.agents.map((singleAgency, i) => {
                return (
                  <li key={i} className="ml-0">
                    <MobileAgentCard singleAgency={singleAgency} />
                  </li>
                );
              })}
          </ul>
        </div>
      </div>
    </div>
  );
};

export default MobileAgentsSlider;
