import { BEST_PROPERTIES_DATA } from "./bestPropertType";
import axios from "axios";

const bestPropertiesFunc = (data) => {
  return {
    type: BEST_PROPERTIES_DATA,
    payload: data,
  };
};

export const bestPropertiesApi = () => {
  return (dispatch) => {
    axios
      .get(`https://uat.chhatt.com/api/properties/search?status=1`)
      .then((res) => {
        const bestProperties = res.data;
        dispatch(bestPropertiesFunc(bestProperties.data));
      });
  };
};
