import React from "react";
import Style from "./Style.module.scss";
import Sliderimg from "../../../assets/sliderImg.webp";
import cn from "classnames";
import { Link } from "react-router-dom";

const FeatureCard = ({
  city,
  cardTitle,
  description,
  cardImage,
  totalWidth,
  mapTitle,
  title,
  featuredProject,
  anc,
}) => {
  // FEATURED PROJECTS DESTRUCTURING START
  if (featuredProject) {
    var { desc, image, name, alter } = featuredProject;
  }
  // FEATURED PROJECTS DESTRUCTURING END

  return (
    <>
      {/* AGENCIES CARD START */}
      {title === "Agencies" && (
        <Link to={anc}>
          <div className={cn(Style.maindiv, totalWidth && Style.totalWidth)}>
            <div className={Style.img_div}>
              <img
                src={cardImage}
                width="100%"
                height="90%"
                alt={cardTitle}
                title={cardTitle}
              />
            </div>
            <div className={Style.under_text}>
              <div> {city} </div>
              <div> {cardTitle} </div>
              <div> {description} </div>
            </div>
          </div>
        </Link>
      )}
      {/* AGENCIES CARD START */}
      {/* MAP CARD START */}
      {title === "Map" && (
        <Link to="/map">
          <div className={cn(Style.maindiv, totalWidth && Style.totalWidth)}>
            <div className={Style.img_div}>
              <img src={Sliderimg} width="100%" height="90%" alt={mapTitle} />
            </div>
            <div className={Style.under_text}>
              <h5> {mapTitle} </h5>
              <h4>Karachi</h4>
              <button>View Details</button>
            </div>
          </div>
        </Link>
      )}
      {/* MAP CARD START */}

      {/* MAP CARD START */}
      {title === "Featured Projects" && (
        // <Link to="/agency">
        <div className={cn(Style.maindiv, totalWidth && Style.totalWidth)}>
          <div className={Style.img_div}>
            <img
              src={image}
              width="100%"
              height="90%"
              alt="under construction projects"
              title="New Project"
            />
          </div>
          <div className={Style.under_text}>
            <div> {city} </div>
            <div> {name} </div>
            <div> {desc} </div>
          </div>
        </div>
        // </Link>
      )}
      {/* MAP CARD START */}
    </>
  );
};

export default FeatureCard;
