import {
  MOBILE_FEATURED_PROJECTS,
  MOBILE_AGENCIES_API,
  MOBILE_AGENCIES_LIST,
  MOBILE_AGENCY_PROPERTIES,
  MOBILE_AGENCY_INPUT_ON_CHANGE,
} from "./featuredProjectsType";

const initialState = {
  mobileFeaturedProperties: "",
  mobileAgencies: "",
  mobileAgenciesList: "",
  mobileAgencyProperties: "",
  mobileAgencyInputVal: "",
};

const mobileFeaturedPropertiesReducer = (state = initialState, action) => {
  switch (action.type) {
    case MOBILE_FEATURED_PROJECTS:
      return {
        ...state,
        mobileFeaturedProperties: action.payload,
      };
    case MOBILE_AGENCIES_API:
      return {
        ...state,
        mobileAgencies: action.payload,
      };
    case MOBILE_AGENCIES_LIST:
      return {
        ...state,
        mobileAgenciesList: action.payload,
      };
    case MOBILE_AGENCY_PROPERTIES:
      return {
        ...state,
        mobileAgencyProperties: action.payload,
      };
    case MOBILE_AGENCY_INPUT_ON_CHANGE:
      return {
        ...state,
        mobileAgencyInputVal: action.payload,
      };
    default:
      return state;
  }
};
export default mobileFeaturedPropertiesReducer;
