import { RELATED_PROPERTIES } from "./relatedPropertiesType";

const initialState = {
  relatedProperties: "",
};

const relatedPropertiesReducer = (state = initialState, action) => {
  switch (action.type) {
    case RELATED_PROPERTIES:
      return {
        ...state,
        relatedProperties: action.payload,
      };
    default:
      return state;
  }
};
export default relatedPropertiesReducer;
