import React from "react";
import Style from "./Style.module.scss";
import Navbar from "../../components/jftower/navbar";
import Menu from "../../components/jftower/menu_1/menu";
import Hero from "../../components/jftower/hero";
import Bio from "../../components/jftower/developerbio";
import Overview from "../../components/jftower/overview";
import Location from "../../components/jftower/location";
import Investment from "../../components/jftower/investment";
import Gallery from "../../components/jftower/gallery";
import Payment from "../../components/jftower/payment";
import Construction from "../../components/jftower/construction";
import Address from "../../components/jftower/address";
import Footer from "../../components/jftower/footer";
import WelModal from "../../components/jftower/modal";
import { connect } from "react-redux";
import Seo from "../../components/seo";

function JfTower({ mobileMenuToggle }) {
  return (
    <div className={Style.main}>
      <Seo title="Chhatt Buy Sell and Rent Property" description="" />
      <Navbar brandlogo="mj" />
      {mobileMenuToggle && <Menu />}
      <Hero bg="mjbg" />
      <Bio />
      <Overview />
      <Location />
      <Investment />
      <Gallery />
      <Payment />
      <Construction />
      <Address />
      <Footer />
      <WelModal />
    </div>
  );
}
const mapStatetoProps = (state) => {
  return {
    mobileMenuToggle: state.MenuState.mobileMenuToggle,
  };
};
export default connect(mapStatetoProps)(JfTower);
