import React, { useEffect } from "react";
import Pagination from "@material-ui/lab/Pagination";
import Style from "./Style.module.scss";
import { mobileAgenciesListApi } from "../../redux";
import { connect } from "react-redux";

const MobileAgencyPagination = ({
  mobileAgenciesList,
  mobileAgenciesListApi,
}) => {
  const HandleChange = (event, value) => {
    mobileAgenciesListApi(value);
  };

  return (
    <>
      {mobileAgenciesList && mobileAgenciesList.data.meta.total > 28 && (
        <div className={Style.main_div}>
          <Pagination
            count={mobileAgenciesList.data.meta.last_page}
            variant="outlined"
            shape="rounded"
            onChange={HandleChange}
            siblingCount={0}
            page={mobileAgenciesList.data.meta.current_page}
          />
        </div>
      )}
    </>
  );
};

const mapDispatchtoProps = (dispatch) => {
  return {
    mobileAgenciesListApi: function (data) {
      dispatch(mobileAgenciesListApi(data));
    },
  };
};

export default connect(null, mapDispatchtoProps)(MobileAgencyPagination);
