import React, { useState, useEffect } from 'react'
import Style from './Style.module.scss'
// import { BsPersonFill } from "react-icons/bs";
import { RiSearch2Line } from 'react-icons/ri'
import ProgressiveImage from 'react-progressive-image'
import Ig1 from '../../../assets/Main_Banner.webp'
import Logo from '../../../assets/logow.png'
import { CgArrowLongDown } from 'react-icons/cg'
// import play from "../../../assets/icons/play.png";
import cn from 'classnames'
import { Link as Lk } from 'react-scroll'
import { Link } from 'react-router-dom'
import useAutocomplete from '@material-ui/lab/useAutocomplete'
import { connect } from 'react-redux'
import { fetchAreaOne } from '../../redux'
import { useHistory } from 'react-router-dom'
import { makeStyles, withStyles } from '@material-ui/core/styles'
import MenuItem from '@material-ui/core/MenuItem'
import FormControl from '@material-ui/core/FormControl'
import Button from '@material-ui/core/Button'

import Select from '@material-ui/core/Select'
import InputBase from '@material-ui/core/InputBase'
import { mainBanner } from '../images'

function Header1({ fetchAreaOne, homeSearch, citiesData }) {
  const [windowSize, setWindowSize] = useState({
    width: undefined,
  })
  const [lgo, setLgo] = useState(Logo)
  const [selectBox, setSelectBox] = useState('karachi')
  let history = useHistory()

  useEffect(() => {
    fetchAreaOne(selectBox)
  }, [selectBox])

  useEffect(() => {
    // Handler to call on window resize
    function handleResize() {
      // Set window width/height to state
      setWindowSize({
        width: window.innerWidth,
      })
    }
    // Add event listener
    window.addEventListener('resize', handleResize)
    // Call handler right away so state gets updated with initial window size
    handleResize()
    // Remove event listener on cleanup
    return () => window.removeEventListener('resize', handleResize)
  }, []) // Empty array ensures that effect is only run on mount

  // HANDLE CHANGE FOR ALL SELECT BOXES
  const handleChange = (e) => {
    setSelectBox(e.target.value)
  }

  // INPUT FIELD STYLING MATERIAL UI START
  const useStyles = makeStyles((theme) => ({
    input: {
      outline: 'none',
      width:
        (windowSize.width <= 1200 && 300) ||
        (windowSize.width <= 1400 && 385) ||
        450,
      padding: '10px 10px',
    },
    listbox: {
      width:
        (windowSize.width <= 1200 && 600) ||
        (windowSize.width <= 1400 && 680) ||
        750,
      margin: 0,
      padding: 0,
      zIndex: 1,
      marginTop: '5px',
      position: 'absolute',
      left: '0%',
      listStyle: 'none',
      backgroundColor: theme.palette.background.paper,
      overflow: 'auto',
      maxHeight: 300,
      color: '#000',
      textAlign: 'left',
      border: '1px solid rgba(0,0,0,.25)',
      '& li[data-focus="true"]': {
        backgroundColor: '#4a8df6',
        color: '#fff',
        cursor: 'pointer',
      },
      '& li:active': {
        backgroundColor: '#2977f5',
        color: '#000',
      },
    },
    button: {
      textTransform: 'none',
    },
  }))
  // INPUT FIELD STYLING MATERIAL UI END

  // SELECT STYLING FROM MATERIAL UI START
  const BootstrapInput = withStyles((theme) => ({
    root: {
      'label + &': {
        marginRight: theme.spacing(1),
      },
    },
    input: {
      borderRadius: 4,
      position: 'relative',
      backgroundColor: '#f2f2f2',
      fontSize: 16,
      padding: '10px 26px 10px 12px',
      transition: theme.transitions.create(['border-color', 'box-shadow']),
      fontWeight: '600',
      width: '100px',
      textAlign: 'left',

      '&:focus': {
        borderRadius: 4,
        borderColor: '#80bdff',
        boxShadow: '0 0 0 0.2rem rgba(0,123,255,.25)',
      },
    },
  }))(InputBase)

  const classes = useStyles()
  let {
    getRootProps,
    getInputProps,
    getListboxProps,
    getOptionProps,
    groupedOptions,
  } = useAutocomplete({
    id: 'use-autocomplete-demo',
    options: homeSearch,
    getOptionLabel: (option) => option.name,
  })

  // FOR NAVBAR SCROLLING EFFECT START
  window.addEventListener('scroll', function () {
    var header = document.querySelector('.navbarContainer')
    header.classList.toggle('sticky', window.scrollY > 1)
  })

  // useEffect(() => {
  //   window.addEventListener("scroll", () => {
  //     if (window.scrollY > 0) {
  //       setLgo(Blogo);
  //       setCol("#000");
  //     } else {
  //       setLgo(Logo);
  //       setCol("#fff");
  //     }
  //     var hder = document.getElementById("head1");
  //     hder.classList.toggle("sticky", window.scrollY > 0);
  //     var hder = document.getElementById("lgo");
  //     hder.classList.toggle("logn", window.scrollY > 0);
  //     var mnu = document.getElementById("mnu");
  //     mnu.classList.toggle("icn_mnu", window.scrollY > 0);
  //     var sing = document.getElementById("sing");
  //     sing.classList.toggle("icn_mnu", window.scrollY > 0);
  //   });
  // }, []);

  // const listStyling = {
  //   padding: "3px",
  //   borderBottom: "1px solid #CCC",
  // };

  const SubmitForm = () => {
    const val = getInputProps().value
    if (val !== '') {
      history.push(
        `/results?home&val=${val.replace(/ /g, "-")}&city=${selectBox}&p=1`
      );
    } else {
      history.push(`/results?home&city=${selectBox}&p=1`)
    }
  }
  return (
    <header id='sctop'>
      <div className={cn(Style.main_div)} id='head1'>
        <div className={`${Style.s_div} navbarContainer`}>
          <div className='innerContainer'>
            <div className={Style.logo} id='lgo'>
              {/* <CgMenuLeftAlt id="mnu" className={Style.menu_icn} /> */}
              <Link to='/'>
                <ProgressiveImage delay={600} src={lgo} placeholder={lgo}>
                  {(src) => <img src={src} alt='home' />}
                </ProgressiveImage>
              </Link>
            </div>
            <div className={Style.tabss}>
              <ul>
                <Link style={{ color: '#fff' }} title='Home' to='/'>
                  <li className={cn(Style.lif)}>
                    <span>HOME</span>
                  </li>
                </Link>
                <Link to='/property' style={{ color: '#fff' }} title='Property'>
                  <li className={cn(Style.lis)}>
                    <span>Property</span>
                  </li>
                </Link>
                <Link
                  style={{ color: '#fff' }}
                  to='/construction'
                  title='Construction'
                >
                  <li className={cn(Style.lis)}>
                    <span>Construction</span>
                  </li>
                </Link>
                <Link
                  style={{ color: '#fff' }}
                  to='/real-estate-agencies'
                  title='real-estate-agencies'
                >
                  <li className={cn(Style.lis)}>
                    <span>Agencies</span>
                  </li>
                </Link>
                <Link
                  style={{ color: '#fff' }}
                  to='/contact-us'
                  title='contact-us'
                >
                  <li className={cn(Style.lis)}>
                    <span>Contact Us</span>
                  </li>
                </Link>
                <Link style={{ color: '#fff' }} to='/wanted' title='wanted'>
                  <li className={cn(Style.lit)}>
                    <span>Wanted</span>
                  </li>
                </Link>
              </ul>
            </div>
            {/* <div className={cn(Style.signin)}>
            <div className={Style.signin_icn}>
              <BsPersonFill />
            </div>
          </div> */}
          </div>
        </div>
      </div>

      <ProgressiveImage delay={400} src={mainBanner} placeholder={Ig1}>
        {(src, loading) => (
          <div className={Style.mn_div}>
            <img
              style={{
                filter: loading ? 'blur(4px)' : 'blur(0px)',
              }}
              src={src}
              alt='house for sell and construction material'
              className={Style.imgs}
            />
            <div className={Style.backg}>
              <div className={Style.backg_sdiv}>
                <h1>Find the Best Properties and Construction Material</h1>
                <div className={Style.main_bar}>
                  <div className={Style.searchdiv}>
                    <span className={Style.searchIcon}>
                      <RiSearch2Line fontSize='26px' />
                    </span>
                    {/* search field start */}
                    <div style={{ width: '450px' }}>
                      {homeSearch && (
                        <div id='listBox'>
                          <div {...getRootProps()}>
                            <input
                              className={classes.input}
                              placeholder='Search...'
                              {...getInputProps()}
                            />
                          </div>
                          {getInputProps().value &&
                          groupedOptions.length > 0 ? (
                            <ul
                              className={classes.listbox}
                              {...getListboxProps()}
                            >
                              {groupedOptions.map((option, index) => (
                                <li
                                  // style={listStyling}
                                  {...getOptionProps({ option, index })}
                                >
                                  <div>{option.name}</div>
                                  <div>{option.parent}</div>
                                  {/* {`${option.name} - ${option.parent} `} */}
                                </li>
                              ))}
                            </ul>
                          ) : null}
                        </div>
                      )}
                    </div>
                    {/* search field end */}
                    {citiesData ? (
                      <div style={{ marginRight: '12px' }}>
                        <FormControl className={classes.margin}>
                          <Select
                            labelId='demo-customized-select-label'
                            id='demo-customized-select'
                            value={selectBox}
                            onChange={handleChange}
                            input={<BootstrapInput />}
                          >
                            {citiesData.map((prev, i) => {
                              const { id, name } = prev
                              return (
                                <MenuItem key={i} value={name.toLowerCase()}>
                                  {name}
                                </MenuItem>
                              )
                            })}
                          </Select>
                        </FormControl>
                      </div>
                    ) : (
                      <div style={{ marginRight: '12px' }}>
                        <FormControl className={classes.margin}>
                          <Select
                            labelId='demo-customized-select-label'
                            id='demo-customized-select'
                            value={selectBox}
                            onChange={handleChange}
                            input={<BootstrapInput />}
                          >
                            <MenuItem value='karachi'>Karachi</MenuItem>
                          </Select>
                        </FormControl>
                      </div>
                    )}

                    <Button
                      variant='contained'
                      color='primary'
                      classes={{ root: classes.button }}
                      onClick={() => SubmitForm()}
                    >
                      Search
                    </Button>
                  </div>
                  {/* <div className={Style.secategories}>
                    <div className={Style.secategories_first_div}>
                      <div className={Style.explore}>
                        Explore our categories
                      </div>
                      <div className={Style.viewmore}>View More</div>
                    </div>
                    <div className={Style.secategories_sec_div}>
                      <ul className={Style.sul}>
                        <li>
                          <button
                            style={{
                              backgroundColor: "#007bff",
                              color: "#ffffff",
                            }}
                          >
                            All
                          </button>
                        </li>
                        <li>
                          <button>Realtors</button>
                        </li>
                        <li>
                          <button>Properties</button>
                        </li>
                        <li>
                          <button>Labour</button>
                        </li>
                        <li>
                          <button>Home Services</button>
                        </li>
                        <li>
                          <button>Construction Material</button>
                        </li>
                        <li>
                          <button>Safety</button>
                        </li>
                      </ul>
                    </div>
                  </div> */}
                </div>
              </div>
              {/* <div className={Style.bottom}>
                <img src={play} width="170px" alt="" />
              </div> */}
              <div className={Style.scroll}>
                <Lk smooth={true} duration={500} to='find'>
                  <div className={Style.txtScroll}>Scroll</div>
                  <div>
                    <CgArrowLongDown size={30} />
                  </div>
                </Lk>
              </div>
            </div>
          </div>
        )}
      </ProgressiveImage>
    </header>
  )
}

const mapStatetoProps = (state) => {
  return {
    homeSearch: state.homeSearchAreas.homeSearch,
    citiesData: state.homeSearchAreas.citiesData,
  }
}
const mapDispatchtoProps = (dispatch) => {
  return {
    fetchAreaOne: function (data) {
      dispatch(fetchAreaOne(data))
    },
    // header1formSubmit: function (data) {
    //   dispatch(header1formSubmit(data));
    // },
  }
}
export default connect(mapStatetoProps, mapDispatchtoProps)(Header1)
