import React from "react";
import { RiSearch2Line } from "react-icons/ri";
import Style from "./Style.module.scss";
import { agenciesSearchApi, agencyInputOnChange } from "../../redux";
import { connect } from "react-redux";

const AgenciesSearch = ({
  agencyInputVal,
  agencyInputOnChange,
  agenciesSearchApi,
}) => {
  const handleChange = (e) => {
    agencyInputOnChange(e.target.value);
    agenciesSearchApi(e.target.value);
  };
  return (
    <div>
      <div className={Style.maindiv}>
        <div className={Style.secdiv}>
          {/* ============================  SEARCH BOX START  ================================ */}
          <h3> Agencies Search </h3>
          <div className={Style.searchdiv}>
            <div className={Style.leftSec}>
              <span>
                <RiSearch2Line fontSize="26px" />
              </span>
              {/* search field start */}
              <div>
                <input
                  type="text"
                  placeholder="Search..."
                  value={agencyInputVal}
                  onChange={(e) => handleChange(e)}
                />
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

const mapStatetoProps = (state) => {
  return {
    agenciesList: state.featuredPropertiesReducer.agenciesList,
    agencyInputVal: state.featuredPropertiesReducer.agencyInputVal,
  };
};
const mapDispatchtoProps = (dispatch) => {
  return {
    agenciesSearchApi: function (data) {
      dispatch(agenciesSearchApi(data));
    },
    agencyInputOnChange: function (data) {
      dispatch(agencyInputOnChange(data));
    },
  };
};
export default connect(mapStatetoProps, mapDispatchtoProps)(AgenciesSearch);
