import React from "react";
import Style from "./Style.module.scss";
import ChLogo from "../../../../assets/chhattLogo.png";
import MJLogo from "../../../../assets/jftower/MJLogo.webp";
import cn from "classnames";

export default function Address() {
  return (
    <div className={Style.main}>
      <div className={cn("mr-3", Style.img_div)}>
        <img src={MJLogo} width="95px" alt="logo" />
      </div>
      <div className="mr-3">
        <h2 className={Style.head}>Booking Office</h2>
        <p className={Style.para}>
          Karachi <br />
          Office # 1, 18-D, Sehar LANE-4, Commercial AVE., PH VII, DHA
        </p>
      </div>
      <div>
        <h2 className={Style.head}>Site Office</h2>
        <p className={Style.para}>Badar Commercial Street 11, Karachi</p>
      </div>
      <div>
        <a href="https://chhatt.com/">
          <img src={ChLogo} width="115px" alt="chhatt.com" />
        </a>
      </div>
    </div>
  );
}
