import React from "react";
import Pagination from "@material-ui/lab/Pagination";
import Style from "./Style.module.scss";
import { agenciesListApi } from "../../redux";
import { connect } from "react-redux";

const AgencyPagination = ({ agenciesList, agenciesListApi }) => {
  const HandleChange = (event, value) => {
    agenciesListApi(value);
  };

  return (
    <>
      {agenciesList && agenciesList.data.meta.total > 28 && (
        <div className={Style.main_div}>
          <Pagination
            count={agenciesList.data.meta.last_page}
            variant="outlined"
            shape="rounded"
            onChange={HandleChange}
            siblingCount={0}
            page={agenciesList.data.meta.current_page}
          />
        </div>
      )}
    </>
  );
};

const mapDispatchtoProps = (dispatch) => {
  return {
    agenciesListApi: function (data) {
      dispatch(agenciesListApi(data));
    },
  };
};

export default connect(null, mapDispatchtoProps)(AgencyPagination);
