import React from "react";
import { MobileView } from "react-device-detect";
import { Switch, Route } from "react-router-dom";
import Jf_Tower from "../../website/pages/jftower";
import WelcomeCenter from "../../website/pages/welcomecenter";
import mobileStore from "../redux/store";
import MobileHome from "../pages/home";
import MobileProperty from "../pages/property";
// import Header from "components/header";
import MobileBestProperties from "../pages/bestProperties";
import MobileProduct from "../pages/product";
import MobileFilterPage from "../pages/filter";
// import HomeSearch from "../components/homeSearchbar";
// import Projects from "../pages/projects";
import MobileAgencies from "../pages/agencies";
import MobileAgency from "../pages/agency";
import ScrollToTop from "../../website/components/ScrollToTop";
import { Provider } from "react-redux";

const MobileRoutes = () => {
  return (
    <>
      {/* MOBILE VIEW START */}
      <Provider store={mobileStore}>
        <MobileView>
          <ScrollToTop />
          {/* <Projects /> */}
          <Switch>
            <Route exact path="/" component={MobileHome} />
            <Route
              exact
              path="/projects/welcome_center"
              component={WelcomeCenter}
            />
            <Route exact path="/projects/jf-tower" component={Jf_Tower} />
            <Route exact path="/property" component={MobileProperty} />
            <Route
              exact
              path={[
                "/results",
                "/property/:propertyType",
                "/property/:propertyType/:page",
              ]}
              component={MobileBestProperties}
            />
            <Route
              exact
              path="/propertyDetail/:productId"
              component={MobileProduct}
            />
            <Route exact path="/filter/:type" component={MobileFilterPage} />
            <Route
              exact
              path="/real-estate-agencies"
              component={MobileAgencies}
            />
            <Route exact path="/agency/:id" component={MobileAgency} />
          </Switch>
        </MobileView>
      </Provider>
      {/* MOBILE VIEW END */}
    </>
  );
};

export default MobileRoutes;
