import React from "react";
import Style from "./Style.module.scss";
import { NavLink } from "react-router-dom";
import { useLocation } from "react-router";

const MobileBuyRent = () => {
	// GETTING URL AS AN OBJECT START
	const location = useLocation();

	const string = location.search.substring(1).split("&");

	var urlData = {};
	for (var i = 0; i < string.length; i++) {
		var pair = string[i].split("=");
		var ind1 = pair[0];
		var ind2 = pair[1];

		urlData = { ...urlData, [ind1]: ind2 };
	}
	// GETTING URL AS AN OBJECT END

	if (urlData.purpose) {
		var { purpose } = urlData;
	}

	return (
		<div className={Style.main_div}>
			<NavLink
				className={(purpose === "for-sale" && Style.activeBtn) || ""}
				activeClassName={Style.activeBtn}
				to="/filter/buy"
			>
				<button>Buy</button>
			</NavLink>
			<NavLink
				className={(purpose === "for-rent" && Style.activeBtn) || ""}
				activeClassName={Style.activeBtn}
				to="/filter/rent"
			>
				<button>Rent</button>
			</NavLink>
			<NavLink
				className={(purpose === "for-booking" && Style.activeBtn) || ""}
				activeClassName={Style.activeBtn}
				to="/filter/booking"
			>
				<button>Booking</button>
			</NavLink>
		</div>
	);
};

export default MobileBuyRent;
